<template>
    <div class="box" style="display:inline-block">
        <el-select class="select"  @change='on_province_change' v-model="province_id" placeholder="选择省" style="width:158px" size="small">
            <el-option v-for="(items,index) in province" :key="index" :label="items.name" :value="items.id"></el-option>
        </el-select>
        <el-select class="select" @change='on_city' v-model="city_id" placeholder="选择市" style="width:158px" size="small">
            <el-option  v-for="(items,index) in city" :key="index" :label="items.name" :value="items.id"></el-option>
        </el-select>
        <el-select class="select"  @change='on_district' v-model="district_id" placeholder="选择县/区" style="width:158px" size="small">
            <el-option  v-for="(items,index) in district" :key="index" :label="items.name" :value="items.id"></el-option>
        </el-select>
    </div>
</template>
<script>
import http from "@/api/http";
export default {
  name: "citycom",
  data() {
    return {
      province: [],
      city: [],
      district: [],
      province_id: null,
      city_id: null,
      district_id: null
    };
  },
  props: {
    province_ids: {},
    city_ids: {},
    district_ids: {}
  },
  created() {
    var that = this;
    that.getlis();
  },
  mounted() {},
  updated() {},
  methods: {
    getlis() {
      var that = this;
      http.ajaxPost("get_region", {}, res => {
        if (res.data.status) {
          that.province = res.data.data;
        } else {
          that.$message({
            type: "info",
            message: "使用数据请求不到,请检查登陆状态或网络"
          });
        }
      });
    },
    on_province_change(value) {
      var that = this;
      this.$emit("province_id", this.province_id);
      this.$emit("province_id2", this.province_id);
      this.city_id = null;
      this.district_id = null;
      http.ajaxPost("get_region", { data: { id: value } }, res => {
        if (res.data.status) {
          that.city = res.data.data;
        } else {
          that.$message({
            type: "info",
            message: "使用数据请求不到,请检查登陆状态或网络"
          });
        }
      });
    },
    on_city(value) {
      var that = this;
      this.$emit("city_id", this.city_id);
      this.$emit("city_id2", this.city_id);
      this.district_id = null;
      http.ajaxPost("get_region", { data: { id: value } }, res => {
        if (res.data.status) {
          that.district = res.data.data;
        } else {
          that.$message({
            type: "info",
            message: "使用数据请求不到,请检查登陆状态或网络"
          });
        }
      });
    },
    on_district(value) {
      this.$emit("district_id", this.district_id);
      this.$emit("district_id2", this.district_id);
    }
  },
  watch: {
    province_ids: function(a, b) {
      this.province_id = a;
      this.on_province_change(a);
    },
    city_ids: function(a, b) {
      this.city_id = a;
      this.on_city(a);
    },
    district_ids: function(a, b) {
      this.district_id = a;
      this.on_district(a);
    }
  }
};
</script>
<style scoped>
</style>



