<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <kalmanleftcomponents></kalmanleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <!-- <img class="search_img" src="../../../static/images/search.png" alt=""> -->
                <div class="search_modle" >
                    <span>卡密生成数量:</span>
                    <el-input size='small' style="width:208px;display:inlie-block;" type="number" v-model="number" placeholder="请输入生成数量"></el-input>
                    <div style="float:right;margin-right:28px;" >
                        <el-button size='small' @click="kalman_add" class="btn" style="width:88px;margin-right:28px;" type="danger">生成卡密</el-button>
                        <el-button size='small' @click="kalman_btn" class="btn" style="width:88px;margin-right:28px;" type="success">全部导出</el-button>
                    </div>
                </div>
                <div class="search_modle" style="height: 38px;color:#ccc;" >
                    <p>ps:如果生成卡密后,没有导出,再次生成则会覆盖之前的卡密数据,请确保导出后再次生成;</p>

                </div>
            </div>
            <div class="vip_lis_table">
                <el-table :data="tableData"  border  style="width: 100%">
                    <el-table-column  type="selection"  width="35"> </el-table-column>
                    <el-table-column  prop="account_number" label="卡密账号"  > </el-table-column>
                    <el-table-column  prop="password" label="卡密密码"  ></el-table-column>
                    <el-table-column  label="创建时间"  >
                        <template   slot-scope="scope" > 
                            {{scope.row.created_at | time}}
                        </template> 
                    </el-table-column>
                </el-table>
            </div>
            <div class="page"><pagecon :pages="pages" @get_lis="get_lis"></pagecon></div>
        </div>
    </div>
</div>
</template>
<script>
import kalman_add from "./kalman_add";
export default kalman_add;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
</style>



