<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <vipleftcomponents></vipleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <img class="search_img" src="../../../static/images/search.png" alt="">
                <div class="search_modle" >
                    <span>搜索:</span>
                    <el-input style="width:208px;display:inlie-block;vertical-align: middle;"  v-model="screen.keyword" placeholder="请输入操作内容"></el-input>
                    <span style="margin-left:38px">时间:</span>
                    <el-date-picker type="datetimerange" style="width: 338px;height:38px;vertical-align: middle;" v-model="screen.time_data" start-placeholder="开始日期" end-placeholder="结束日期" :default-time="['12:00:00']"> </el-date-picker>
                    <el-button class="btn" @click="search_btn" type="danger" style="vertical-align: middle;">搜索</el-button>    
                </div>
            </div>
            <!-- 操作信息列表展示区 -->
            <div class="table">
                <el-table :data="tableData"  style="width: 100%" > 
                    <el-table-column  type="selection"  width="35"> </el-table-column>
                    <el-table-column  prop="admin_name"  label="管路员账号" width="90"  :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column   label="时间" :show-overflow-tooltip="true">
                      <template   slot-scope="scope" > 
                            {{scope.row.created_at | time}}
                        </template> 
                    </el-table-column>
                    <el-table-column  prop="ip" label="IP"  width="140" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column  prop="system" label="操作系统" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column  prop="browser" label="浏览器" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column  prop="content" label="操作id" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column  prop="url_name" label="操作内容" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column  prop="type" label="操作类型" :show-overflow-tooltip="true"></el-table-column>
                </el-table>
            </div>
            <!-- 会员列表信息分页 -->
            <div class="page"><pagecon :pages="pages" @get_lis="get_lis"></pagecon></div>                        
        </div>
    </div>
</div>
</template>
<script>
import vip_log_operation from "./vip_log_operation";
export default vip_log_operation;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
</style>



