import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'

export default {
  name: 'Commodity_brand'
  , data() {
    return {
      tableData: [],
      is_show: 1,
      title: '添加品牌'
      , title_top: '品牌列表'
      , add_lis: {}  //添加品牌
      //图片上传
      , base_url: 'uploading'
      , data_url: { type: 2 }
      , data_lis: []
      , fileList: [{ url: '' }]
      , multipleSelection: [] //选择下拉框值
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
  }
  , created() {
    var that = this;
    that.get_lis()
    that.base_url = this.axios.defaults.baseURL + that.base_url;
  }
  , methods: {
    get_lis() { //获取table数据  
      var that = this;
      http.ajaxPost('brand', { data: { token: http.getToken() } }, res => {
        that.tableData = res.data.data;
      })
    }
    , del_all() { //全部删除
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '请选择删除内容' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      data.id = id;
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      var http_post = function () {
        http.ajaxPost('brand/destroy', { data: data }, (res) => {
          console.log(res.data)
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '删除成功' })
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '删除后无法恢复,您确定要删除吗?', '全部删除', http_post)
    }
    , handleSelectionChange(val) {  //选中的元素
      this.multipleSelection = val;
    }
    , all_yes_select(rows) { //快捷全选   全不选
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    }
    , reverse_select() {
      var that = this;
      if (that.multipleSelection.length == 0) { //如果没有勾选  反选就是全选
        that.all_yes_select(that.tableData);
      } else {
        var select_data = []; //存放已经被勾选的数据
        var new_data = []; //反选后填入的数据
        for (let i in that.tableData) { //首先存入全部数据
          new_data.push(that.tableData[i])
        }
        for (var i in that.multipleSelection) { //此时 获取之前 已经勾选过得数据 
          select_data.push(that.multipleSelection[i])
        }
        that.all_yes_select();//清空之前勾选过得内容
        //获取没有被勾选的数据
        for (var i = 0; i < select_data.length; i++) {
          for (var j = 0; j < new_data.length; j++) {
            if (new_data[j] == select_data[i]) {
              new_data.splice(j, 1)
            }
          }
        }
        if (new_data.length == 0) { //如果没有
          that.all_yes_select();
        } else {
          that.all_yes_select(new_data);
        }
      }
    }
    , commodity_brand_add() { //添加商品  //修改
      var that = this;
      var data = that.add_lis;
      data.token = http.getToken();
      if (that.add_lis.category_id == null || that.add_lis.name == null || that.add_lis.order == null || that.add_lis.pic == null || that.add_lis.url == null) {
        that.$message({ type: 'info', message: '请填写完整信息' })
        return;
      }
      if (that.title_top == '修改品牌') {
        http.ajaxPost('brand/edit/' + that.add_lis.id, { data: data }, res => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '修改成功' })
            that.is_show_updata();
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      } else {
        http.ajaxPost('brand/create', { data: data }, res => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '发布成功' })
            that.is_show_updata();
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
    }
    , is_show_updata() {  //控制添加 和 列表页面展示 
      var that = this;
      that.is_show = !that.is_show;   //页面替换
      that.add_lis = {};  //添加数据提前清空
      that.fileList[0].url = '' //清空照片
      that.title = that.is_show ? '添加品牌' : '返回品牌列表';
      that.title_top = that.is_show ? '品牌列表' : '添加品牌';
      if (!that.is_show) {
        http.ajaxPost('brand/create', { data: { token: http.getToken() } }, res => {
          that.data_lis = res.data.data
        }
        )
      }
    }
    , commodity_brand_updata(id) { //修改页面获取 展示
      var that = this;
      that.is_show_updata();
      that.title_top = '修改品牌'
      http.ajaxPost('brand/edit/' + id, { data: { token: http.getToken() } }, res => {
        that.add_lis = res.data.data.brand
        that.fileList[0].url = that.axios.defaults.baseURL + that.add_lis.pic
      })
    }
    , commodity_brand_del(id) { //删除数据  
      var that = this;
      var http_del = function () {
        http.ajaxPost('brand/destroy', { data: { token: http.getToken(), id: [id] } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '删除成功' })
            that.get_lis();
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '删除后不可恢复,您确定继续吗', '删除操作', http_del)
    }
    //手机图片
    , pic_status(e) { this.add_lis.pic = e.url; }
  }
}



