import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import distributionleftcomponents from '@/views/distribution/distribution_left_components.vue'

export default {
  name: 'Tixian_setting'
  , data() {
    return {
      data_lis: {},
      xingqi:['星期日','星期一','星期二','星期三','星期四','星期五','星期六']
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , distributionleftcomponents

  }
  , created() {
    var that = this;
    that.get_lis();
  }
  ,updated(){
  }
  , methods: {
    get_lis() {
      var that = this;
      let data = { data: { token: http.getToken() } };
      that.axios.post('tixian/edit', data).then(res => {
        that.data_lis = res.data.data;
        that.data_lis.tixiandate.forEach(function(ele,key){
            that.data_lis.tixiandate[key] = parseInt(ele) ;
        });
        console.log(that.data_lis)
      })
    }
    , is_updata() {
      var that = this;
      let data = {};
      if(that.data_lis.proportion > 100){
        that.$message('提现比例不能超过100%');
        return;
      }
      data.data = that.data_lis;
      data.data.token = http.getToken('token');
      that.axios.post('tixian/edit', data).then(res => {
        if(res.data.message){
          that.$message({
            message:res.data.message ,
            type: 'success'
          });
        }else{that.$message(res.data.message)};
      })
    }

  }
}


