<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <vipleftcomponents></vipleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>发送站内信</span>
                    <el-button type="success" plain style="vertical-align: middle;width:144px;float:right;margin: 10px;" @click="go_vip_letter_record">收/发件箱列表</el-button>
                </div>
            </div>
            <div class="vip_vip_add">
                 <div class="vip_set_cont_modle">
                    <span>发件人:</span>
                    <el-input v-model="data_lis.send_name" :disabled="true"></el-input>
                </div>
                 <div class="vip_set_cont_modle">
                    <span>发件时间:</span>
                    <el-input placeholder="已发送时间为准" :disabled="true"></el-input>
                </div>
                <div class="vip_set_cont_modle">
                    <span>发送类型:</span>
                    <el-radio v-model="type" :label="1">会员等级</el-radio>
                    <el-radio v-model="type" :label="0">指定会员</el-radio>
                </div>
                <!-- 选择会员等级 收件会员名称选择其一显示 -->
                 <div class="vip_set_cont_modle" v-if="type">
                    <span>选择会员等级:</span>
                    <el-select class="select"  v-model="data_lis.level" placeholder="选择等级">
                        <el-option label="全部会员" :value="0" ></el-option>
                        <el-option v-for="(items,index) in level" :key="index" :label="items.name" :value="items.id"></el-option>
                    </el-select>
                </div>
                 <div class="vip_set_cont_modle" v-if="!type">
                    <span>收件会员名称:</span>
                    <el-input v-model="data_lis.name"></el-input>
                </div>
                 <div class="vip_set_cont_modle clearfix">
                    <span>信息内容:</span>
                     <el-input v-model="data_lis.content" style="width:calc(100% - 200px);vertical-align: middle;" type="textarea" autosize placeholder="请输入内容" ></el-input>
                </div>
                <el-button type="success" @click="add_data" plain>提交</el-button>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import send_letter from "./send_letter";
export default send_letter;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_vip_add {
    background: #fff;
    border-radius: 12px;

    margin-top: 12px;
    padding: 18px 0;
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 128px;
        text-align: right;
        margin-right: 18px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 180px;
      }
      // 上传头像样式
      .avatar-uploader {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        display: inline-block;
      }
      .avatar-uploader:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
      }
      .avatar {
        width: 178px;
        height: 178px;
        display: block;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
}
</style>



