import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import pagecon from '@/components/page/pagecon'
import citycom from '@/components/city/city'
import kalmanleftcomponents from '@/views/kalman/kalman_left_components.vue'

export default {
  name: 'Kalman_add'
  , data() {
    return {
      tableData: [] //table表格数据接收包
      , pages: null
      , number: null
      // ,is_updata:0 //通知遮罩层
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , kalmanleftcomponents
    , pagecon
    , citycom
  }
  , created() {
    this.get_lis();
  }
  , methods: {
    kalman_btn() {
      let URL = this.axios.defaults.baseURL + 'kalman/export/' + http.getToken();
      window.open(URL, '_blank', 'width=320,height=568,menubar=no,toolbar=no,status=no,scrollbars=yes')
    }
    , kalman_add() {
      var that = this;
      if(that.number==null || that.number ==0 || that.number == ''){
        that.$message('请输入生成数量') ;return;
      }
      http.ajaxPost('kalman/create', { data: { token: http.getToken(),number:that.number}}, res => {
        if(res.data.status){
          that.$message({
            message:res.data.message ,
            type: 'success'
          });
          that.get_lis();
        }else{that.$message(res.data.message)}
      })
    }

    , get_lis(e) { //获取table数据  
      var that = this;
      http.ajaxPost('kalman/create', { data: { token: http.getToken()}, page: e }, res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      })
    }
  }
}


