import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'
import pagecon from '@/components/page/pagecon'

export default {
  name: 'Vip_log_operation'
  , data() {
    return {
      screen: {keyword:null,time_data:[]},
      tableData: [],
      pages: null,
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , vipleftcomponents
    , pagecon

  }
  , created() {
    let that = this;
    that.get_lis();
  }
  , methods: {
    get_lis(e) { //获取table数据  
      var that = this;
      http.ajaxPost('action_log', { data: { token: http.getToken(), screen: that.search }, page: e }, res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      })
    }
    , search_btn() {//搜索
      var that = this;
      // if( that.screen.keyword=='' || that.screen.keyword==null) {
      //   that.$message({ type: 'info', message: '请输入查询内容' });
      //   return;
      // }
      let data = {};
      data.screen = that.screen;
      data.token = http.getToken();
      http.ajaxPost('action_log', { data }, res => {
        if (res.data.status) {
          that.tableData = res.data.data.data;
          that.pages = res.data.data;
          delete that.pages.data;
        } else { that.$message({ type: 'info', message: res.data.message }) ;that.tableData = {} ;}
      })
    }
  }
}


