import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'
export default {
  name: 'Commodity_column'
  , data() {
    return {
      tableData: [],
      is_other: 0 //控制隐藏部分
      , is_all: false  //控制是否全部展开
      , data_lis: []
      , value: null  //栏目id值
      , value_name: null
      , table_show: 0
      , base_url: 'uploading'
      , data_url: { type: 2 }
      , data_url1: { type: 1 }
      , fileList2: [
        { url: '' }
      ]
      , fileList: [
        { url: '' }
      ]
      , updata_lis: {}  //要编辑的内容
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
  }
  , created() {
    var that = this;
    that.get_lis()
    that.base_url = this.axios.defaults.baseURL + that.base_url;
  }
  , updata() {
  }
  , mounted() {


  }
  , methods: {
    //添加管理员信息提交
    submitForm(formName) {
      var that = this;
      that.$refs[formName].validate((valid) => {
        if (valid) {

        } else {
          console.log('error submit!!');
          return false;
        }
      });
    }
    //跳转至管理员列表
    , go_vip_admin_lis() {
      this.$router.push({ path: '/home/vip_admin_lis' })
    }
    , close(e, id) {  //传入e=1  为进入编辑页面
      var that = this;
      if (id) { // 如果传入id 就展示编辑页面
        if (e) {  //1 获取当前信息
          http.ajaxPost('category/edit/' + id, { data: { token: http.getToken() } }, res => {
            console.log(res)
            if (res.data.status) {
              that.updata_lis = res.data.data.category
              that.fileList2[0].url = that.axios.defaults.baseURL + that.updata_lis.pic
              that.fileList[0].url = that.axios.defaults.baseURL + that.updata_lis.cover_pic
            } else { that.$message({ type: 'info', message: '获取信息失败' }) }
          })
        } else {  //获取当前id  下拉框显示
          that.value = id;
        }
      } else {
        that.updata_lis = {};
        that.value_name = null;
        that.value = null;
      }
      that.is_other = !that.is_other;
      that.table_show = e == 1 ? 1 : 0; //判断弹出页面
    }
    , table_all() {
      this.is_all = !this.is_all;

    }
    , commodity_column_del(e) { //删除栏目  e为id
      var that = this;
      var http_post = function () {
        http.ajaxPost('category/destroy', { data: { token: http.getToken(), id: e } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '删除成功' })
            that.get_lis();
          } else {
            that.$message({ type: 'info', message: '操作失败' })
          }
        });
      }
      http.my_alert(that, '删除后无法恢复, 是否继续', '确认删除', http_post)
    }
    , commodity_column_updata() {  //修改
      var that = this;
      let data = that.updata_lis;
      data.token = http.getToken();
      http.ajaxPost('category/edit/' + that.updata_lis.id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '修改成功' })
          that.get_lis();
          that.close()
        } else { that.$message({ type: 'info', message: '操作失败' }) }
      })
    }
    , column_updata(names, id, value) { //单独修改状态值
      var that = this;
      var data = { token: http.getToken() };
      data[names] = value ? 1 : 0;
      http.ajaxPost('category/edit/' + id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '修改成功' })
        } else {
          that.$message({ type: 'info', message: '操作失败' })
          that.get_lis();
        }
      })
    }
    , get_lis() {
      var that = this;
      //获取列表
      http.ajaxPost('category', { data: { token: http.getToken() } }, res => {
        that.tableData = res.data.data;
      })
      //获取分类
      http.ajaxPost('category/create', { data: { token: http.getToken() } }, res => {
        that.data_lis = [];
        that.data_lis.push({ id: 0, name: '顶级栏目' })
        for (var i = 0; i < res.data.data.length; i++) { //顶级
          that.data_lis.push({ id: res.data.data[i].id, name: res.data.data[i].name })
          for (var e = 0; e < res.data.data[i].children.length; e++) { //二级
            that.data_lis.push({ id: res.data.data[i].children[e].id, name: '---' + res.data.data[i].children[e].name })
            for (var s = 0; s < res.data.data[i].children[e].children.length; s++) { //三级
              that.data_lis.push({ id: res.data.data[i].children[e].children[s].id, name: '------' + res.data.data[i].children[e].children[s].name })
            }
          }
        }
      })

    }
    , column_add() { //添加栏目
      var that = this;
      that.value_name = that.value_name.trim();  //去空格
      if (that.value_name == null || that.value == null || that.value_name == '') {
        that.$message({ type: 'info', message: '请 填写栏目/选择所在位置' })
      } else {
        http.ajaxPost('category/create', { data: { token: http.getToken(), name: that.value_name, pid: that.value } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '添加成功' })
            that.get_lis();
            that.close();
          } else {
            that.$message({ type: 'info', message: '操作失败' })
          }
        })
      }
    }
    //图片上传
    , handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
    , beforeAvatarUpload(file) {
      return;
    }
    //手机图片
    , pic_status(e) { this.updata_lis.pic = e.url }
    //手机分类图片
    , cover_pic_status(e) { this.updata_lis.cover_pic = e.url }
  }
  , watch: {

  }

}



