import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import settingcompent from '@/views/setting/settingcompent.vue'

import E from 'wangeditor'
export default {
    name: 'Setting'
    , data() {
        return {
             tableData: [] //table表格数据接收包
            ,show:0
            ,order:null
            ,url:null
            ,pic:null
            ,id:null
            ,type:1
            ,max_url: { type: 2 }     
            ,pic_url: 'uploading' 
            ,base_Url:''
        }
    }
    , components: {
        'homelefttop': homelefttop
        , 'homeleftbottom': homeleftbottom
        , 'settingcompent': settingcompent
    }
    , created() {
        this.base_Url = this.axios.defaults.baseURL;
        this.pic_url = this.axios.defaults.baseURL + this.pic_url
        this.get_lis();

        
    }
    , mounted() {
       
    }
    , updated() {
        
        
    }
    , methods: {
        get_lis(){
            var that = this ;
            http.ajaxPost('banners', { data: { token: http.getToken() } }, res => {
                that.tableData = res.data.data;
              })
        }
        ,commodity_del(id){  //删除
            var that = this;
            var http_post = function () {
              http.ajaxPost('banners/destroy', { data: { token: http.getToken(), id: id } }, res => {
                if (res.data.status) {
                  that.$message({ type: 'success', message: '删除成功' })
                  that.get_lis();
                } else { that.$message({ type: 'info', message: '操作失败' }) }
              })
            }
            http.my_alert(that, '删除后无法恢复, 是否继续', '确认删除', http_post);
        }
        ,fahuo(id){ //修改
            var that = this ;
            that.show = 1 ;
            that.id = id ;
            http.ajaxPost('banners/edit/'+ id, {data:{token: http.getToken()}}, res => {
                that.url = res.data.data.url;
                that.order = res.data.data.order;
                that.pic = res.data.data.pic;
              })
        }
        ,id_updata(){ //确认修改
            var that = this ;
            let data = { data: { token: http.getToken(),order:that.order,pic:that.pic,url:that.url} };
            if(that.type){
                http.ajaxPost('banners/edit/'+ that.id, data, res => {
                    if (res.data.status) {
                        that.$message({ type: 'success', message: res.data.message })
                        that.get_lis();
                        that.close();
                      } else { that.$message({ type: 'info', message: res.data.message  }) }
                  })
            }else{
                http.ajaxPost('banners/create', data, res => {
                    if (res.data.status) {
                        that.$message({ type: 'success', message: res.data.message })
                        that.get_lis();
                        that.close();
                      } else { that.$message({ type: 'info', message: res.data.message  }) }
                  })
            } 
        }
        ,add_data(){ //添加轮播图
            var that = this ;
            that.show = 1 ;
            that.type = 0 ;
        
        }
        ,close(){
            this.show  = 0 ;
            this.url = null;
            this.order = null;
            this.pic = null;
            this.type = 1 ;
        }
        ,beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;
      
            if (!isJPG) {
              this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
              this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
          }
          ,handlePreview(){
      
          }
          ,handleRemove(){
      
          }
          ,beforeRemove(){
      
          }
          ,pic_status(e){
              this.pic = e.url;
          }
          ,handleExceed(){
      
          }
          ,fileList_pic(){
      
          }
    }
}



