import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'
import citycom from '@/components/city/city'

export default {
  name: 'Vip_vip_add'
  , data() {
    return {
      level: [] //会员等级
      , base_url: 'uploading'
      , data_url: { type: 2 }
      , agent_province: 1  //控制agent_province 是否显示
      , region: [] //省列表
      , city: [] //市列表
      , city1: [] //市列表
      , district: [] //区列表
      , district1: [] //区列表
      , data_all: {
        name: null
        , password: null
        , is_password: null
        , nickname: null
        , detailed: null
        , mobile: null
        , qq: null
        , email: null
        , level: null
        , status: null
        , province: null
        , city: null
        , district: null
        , avater: null
        , type: null
        , agent_province: null
        , agent_city: null
        , agent_district: null
        , supply: {
          category_id: []
          , shop_logo: null
          , name: null
          , free_postage: null
          , service_mobile: null
          , service_qq: null
          , service_wangwang: null
        }
      }
      , show_data_all: {
        name: '用户名'
        , password: '密码'
        , is_password: '确认密码'
        , nickname: '姓名'
        , detailed: '联系地址'
        , mobile: '联系电话'
        , qq: '联系QQ'
        , email: 'E-mail'
        , level: '会员等级'
        , status: '是否激活'
        , province: '所在区域'
        , city: '所在区域'
        , district: '所在区域'
        , avater: '上传头像'
        , type: '机器人'
        , agent_province: '总代地区'
        , agent_city: '总代地区'
        , agent_district: '总代地区'
        , supply: {
          category_id: '可发布类别'
          , shop_logo: 'LOGO'
          , name: '商店名称'
          , free_postage: '免邮额度'
          , service_mobile: '客服电话'
          , service_qq: '客服QQ'
          , service_wangwang: '客服旺旺'
        }
      }
      , category_id: []
      , imageUrl: ''  //头像
      , imageUrl1: '' //LOGO
      , checked: null
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , vipleftcomponents
    , citycom
  }
  , created() {
    var that = this;
    that.base_url = this.axios.defaults.baseURL + that.base_url;
    this.getlis();

  },
  methods: {
    // 头像上传-------------------------------
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      this.data_all.avater = res.url;
    },
    handleAvatarSuccess1(res, file) {    //LOGO
      this.imageUrl1 = URL.createObjectURL(file.raw);
      this.data_all.supply.shop_logo = res.url;
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    }
    //-----------------------------------------------------
    , go_vip_lis() { //跳转到会员列表页
      this.$router.push({ path: '/home/vip_lis' })
    }
    , getlis() { //页面初次加载获取到的数据
      var that = this;
      http.ajaxPost('user/create', { data: { token: http.getToken() } }, res => {
        if (res.data.status) {
          that.level = res.data.data.level;
          that.region = res.data.data.region;
          that.category_id = res.data.data.category;
        } else { that.$message({ type: 'info', message: '使用数据请求不到,请检查登陆状态或网络' }) }
      })
    }
    , vip_change(id) {  //处理会员选择显示不同的内容
      var that = this;
      for (let i in that.level) {
        if (id == that.level[i].id) {
          that.agent_province = that.level[i].type;
        }
      }
    }
    , add_vip() {
      var that = this;
      var data = {}
      for (let i in that.data_all) {
        data[i] = that.data_all[i];
      }
      if (that.agent_province == 1) {
        delete data.supply;
        delete data.agent_province;
        delete data.agent_city;
        delete data.agent_district;
      } else if (that.agent_province == 2) {
        delete data.supply;
      } else if (that.agent_province == 3) {
        delete data.agent_province;
        delete data.agent_city;
        delete data.agent_district;
        data.supply.category_id = [] ;
        for (let i in that.category_id) {
          if (that.category_id[i].checked == 1 ) {
            data.supply.category_id.push(that.category_id[i].id)
          }
        }
      }
      for (let i in data) {
        if (data[i] == null || data[i] === '') {
          that.$message({ type: 'info', message: '请输入' + that.show_data_all[i] }); return;
        }
        if (data.supply != null) {
          for (let s in data.supply) {
            if (data.supply[s] == null || data.supply[s] === '') {
              that.$message({ type: 'info', message: '请输入' + that.show_data_all.supply[s] }); return;
            }
          }
        }
      }
      if (data.password != data.is_password) {  //判断密码
        that.$message({ type: 'info', message: '两次密码输入不一致' }); return;
      }
      delete data.is_password;
      data.token = http.getToken();
      http.ajaxPost('user/create', { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'warning', message: res.data.message })
          that.go_vip_lis();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    // 三级联动处理--------------------------------------------------------------------------------------------
    , province_id(value) {
      this.data_all.province = value;
    }
    , city_id(value) {
      this.data_all.city = value;
    }
    , district_id(value) {
      this.data_all.district = value;
    }
    , province_id2(value) {
      this.data_all.agent_province = value;
    }
    , city_id2(value) {
      this.data_all.agent_city = value;
    }
    , district_id2(value) {
      this.data_all.agent_district = value;
    }
    //-------------------------------------------------------------------------------------------------------------------------------
  }

}


