import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
export default {
    name: 'Home_page'
    , data() {
        return {
            data_lis:{}
        }
    }
    , components: {
        'homelefttop': homelefttop
        , 'homeleftbottom': homeleftbottom
    }
    , created() {
        this.get_lis();
    }
    , methods: {
        get_lis() {
            var that = this;
            var data = { data:{token: http.getToken()}  };
            http.ajaxPost('datacount',  data , res => {
                that.data_lis = res.data.data
                console.log(res.data)
            })
        }
    }
}


