                <!-- 待审核 -->
                <el-tab-pane label="待审核" name="4">
                    <div class="vip_lis_table">
                        <el-table v-if="type_num=='4'" :data="tableData" @selection-change="handleSelectionChange" ref="multipleTable" border max-height="800" style="width: 100%">
                            <el-table-column  type="selection"  width="35"> </el-table-column>
                            <el-table-column   label="产品"  width="320" > 
                                <template   slot-scope="scope" > 
                                    <ul class="clearfix">
                                        <li style="float:left;width:35%;">
                                            <img :src="img_url+scope.row.cover"  width="66" height="66" style="display:block;margin: 0 auto;border:1px #eee solid" />
                                        </li>
                                        <li style="float:left;width:65%;font-size:12px;min-height:58px;">
                                            <p><span style="color:#000">标题:</span>{{scope.row.name}}</p>
                                        </li>
                                    </ul>          
                                </template> 
                            </el-table-column>
                            <el-table-column   label="价格"  >
                                <template   slot-scope="scope" > 
                                <p><span style="display:inline-block;width:88px" >本站零售价:</span> ¥{{scope.row.retail_price}}</p>
                                <p><span style="display:inline-block;width:88px" >销售成本:</span> ¥{{scope.row.sale_const}}</p>
                                <p><span style="display:inline-block;width:88px" >分销总奖金:</span> ¥{{scope.row.distribution}}</p>
                                </template> 
                            </el-table-column>
                            <el-table-column   label="供货价">
                                <template   slot-scope="scope" > 
                                <p style="margin-top:3px;"><span style="display:inline-block;width:64px" >供应商:</span>{{scope.row.supply.name}}</p>
                                <p style="margin-top:3px;"><span style="display:inline-block;width:64px" >供应成本:</span>{{scope.row.supply_cost}}</p>
                                </template> 
                            </el-table-column>
                            <el-table-column   label="操作"  width="150">
                                <template slot-scope="scope">
                                    <div >
                                        <el-button  @click='lis_other(3,scope.row.id)' size="mini" type="primary">详情</el-button>
                                        <el-button  @click='commodity_del(scope.row.id)'  size="mini" type="danger" >删除</el-button>
                                    </div>
                                    <div style="margin-top:6px">
                                        <el-button size="mini" @click.native="lis_other(1,scope.row.id)" >库存</el-button> 
                                        <el-button size="mini" @click.native="lis_other(8,scope.row.id,scope.row.name,scope.row.market,scope.row.retail_price,scope.row.sale_const,scope.row.supply_cost,scope.row.integral_deduction,scope.row.status,scope.row.auditing)" >通过</el-button>
                                    </div>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                </el-tab-pane>
            </el-tabs>
            
            <!-- 快捷操作 -->
            <div class="operation" style='padding:12px 0 ;background:#fff'>
                <el-button style="margin-left:28px;" @click="all_yes_select(tableData)">全选</el-button><el-button @click="all_yes_select()">全不选</el-button>
                <el-button @click="reverse_select">反选</el-button><el-button  @click="del_all">删除</el-button>
                <el-button  @click="all_status" >{{type_num=='0'?'批量上架':'批量下架'}}</el-button>
                    <el-select size='small' class="select"  v-model="category_all" placeholder="全部分类">
                        <el-option  v-for="(items,k) in category" :key="k" :label="items.name"  :value='items.id'></el-option>
                    </el-select>
                <el-button @click="category_all_updata" >批量修改</el-button>
            </div>
            <!-- 会员列表信息分页 -->
            <div class="page"><pagecon :pages="pages" @get_lis="get_lis"></pagecon></div>                                    
        </div>
    </div>
    <!-- 隐藏区域 -->
    <div v-if="is_other" class="back_shadow" >
        <div class="vip_vip_updata vip_grade" v-if="is_other == 4">
            <div class="vip_set_cont_modle_top">加入抢购<span @click="lis_other(7)" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle" style="padding-top:18px"><span>产品名称:</span><el-input size='small ' v-model="other_data.name" style="width:282px;margin-left:3px"></el-input></div>
            <div class="vip_set_cont_modle" ><span>抢购价:</span><el-input size='small ' @keyup.native="proving('other_data','rush_price')" type="number" step="1" min="0" v-model="other_data.rush_price" style="width:282px;margin-left:3px"></el-input></div>
            <div class="vip_set_cont_modle"><span>开始时间:</span>
                <el-date-picker v-model="other_data.rush_start_time" type="datetime"  placeholder="选择日期时间" style="width:280px" format="yyyy-MM-dd HH:mm:ss"  value-format="timestamp"></el-date-picker>
            </div>          
            <div class="vip_set_cont_modle"><span>结束时间:</span>
                <el-date-picker v-model="other_data.rush_end_time"  type="datetime"  placeholder="选择日期时间" style="width:280px" format="yyyy-MM-dd HH:mm:ss"  value-format="timestamp"></el-date-picker>
            </div>          
            <div class="vip_set_cont_modle" style="text-align:center;margin-top:38px">
                <el-button type="success" @click="other_data_updata(1)"  plain>确认修改</el-button>
                <el-button type="success" @click='clear_show(1)'  plain>取消限时抢购</el-button>
            </div>
        </div>
        <div class="vip_vip_updata vip_grade" v-if="is_other == 5">
            <div class="vip_set_cont_modle_top">加入积分兑换<span @click="lis_other(7)" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle" style="padding-top:18px"><span>产品名称:</span><el-input size='small ' v-model="other_data.name" style="width:282px;margin-left:3px"></el-input></div>
            <div class="vip_set_cont_modle" ><span>积分兑换价:</span><el-input size='small ' @keyup.native="proving('other_data','integral_price')" v-model="other_data.integral_price" style="width:282px;margin-left:3px"></el-input></div>         
            <div class="vip_set_cont_modle" style="text-align:center;margin-top:38px">
                <el-button type="success"  @click="other_data_updata(2)" plain>确认修改</el-button>
                <el-button type="success" @click='clear_show(2)' plain>取消积分兑换</el-button>
            </div>
        </div>
        <div class="vip_vip_updata vip_grade" v-if="is_other == 6">
            <div class="vip_set_cont_modle_top">加入零元购产品<span @click="lis_other(7)" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle" style="padding-top:18px"><span>产品名称:</span><el-input size='small ' v-model="other_data.name" style="width:282px;margin-left:3px"></el-input></div>
            <div class="vip_set_cont_modle" ><span>邮费:</span><el-input size='small'  @keyup.native="proving('other_data','postage')" type="number" step="1" min="0" v-model="other_data.postage" style="width:282px;margin-left:3px"></el-input></div>         
            <div class="vip_set_cont_modle" style="text-align:center;margin-top:38px">
                <el-button type="success"  @click="other_data_updata(3)" plain>确认修改</el-button>
                <el-button type="success" @click='clear_show(3)' plain>取消零元购</el-button>
            </div>
        </div>
        <div class="vip_vip_updata vip_grade" v-if="is_other == 8">
            <div class="vip_set_cont_modle_top">审核商品<span @click="lis_other(7)" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle" style="padding-top:18px"><span>产品名称:</span><el-input  v-model="other_data.name" style="width:282px;margin-left:3px" size='small'></el-input></div>
            <div class="vip_set_cont_modle" ><span>市场价:</span><el-input   @keyup.native="proving('other_data','market')" type="number" step="1" min="0" v-model="other_data.market"  size='small' style="width:282px;margin-left:3px"></el-input></div>         
            <div class="vip_set_cont_modle" ><span>本站价:</span><el-input   @keyup.native="proving('other_data','retail_price')" type="number" step="1" min="0" v-model="other_data.retail_price" size='small' style="width:282px;margin-left:3px"></el-input></div>         
            <div class="vip_set_cont_modle" ><span>销售成本:</span><el-input @keyup.native="proving('other_data','sale_const')" type="number" step="1" min="0" v-model="other_data.sale_const" size='small' style="width:282px;margin-left:3px"></el-input></div>         
            <div class="vip_set_cont_modle" ><span>供货成本:</span><el-input @keyup.native="proving('other_data','supply_cost')" type="number" step="1" min="0" v-model="other_data.supply_cost" size='small' style="width:282px;margin-left:3px"></el-input></div>         
            <div class="vip_set_cont_modle" ><span>积分抵扣限额:</span><el-input @keyup.native="proving('other_data','integral_deduction')" type="number" step="1" min="0" v-model="other_data.integral_deduction" size='small' style="width:282px;margin-left:3px"></el-input></div>         
            <div class="vip_set_cont_modle" ><span>是否上架:</span><el-radio v-model="other_data.status" :label="1">上架</el-radio><el-radio v-model="other_data.status" :label="0">下架</el-radio></div>         
            <div class="vip_set_cont_modle" ><span>是否审核:</span><el-radio v-model="other_data.auditing" auditing :label="1">显示</el-radio><el-radio v-model="other_data.auditing" :label="0">不显示</el-radio></div>         
            <div class="vip_set_cont_modle" style="text-align:center;margin-top:38px">
                <el-button type="success"  @click="other_data_updata(4)" plain>确认审核</el-button>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import commodity_supplier_lis from "./commodity_supplier_lis";
export default commodity_supplier_lis;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 68px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
.back_shadow {
  width: 100%;
  min-width: 1000px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4;
  .vip_vip_updata {
    position: absolute;
    width: 768px;
    background: #fff;
    left: calc(50% - 384px);
    top: 18%;
    border-radius: 12px;
    margin-top: 12px;
    padding: 0 0 18px;
    .vip_set_cont_modle_top {
      background: #409eff;
      border-radius: 12px 12px 0 0;
      height: 58px;
      line-height: 58px;
      position: relative;
      text-align: center;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      span {
        position: absolute;
        font-size: 26px;
        color: #ccc;
        float: right;
        top: 16px;
        right: 16px;
        cursor: pointer;
      }
    }
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 128px;
        text-align: right;
        margin-right: 18px;
      }
      i {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
    }
  }
}
</style>



