import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import huanhuocompent from '@/views/huanhuo/huanhuocompent.vue'

export default {
  name: 'Zhiding'
  , data() {
    return {
        expresss: null
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , huanhuocompent

  }
  , created() {
    var that = this;
    that.get_lis();
  }
  , methods: {
    get_lis() {
      var that = this;
      let data = { data: { token: http.getToken() } };
      that.axios.post('tihuo/express', data).then(res => {
        that.expresss = res.data.data.expresss;
      })
    }
    , is_updata() {
      var that = this;
      let data = {data:{expresss:that.expresss,token:http.getToken('token')}};
      that.axios.post('tihuo/express', data).then(res => {
        if(res.data.message){
          that.$message({
            message:res.data.message ,
            type: 'success'
          });
        }else{that.$message(res.data.message)};
      })
    }

  }
}


