<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>栏目设置</span>
                    <el-button type="success" @click="open_add(0)"  plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">添加一级分类</el-button>
                </div>
            </div>
            <!-- 分类展示 -->
            <div class="column_modle">
                <!-- 顶级  :default-expand-all='true'--> 
                <el-table :data="tableData"   style="width: 100%;" >
                    <el-table-column type="expand" >
                        <template slot-scope="props" style="width:100%;padding:0;margin:0" >
                            <!-- 子级 -->
                            <el-table  :show-header='false' :data="props.row.children"  style="width: 100%;color:#0080ff"  >
                                <el-table-column type="expand" style="background:red">
                                    
                                </el-table-column>
                                <el-table-column   label="导航名称"  show-overflow-tooltip >
                                    <template   slot-scope="scope" > 
                                    <p>{{scope.row.name}}</p>
                                    </template> 
                                </el-table-column>
                                <el-table-column   label="所属分类"  show-overflow-tooltip >
                                    <template   slot-scope="scope" > 
                                    <p>{{scope.row.categoryid}}</p>
                                    </template> 
                                </el-table-column>
                                <el-table-column   label="排序"   width="90" >
                                    <template   slot-scope="scope" > 
                                        <el-input @change='status_updata("order",scope.row.id,scope.row.order)' type="number" size='mini' v-model.lazy="scope.row.order" ></el-input>                                    
                                    </template> 
                                </el-table-column>
                                <el-table-column   label="显示"   width="80"  >
                                    <template   slot-scope="scope" > 
                                        <el-switch :active-value= 1  :inactive-value= 0 v-model="scope.row.status" @change='status_updata("status",scope.row.id,scope.row.status)' ></el-switch>                                   
                                    </template> 
                                </el-table-column>
                                <el-table-column   label="操作"  width="220">
                                    <template slot-scope="scope">
                                        <div >
                                            <el-button size="mini" type="primary">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
                                            <el-button size="mini" type="primary" @click="open_add(scope.row.id)">设置</el-button>
                                            <el-button size="mini" type="danger" @click="data_del(scope.row.id)" >删除</el-button> 
                                        </div>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                    </el-table-column>
                    <el-table-column   label="导航名称"  show-overflow-tooltip >
                        <template   slot-scope="scope" > 
                        <p>{{scope.row.name}}</p>
                        </template> 
                    </el-table-column>
                    <el-table-column   label="所属分类"  show-overflow-tooltip >
                        <template   slot-scope="scope" > 
                        <p>{{scope.row.categoryid}}</p>
                        </template> 
                    </el-table-column>
                    <el-table-column   label="排序"   width="90" >
                        <template   slot-scope="scope" > 
                            <el-input type="number" @change='status_updata("order",scope.row.id,scope.row.order)' size='mini' v-model.lazy="scope.row.order" ></el-input>                                    
                        </template> 
                    </el-table-column>
                    <el-table-column   label="显示"   width="80"  >
                        <template   slot-scope="scope" > 
                            <el-switch :active-value= 1  :inactive-value= 0 v-model="scope.row.status" @change='status_updata("status",scope.row.id,scope.row.status)' ></el-switch>                                   
                        </template> 
                    </el-table-column>
                    <el-table-column   label="操作"  width="220">
                        <template slot-scope="scope">
                            <div >
                                <el-button size="mini" type="primary" @click="open_add(scope.row.id,1)">添加</el-button>
                                <el-button size="mini" type="primary" @click="open_add(scope.row.id)">编辑</el-button>
                                <el-button size="mini" type="danger"  @click="data_del(scope.row.id)" >删除</el-button> 
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="commodity_parameter_foot">
                <p>页面功能说明：</p>
                <p>产品参数管理，可以预设产品的所有特定参数，根据特定参数可指定相关类别产品，设定后，在上传产品时将会显示选择参数，在前台可根据参数进行复合搜索</p>
            </div>
        </div>
    </div>
    <!-- 隐藏区域 -->
    <div v-if="is_other" class="back_shadow" >
        <div class="vip_vip_updata vip_grade">
            <div class="vip_set_cont_modle_top">{{title_top}}<span @click="close" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle" style="padding-top:18px"><span>导航名称:</span><el-input size='small ' v-model="data_lis.name" style="width:282px"></el-input></div>
            <div class="vip_set_cont_modle">
                <span>所属分类:</span>
                <el-select class="select" placeholder="请选择分类" size='small' v-model="data_lis.category_id" >
                    <el-option v-for="(item,index) in category" :key="index" :label="item.name" :value="item.id">{{item.name}}</el-option>
                </el-select>
                <i style="color:#606222">只需要一级栏目选择</i>
            </div>
            <div class="vip_set_cont_modle">
                <span>所属栏目:</span>
                <el-select class="select" placeholder="请选择栏目" size='small' v-model="data_lis.pid" >
                    <el-option v-for="(item,index) in column" :key="index" :label="item.name" :value="item.id">{{item.name}}</el-option>
                </el-select>
            </div>
             <div class="vip_set_cont_modle">
                <span>是否显示:</span>
                <el-radio v-model="data_lis.status" :label="1">是</el-radio>
                <el-radio v-model="data_lis.status" :label="0">否</el-radio>
            </div>
            <div class="vip_set_cont_modle"><span>排序:</span><el-input type="number" v-model="data_lis.order" size='small ' ></el-input></div>            
            <el-button type="success" @click="lis_updata" plain>提 交</el-button>
        </div>
    </div>
</div>
</template>
<script>
import commodity_parameter from "./commodity_parameter";
export default commodity_parameter;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    min-height: 100px;
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
  }
  .commodity_parameter_foot {
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
    height: 58px;
    p:first-child {
      font-weight: 700;
    }
    p {
      width: 90%;
      margin: 0 auto;
      padding-top: 12px;
    }
  }
}
.back_shadow {
  width: 100%;
  min-width: 1000px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4;
  .vip_vip_updata {
    position: absolute;
    width: 768px;
    background: #fff;
    left: calc(50% - 384px);
    top: 15%;
    border-radius: 12px;
    margin-top: 12px;
    padding: 0 0 18px;
    .vip_set_cont_modle_top {
      background: #409eff;
      border-radius: 12px 12px 0 0;
      height: 58px;
      line-height: 58px;
      position: relative;
      text-align: center;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      span {
        position: absolute;
        font-size: 26px;
        color: #ccc;
        float: right;
        top: 16px;
        right: 16px;
        cursor: pointer;
      }
    }
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 108px;
        text-align: right;
        margin-right: 18px;
      }
      i {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
}
</style>



