import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import pagecon from '@/components/page/pagecon'
import citycom from '@/components/city/city'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'

export default {
  name: 'Vip_lis'
  , data() {
    return {
      value: null,
      time_data: null
      , tableData: [] //table表格数据接收包
      , pages: null
      // ,is_updata:0 //通知遮罩层
      , is_other: 0 //其他操作显示
      , imageUrl: null
      , imageUrl1: null
      , base_Url: ''
      , multipleSelection: []       //记录勾选内容
      , province: null
      , city: null
      , district: null
      , level: []   //选择等级数据
      , levels: null  //要修改的内容
      , order: []   //选择排序
      , updata_data: {}  //编辑数据
      , min_url: { type: 2 }                                                  //上传图片分类
      , pic_url: 'uploading'                                                  //上传图片接口
      , puil_id: null
      , grade: {}   //设置等级
      , updata_user: {} //设置推荐人
      , receivables: {} //收款信息 
      , recharge: {} //充值 
      , content: null // 站内信内容
      , recharge_type: [] //充值类型 
      , supplier_data: {}  //供应商
      , supply: {}  //供应商数据
      , search: {}  //搜索内容
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , vipleftcomponents
    , pagecon
    , citycom
  }
  , created() {
    this.get_lis();
    this.base_Url = this.axios.defaults.baseURL;
    this.pic_url = this.axios.defaults.baseURL + this.pic_url
  }
  , methods: {
    go_vip_vip_add() {//跳转至添加会员页面
      this.$router.push({ path: '/home/vip_vip_add' })
    }
    , go_vip_details(e) {//跳往会员详情
      this.$router.push({ path: '/home/vip_details', query: { id: e } })
    }
    , vip_other(e, id, status, level_id) { //显示其他操作选项
      var that = this;
      if (e == 1) { //编辑
        that.puil_id = id;
        http.ajaxPost('user/edit/' + id, { data: { token: http.getToken() } }, res => {
          if (res.data.status) {
            that.updata_data = res.data.data;
            that.imageUrl = this.axios.defaults.baseURL + that.updata_data.avater;
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      } else if (e == 2) {  //收款信息
        that.puil_id = id;
        http.ajaxPost('user/edit/' + id, { data: { token: http.getToken() } }, res => {
          if (res.data.status) {
            that.receivables = res.data.data;
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
        that.puil_id = id;
        that.grade.status = status;
        if (level_id != null) {
          that.grade.level = level_id.id;
        }
      } else if (e == 4) { //账户减充
        that.puil_id = id;
        that.recharge.name = status;
        http.ajaxPost('user/user_allowance/' + id, { data: { token: http.getToken() } }, res => {
          if (res.data.status) {
            that.recharge_type = res.data.data.type;
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      } else if (e == 5) {
        that.updata_user.username = status;
        that.puil_id = id;
      } else if (e == 6) {
        that.puil_id = id;
      } else if (e == 7) {
        that.puil_id = id;
        http.ajaxPost('user/edit/' + id, { data: { token: http.getToken() } }, res => {
          if (res.data.status) {
            that.updata_data = res.data.data;
            that.updata_data.name = status;
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      } else if (e == 8) {
        that.puil_id = id;
        http.ajaxPost('user/update_supplier/' + id, { data: { token: http.getToken() } }, res => {
          if (res.data.status) {
            that.supplier_data = res.data.data;
            if (that.supplier_data.supply != null) {
              that.imageUrl = that.axios.defaults.baseURL + that.supplier_data.supply.shop_logo;
              that.imageUrl1 = that.axios.defaults.baseURL + that.supplier_data.supply.mobile_cover;
              that.supply = that.supplier_data.supply;
              if (that.supplier_data.supply.category_id.length != 0) {
                let checkbox_id = that.supplier_data.supply.category_id;
                for (let i in that.supplier_data.category) {
                  if (that.supplier_data.category[i].id == checkbox_id[i]) {
                    that.supplier_data.category[i].checked = 1;
                  }
                }
              }
            } else { that.supplier_data.level = that.supplier_data.level_list[0] }
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      } else if (e == 9) { //网页二维码
        http.ajaxPost('user/user_qrcode/' + id, { data: { token: http.getToken() } }, res => {
          var URL = that.base_Url + res.data.url;
          window.open(URL, '_blank', 'width=320,height=568,menubar=no,toolbar=no,status=no,scrollbars=yes')
        })
        return;
      } else if (e == 10) { //微信二维码
        window.open('http://www.baidu.com', '_blank', 'width=320,height=568,menubar=no,toolbar=no,status=no,scrollbars=yes')
        return;
      } else if (e == 11) { //重置密码
        http.my_alert(this, '如果您确认后，当前会员的密码将会重置', '密码是否重置', res => {
          this.$prompt('请输入密码', '密码重设', {
            confirmButtonText: '确定',
            cancelButtonText: '取消'
          }).then(({ value }) => {
            http.ajaxPost('user/edit/' + id, { data: { token: http.getToken(), password: value } }, res => {
              if (res.data.status) {
                that.$message({ type: 'success', message: res.data.message })
              } else { that.$message({ type: 'info', message: res.data.message }) }
            })
          }).catch(() => {

          })
        })
        return;
      } else if (e == 12) { //重置二维码
        http.my_alert(this, '如果您确认后，当前会员的重置二维码将会重置', '二维码是否重置', res => { })
        return;
      } else if (e == 88) { //删除 
        var http_function = function () {
          var data = { id, token: http.getToken() };
          http.ajaxPost('user/destroy', { data }, res => {
            if (res.data.status) {
              that.$message({ type: 'success', message: '删除成功' })
              that.get_lis();
            } else { that.$message({ type: 'info', message: '操作失败' }) }
          })
        }
        http.my_alert(this, '如果您确认后，当前会员将会删除', '是否删除', res => {
          http_function();
        })
        return;
      }
      this.is_other = e;
    }
    , close() {//关闭
      this.is_other = 0;
      this.updata_data = {};
      this.puil_id = null;
      this.grade = {};
      this.updata_user = {};
      this.receivables = {};
      this.recharge = {};
      this.content = null;
      this.supplier_data = {};
      this.supply = {};
      this.imageUrl = null;
      this.imageUrl1 = null;
    }
    , search_btn() {//搜索
      var that = this;
      let data = {};
      data.screen = that.search;
      data.token = http.getToken();
      http.ajaxPost('user', { data }, res => {
        if (res.data.status) {
          that.tableData = res.data.data.data;
          that.pages = res.data.data;
          delete that.pages.data;
        } else { that.$message({ type: 'info', message: res.data.message }); that.tableData = {}; }
      })
    }
    , grade_updata() { //设置等级
      var that = this;
      let data = that.grade;
      data.token = http.getToken();
      http.ajaxPost('user/edit/' + that.puil_id, { data }, res => {
        console.log(res.data)
        if (res.data.status) {
          that.$message({ type: 'success', message: '操作成功' })
          that.get_lis();
          that.close();
        } else { that.$message({ type: 'info', message: '操作失败' }) }
      })
    }
    , id_updata() { //提交编辑会员信息
      var that = this;
      var data = that.updata_data;
      data.token = http.getToken();
      http.ajaxPost('user/edit/' + that.puil_id, { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '修改成功' })
          that.get_lis();
          that.close();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , status_change(status, id) { //修改激活状态
      var that = this;
      http.ajaxPost('user/edit/' + id, { data: { token: http.getToken(), status: status } }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message })
        } else { that.$message({ type: 'info', message: res.data.message }); that.get_lis(); }
      })
    }
    , status_change1(type, id) { //修改激活状态
      var that = this;
      that.$confirm('修改成为合伙人', '确认修改成为合伙人吗', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then( ()=> {
        http.ajaxPost('/user/updates_hehuoren', { data: { token: http.getToken(), id,type } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: res.data.message })
          } else { that.$message({ type: 'info', message: res.data.message }); that.get_lis(); }
        })
      }
      )
      .catch(() => {
        
          that.$message({
            type: 'info',
            message: '已取消操作'
          });
          that.get_lis();
      });
    
    }
    , is_updata_user() {  //修改推荐人
      var that = this;
      http.ajaxPost('user/update_relation', { data: { token: http.getToken(), id: that.puil_id, name: that.updata_user.name } }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message });
          that.close();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , updata_receivables() {  //修改收款信息
      var that = this;
      let data = that.receivables;
      data.token = http.getToken();
      http.ajaxPost('user/edit/' + that.puil_id, { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message });
          that.close();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , updata_recharge() { //提交充减
      var that = this;
      let data = that.recharge;
      if (data.type == null) {
        that.$message({ type: 'info', message: '请选择充值类型' });
        return;
      }
      if (data.number == null) {
        that.$message({ type: 'info', message: '请输入金额' });
        return;
      }
      data.token = http.getToken();
      http.ajaxPost('user/user_allowance/' + that.puil_id, { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message });
          that.close();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , updata_content() {  //发送站内信
      var that = this;
      let data = {}
      data.token = http.getToken();
      if (data.content == null) {
        that.$message({ type: 'info', message: '请输入发送内容' });
        return;
      }
      data.content = that.content;
      data.id = that.puil_id;
      http.ajaxPost('user/inletter', { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message });
          that.close();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , agent() { //代理商修改
      var that = this;
      let data = that.updata_data;
      delete data.name;
      delete data.level_list;
      data.token = http.getToken();
      http.ajaxPost('user/edit/' + that.puil_id, { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message });
          that.close();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })

    }
    , updata_supply() { //供应商
      var that = this;
      let data = {};
      that.supplier_data.supply = that.supply;
      data = that.supplier_data;
      data.token = http.getToken();
      data.category_id = [];
      for (let i in that.supplier_data.category) {
        if (that.supplier_data.category[i].checked) {
          data.category_id.push(that.supplier_data.category[i].id);
        }
      }
      http.ajaxPost('user/update_supplier/' + that.puil_id, { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message });
          that.close();
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , get_lis(e) { //获取table数据  
      var that = this;
      http.ajaxPost('user', { data: { token: http.getToken(), screen: that.search }, page: e }, res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      })
      http.ajaxPost('get_screen', { data: { type: 2 } }, res => {
        if (res.data.status) {
          that.level = res.data.data.level;
          that.order = res.data.data.order;
        } else { that.$message({ type: 'info', message: '获取数据失败' }) }
      })
    }
    // 头像上传
    , handleAvatarSuccess(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.imageUrl = this.axios.defaults.baseURL + res.url;
      this.updata_data.avater = res.url;
    }
    , handleAvatarSuccess1(res, file) {
      this.imageUrl = this.axios.defaults.baseURL + res.url;
      this.supply.shop_logo = res.url;
    }
    , handleAvatarSuccess2(res, file) {
      this.imageUrl1 = this.axios.defaults.baseURL + res.url;
      this.supply.mobile_cover = res.url;
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , handleSelectionChange(val) {  //选中的元素
      this.multipleSelection = val;
    }
    , all_yes_select(rows) { //快捷全选   全不选
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    }
    , reverse_select() {
      var that = this;
      if (that.multipleSelection.length == 0) { //如果没有勾选  反选就是全选
        that.all_yes_select(that.tableData);
      } else {
        var select_data = []; //存放已经被勾选的数据
        var new_data = []; //反选后填入的数据
        for (let i in that.tableData) { //首先存入全部数据
          new_data.push(that.tableData[i])
        }
        for (var i in that.multipleSelection) { //此时 获取之前 已经勾选过得数据 
          select_data.push(that.multipleSelection[i])
        }
        that.all_yes_select();//清空之前勾选过得内容
        //获取没有被勾选的数据
        for (var i = 0; i < select_data.length; i++) {
          for (var j = 0; j < new_data.length; j++) {
            if (new_data[j] == select_data[i]) {
              new_data.splice(j, 1)
            }
          }
        }
        if (new_data.length == 0) { //如果没有
          that.all_yes_select();
        } else {
          that.all_yes_select(new_data);
        }
      }
    }
    , del_all() { //全部删除
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '请选择删除内容' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      var http_post = function () {
        http.ajaxPost('user/destroy', { data: data }, (res) => {
          console.log(res.data)
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '删除成功' })
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '删除后无法恢复,您确定要删除吗?', '全部删除', http_post)
    }
    , updata_all() {  //批量修改
      var that = this;
      if (that.multipleSelection.length == 0 || that.levels == null || that.levels == 0) { that.$message({ type: 'info', message: '请选择修改内容' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      data.level = that.levels;
      var http_post = function () {
        http.ajaxPost('user/updates', { data }, (res) => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '修改成功' })
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '您确定要批量修改吗?', '全部修改', http_post)
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    //城市三级联动
    , province_id(value) {
      this.province = value;
    }
    , city_id(value) {
      this.city = value;
    }
    , district_id(value) {
      this.district = value;
    }
    , province_id_search(value) {
      this.search.province = value;
    }
    , city_id_search(value) {
      this.search.city = value;
    }
    , district_id_search(value) {
      this.search.district = value;
    }
    //修改的三级联动
    , province_ids(value) {
      this.updata_data.province = value;
    }
    , city_ids(value) {
      this.updata_data.city = value;
    }
    , district_ids(value) {
      this.updata_data.district = value;
    }
    //修改的三级联动
    , province_idss(value) {
      this.supply.province = value;
    }
    , city_idss(value) {
      this.supply.city = value;
    }
    , district_idss(value) {
      this.supply.district = value;
    }
  }
}


