import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'
import E from 'wangeditor'
export default {
    name: 'Commodity_brand'
    , data() {
        return {
            editorContent: ''                                                       //富文本内容
            , title: '添加产品'
            , img_url: []                                                           // 图片
            , max_url: { type: 2 }                                                  //上传图片  
            , min_url: { type: 1 }                                                  //上传图片分类
            , pic_url: 'uploading'                                                  //上传图片接口
            , all_data: {                                                           //全部填写的数据
                category: [{ category_id: null }]                                   //初始化栏目
                , product_parameter: [{ name: null, values: null }]                  //初始化产品参数标题
                , product_plus_price: [{ name: null, values: null }]                 //初始化加价属性标题
                , product_pic: [{ pic_name: null, pic: null, big_pic: null }]       //初始化产品配图  
                , number: null                                                      //编号
                , name: null                                                        //商品名称
                , market: 0
                , retail_price: 0
                , sale_const: 0
                , weight: null
                , weight_type: null
                , cover: null
                , pic: null
                , stock: 0
                , sales_volume: 0
                , publisher: null
                , updated_at: new Date()
                , click_volume: null
                , order: null
                , recommend: null
                , hot_sale: null
                , new_product: null
                , status: null
                , content: null
            }
            , brand: []                     //全部商品品牌
            , category: []                  //全部栏目
            , supply: []                    //全部供货商
            , fileList_pic: []              //商品主图
            , fileList_cover: []            //商品封面图
            , fileList_product_pic_pic: []  //缩略图
            , fileList_big_pic: []          //大图
            , upload_type: {}               //存储当前点击上传的位置
            , product_level: []             //会员等级价格
            , updata_vip_show: 0
            , cont_is: {
                number: '编号'
                , name: '商品名称'
                , market: '市场价'
                , retail_price: '保险金'
                , sale_const: '销售成本价'
                , weight: '重量'
                , weight_type: '重量单位'
                , stock: '商品库存'
                , sales_volume: '销售量'
                , publisher: '产品发布人'
                , updated_at: '更新时间'
                , click_volume: '点击量'
                , order: '排序'
                , recommend: '是否推荐'
                , hot_sale: '是否热销'
                , new_product: '是否新品'
                , status: '上架/下架'
                , content: '内容描述'
                , cover: '商品封面'
                , pic: '商品主图'
            }
            , category_id: null
        }
    }
    , components: {
        'homelefttop': homelefttop
        , 'homeleftbottom': homeleftbottom
        , 'commodityleftcomponents': commodityleftcomponents
    }
    , created() {
        var that = this; //all_data.publisher
        that.pic_url = that.axios.defaults.baseURL + that.pic_url;
        that.get_lis();// Number.isInteger('参')  判断是否为字符串
        if (that.$route.query.id) {  //修改
            that.title = '修改产品'
            http.ajaxPost('product/edit/' + that.$route.query.id, { data: { token: http.getToken() } }, res => {
                if (res.data.status) {
                    that.all_data = res.data.data.product
                    that.all_data.updated_at = that.all_data.updated_at * 1000; //更新时间转化
                    that.editorContent = that.all_data.content;
                    that.all_data.publisher = that.publisher;      //产品发布人
                    that.fuwenben(); //重新挂载富文本插件
                    //-------处理图片上传 
                    that.fileList_cover.push({ name: '商品封面图', value: that.all_data.cover })//商品封面图 
                    that.fileList_pic.push({ name: '商品主图', value: that.all_data.cover })//商品主图
                    // fileList_product_pic_pic 缩略图 fileList_big_pic 大图 
                    if (that.all_data.product_pic.length == 0) {
                        that.all_data.product_pic = [{ pic_name: null, pic: null, big_pic: null }];
                    } else {
                        for (let i in that.all_data.product_pic) {
                            that.fileList_product_pic_pic.push([{ name: '产品缩略图', value: that.all_data.product_pic[i].pic }]);
                            that.fileList_big_pic.push([{ name: '产品大图', value: that.all_data.product_pic[i].big_pic }]);
                        }
                    }
                    //会员等级选择显示
                    if (that.all_data.product_level) {
                        if (that.all_data.product_level.length > 0) {
                            that.product_level = that.all_data.product_level;
                            that.updata_vip_show = 1;
                        }
                    }
                    //product_parameter 产品参数标题 数据处理 方便后台操作判断
                    if (that.all_data.product_parameter.length == 0) {
                        that.all_data.product_parameter = [{ name: null, values: null }]
                    } else {
                        for (let i in that.all_data.product_parameter) {
                            delete that.all_data.product_parameter[i].id;
                            delete that.all_data.product_parameter[i].product_id;
                        }
                    }
                    //product_plus_price 加价属性标题 数据处理 方便后台操作判断
                    if (that.all_data.product_plus_price.length == 0) {
                        that.all_data.product_plus_price = [{ name: null, values: null }]
                    } else {
                        for (let i in that.all_data.product_plus_price) {
                            delete that.all_data.product_plus_price[i].id;
                            delete that.all_data.product_plus_price[i].product_id;
                        }
                    }
                } else { that.$message({ type: 'info', message: '获取产品信息失败,请检查网络' }) }
            })
        }
    }
    , mounted() {
        this.fuwenben();
    }
    , updated() {

    }
    , methods: {
        get_lis() {
            var that = this;
            http.ajaxPost('product/create', { data: { token: http.getToken() } }, res => {
                for (var i = 0; i < res.data.data.category.length; i++) {                       //顶级
                    that.category.push({ id: res.data.data.category[i].id, name: res.data.data.category[i].name })
                    if (res.data.data.category[i].children) {
                        for (var e = 0; e < res.data.data.category[i].children.length; e++) {   //二级
                            that.category.push({ id: res.data.data.category[i].children[e].id, name: '---' + res.data.data.category[i].children[e].name })
                            for (var s = 0; s < res.data.data.category[i].children[e].children.length; s++) { //三级
                                that.category.push({ id: res.data.data.category[i].children[e].children[s].id, name: '------' + res.data.data.category[i].children[e].children[s].name })
                            }
                        }
                    }
                }
                that.publisher = res.data.data.publisher;               //产品发布人
                that.all_data.publisher = res.data.data.publisher;      //产品发布人
            })
        }
        , go_commodity_lis() {   //返回商品列表
            this.$router.push({ path: '/home/commodity_lis' })
        }
        , fuwenben() {          //富文本
            var that = this;
            var editor = new E('#editorElem')
            editor.customConfig.uploadImgShowBase64 = true   // 使用 base64 保存图片
            editor.customConfig.onchange = (html) => {
                this.editorContent = html
            }
            editor.customConfig.uploadImgHooks = {
                before: function (xhr, editor, files) { },
                success: function (xhr, editor, result) { },
                fail: function (xhr, editor, result) { },
                error: function (xhr, editor) { },
                timeout: function (xhr, editor) { },
                customInsert: function (insertImg, result, editor) {
                    var url = that.axios.defaults.baseURL + result.url
                    insertImg(url)
                }
            }
            editor.customConfig.uploadImgServer = that.axios.defaults.baseURL + 'uploading'
            editor.customConfig.uploadFileName = 'file'
            editor.customConfig.uploadImgParams = {
                type: 2
            }
            editor.create()
            if (that.$route.query.id) {
                editor.txt.html(that.all_data.content)
            } else {
                editor.txt.html('<p>请输入商品详细</p>')
            }
        }
        , add_category(index) { //增加删除栏目组 
            var that = this;
            if (!index) {
                that.all_data.category.push({ category_id: null });
            } else { that.all_data.category.splice(index, 1) }
        }
        , add_product_parameter(index) {//增加删除产品参数标题
            var that = this;
            if (!index) {
                that.all_data.product_parameter.push({ name: null, values: null });
            } else { that.all_data.product_parameter.splice(index, 1) }
        }
        , add_product_plus_price(index) {//增加删除加价属性标题
            var that = this;
            if (!index) {
                that.all_data.product_plus_price.push({ name: null, values: null });
            } else { that.all_data.product_plus_price.splice(index, 1) }
        }
        , add_product_pic(index) { //增加删除化产品配图
            var that = this;
            if (!index) {
                that.all_data.product_pic.push({ pic_name: null, pic: null, big_pic: null });

            } else { that.all_data.product_pic.splice(index, 1) }
            console.log(that.all_data.product_pic);
        }
        , updata_vip() { //展开会员等级价格
            var that = this;
            that.all_data.product_level = that.product_level//初始化编辑会员等级价格
            that.updata_vip_show = 1;
        }
        , handleExceed() { //上传图片超出后执行
            this.$message({ type: 'info', message: '仅能上传单张图片,如要替换请删除上一张' })
        }
        , beforeRemove() {

        }
        , handleRemove() {

        }
        , handlePreview() {

        }
        , beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif';
            const isLt2M = file.size / 1024 < 500;
            if (!isJPG) {
                this.$message.error('上传图片只能是 JPG 和 png 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 500kb!');
            }
            return isJPG && isLt2M;
        }

        , proving(name, top, index) { //限制输入数字格式
            if (index !== undefined) {
                this.all_data[top][index][name] = this.all_data[top][index][name].replace(/[^\.\d]/g, ''); //为正数
                this.all_data[top][index][name] = this.all_data[top][index][name].match(/^\d*(\.?\d{0,2})/g)[0]; //保留小数点两位
            } else {
                this.all_data[name] = this.all_data[name].replace(/[^\.\d]/g, ''); //为正数
                if (top === 1) {
                    this.all_data[name] = this.all_data[name].replace('.', ''); //不保留小数点
                } else {
                    this.all_data[name] = this.all_data[name].match(/^\d*(\.?\d{0,2})/g)[0]; //保留小数点两位 
                }
            }
            if (name == 'retail_price') {
                this.all_data.distribution = this.all_data.retail_price - this.all_data.sale_const;
            }
            console.log(name)
            if (name == 'sale_const') {
                this.all_data.distribution = this.all_data.retail_price - this.all_data.sale_const;
            }
        }
        , pic_status(e) { //图片上传成功后 添加到上传数据中
            var that = this;
            if (that.upload_type.index === undefined) { // 商品封面图 和 商品主图
                that.all_data[that.upload_type.father] = e.url;
            } else { //产品配图的缩略图 和 大图
                that.all_data[that.upload_type.father][that.upload_type.index][that.upload_type.name] = e.url
            }
        }
        , now_add(father, name, index) { //点击的某个上传图片按钮
            this.upload_type = { father: father, name: name, index: index }
        }
        , sure_add() { //确定添加 
            var that = this;
            that.all_data.content = that.editorContent; //富文本内容赋值到上传数据中
            //--------------------------提交前的判断
            for (let i of that.all_data.category) {//判断用户是否选择栏目
                if (i.category_id === null) {
                    that.$message({ type: 'info', message: '请选择分类' }); return;
                }
            }
            for (let i in that.all_data) { //判断上传数据 直接子级是否填写数据
                if (that.all_data[i] === null || that.all_data[i] === '') {
                    that.$message({ type: 'info', message: '请填写 / 选择 ' + that.cont_is[i] }); return;
                }
            }
            if (that.updata_vip_show) { //判断用户是否选择编辑会员等级价格 (选择后判断是否填写)
                for (let i of that.product_level) {
                    if (i.level_price === null || i.level_price === '') {
                        that.$message({ type: 'info', message: '请编辑 ' + i.level_name + '的会员价格' }); return;
                    }
                }
            }
            for (let i of that.all_data.product_pic) {//判断用户是否填写产品配图 
                if (i.pic_name !== null || i.pic !== null || i.big_pic !== null) {
                    that.$message({ type: 'info', message: '填写/上传 产品配图/名称,会影响库存' }); 
                }
            }
            for (let i of that.all_data.product_parameter) {//判断用户是否填写产品参数标题 
                if (i.name !== null || i.name === '' || i.value !== null || i.value === '') {
                    that.$message({ type: 'info', message: '填写产品参数标题,会影响库存' }); 
                }
            }
            for (let i of that.all_data.product_plus_price) {//判断用户是否填写加价属性标题 
                if (i.name !== null || i.name === '' || i.value !== null || i.value === '') {
                    that.$message({ type: 'info', message: '填写加价属性标题,会影响库存' }); 
                }
            }
            that.all_data.updated_at = that.all_data.updated_at / 1000;
            var data = that.all_data;
            data.token = http.getToken();
            if (that.$route.query.id) {       //修改逻辑
                http.ajaxPost('product/edit/' + that.$route.query.id, { data: data }, res => {
                    if (res.data.status) {
                        that.$message({ type: 'success', message: '修改成功' })
                        that.go_commodity_lis();
                    } else { that.$message({ type: 'info', message: res.data.message }); that.all_data.updated_at = that.all_data.updated_at * 1000; }
                })
            } else {                          //添加逻辑
                http.ajaxPost('product/create', { data: data }, res => {
                    if (res.data.status) {
                        that.$message({ type: 'success', message: '添加成功' })
                        that.go_commodity_lis();
                    } else { that.$message({ type: 'info', message: res.data.message }); that.all_data.updated_at = that.all_data.updated_at * 1000; }
                })
            }
        }
    }
}



