<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>{{title}}</span>
                    <el-button type="success" @click="go_commodity_lis"  plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">返回产品列表</el-button>
                </div>
            </div>
            <!-- 分类展示 -->
            <div class="column_modle">
               <ul>
                   <li><span>编号</span><el-input size="mini" style="width:218px" v-model="all_data.number" clearable></el-input></li>
                   <li><span>商品名称</span><el-input size="mini" style="width:688px"  v-model="all_data.name" clearable></el-input></li>
                   <li><p>PS:商品名称就是商品的标题部分，建议商品关键词组合为佳，写长点！编号：是对应您的ERP系统系统的产品编号，将来可用于导入ERP库存</p></li>
                   <li v-for="(item,j) in all_data.category" :key="j"><span><i @click="add_category(j)" v-if="j==0">[+]</i><i  @click="add_category(j)" v-if="j!=0">[-]</i>选择分类</span>
                        <el-select   size='mini' v-model="item.category_id">
                            <el-option  v-for="(items,x) in category" :key="x" :label="items.name"  :value='items.id'> {{items.name}}</el-option>
                        </el-select>
                    </li>
                   <li><p>PS:选择商品相关分类，点击选择栏目左侧“[+]”可增加所属分类选择，就是指一个产品可以放在多个分类下</p></li>
                   <li>
                        <span>销售成本价</span><el-input @keyup.native="proving('sale_const')" v-model="all_data.sale_const" size="mini" style="width:158px"  type="number"   min="0" step="1" clearable></el-input>
                        <span>保险金</span><el-input @keyup.native="proving('retail_price')" v-model="all_data.retail_price" size="mini" style="width:158px" type="number"   min="0" step="1" clearable></el-input>
                    </li>
                  
                    <li><p>PS：如果非本站商品，这里将记录上传该商品的供货商名称及他给到您的供货价，系统将根据供货价实时结算给供应商；本站商品请忽略</p></li>
                    <li v-if="updata_vip_show"><p>PS：以上为各个等级的会员价格，如果根据会员统一折扣来打折，则无需填写，如果不按统一折扣，可分别填写价格</p></li>
                    <li>
                        <span>重量</span><el-input  @keyup.native="proving('weight')"  v-model="all_data.weight"  size="mini" style="width:158px" type="number"   min="0" step="1"  clearable></el-input>
                        <span>单位</span><el-radio v-model="all_data.weight_type" :label="1">KG</el-radio><el-radio v-model="all_data.weight_type" :label="0">G</el-radio>
                    </li>
                    <li>
                        <span>商品封面图</span>
                        <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                            :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_cover" :data="max_url" > <el-button @click="now_add('cover','cover')" size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                        <span style="width:48px">商品主图</span>
                        <el-upload style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                             :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_pic" :data="max_url"> <el-button size="small" @click="now_add('pic','pic')" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </li>
                    <li><p>PS：商品封面图是显示在商品列表处，尺寸280*280，商品主图：显示在商品详细页，尺寸450*450</p></li>
                     <!-- <li v-for="(item,index) in all_data.product_pic" :key="index"> -->
                     <li v-for="(item,index) in all_data.product_pic" >
                         <span><i @click="add_product_pic(index)" v-if="index==0">[+]</i><i  @click="add_product_pic(index)" v-if="index!=0">[-]</i>产品配图名称</span>
                       <el-input size="mini" style="width:158px" v-model="item.pic_name" clearable></el-input>
                       <span style="width:48px">缩略图</span>
                        <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                            :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_product_pic_pic[index]" :data='min_url' ><el-button size="small"  @click="now_add('product_pic','pic',index)" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                       <span style="width:48px">大图</span>
                        <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                            :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_big_pic[index]" :data="max_url" ><el-button size="small" @click="now_add('product_pic','big_pic',index)" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </li>
                    <li><p>PS：商品配图的缩略图是显示在商品详细页标题下面的选择器，尺寸80*80，比如颜色选择，大图是显示在产品详细页主图位置，尺寸：450*450</p></li>
                    <!-- <li v-for="(items,s) in all_data.product_parameter" :key="s" > -->
                    <li v-for="(items,s) in all_data.product_parameter" >
                        <!-- <span><i @click="add_product_parameter(s)" v-if="s==0">[+]</i><i  @click="add_product_parameter(s)" v-if="s!=0">[-]</i>产品参数标题</span> -->
                        <span><i></i> 产品参数标题</span>
                        <el-input size="mini" style="width:158px" v-model="items.name" clearable></el-input>
                        <span>属性值</span>
                        <el-input size="mini"  style="width:358px" v-model="items.values" clearable></el-input>
                    </li>
                    <li ><p>PS：商品属性 例如服装商品，有尺码选择，标题：尺码；属性值：X,L,XXL,XXXL 注意：属性值间用“,”隔开</p></li>
                    <!-- <li v-for="(items,ss) in all_data.product_plus_price" :key="ss"> -->
                    <li v-for="(items,ss) in all_data.product_plus_price" >
                        <span><i @click="add_product_plus_price(ss)" v-if="ss==0">[+]</i><i  @click="add_product_plus_price(ss)" v-if="ss!=0">[-]</i>加价属性标题</span>
                        <el-input size="mini" v-model="items.name" style="width:158px" clearable></el-input>
                        <span>递增价</span>
                        <el-input size="mini"  @keyup.native="proving('values','product_plus_price',ss)"   v-model="items.values" style="width:158px" type="number"   min="0" step="1"  clearable></el-input>
                    </li>
                    <li><p>PS：加价属性用于同一款产品，不同型号或包装，不同价格；效果可见商品详细页，点击选择属性，价格会变动</p>
                        <p>如：主商品发布新生儿奶粉为88元，加价参数填写 标题：新生儿 递增价：0 标题：6-12月 递增价20</p>
                    </li>
                    <li><span style="font-size:14px;padding:8px 0 ;">内容描述</span></li>
                    <li>
                        <div class='tinymce' id="editorElem" style="text-align:left"></div>
                    </li>
                     <li>
                        <span>商品库存</span><el-input  @keyup.native="proving('stock',1)"  v-model="all_data.stock" size="mini"  style="width:158px" clearable></el-input>
                        <span>销售量</span><el-input  @keyup.native="proving('sales_volume',1)"  v-model="all_data.sales_volume" size="mini" style="width:158px" clearable></el-input>
                    </li>
                    <li><p>商品品牌可在品牌管理预添加，库存会根据商品销售而减少，销售量可随机填写，制造热销假象</p></li>
                    <li>
                        <span>产品发布人</span><el-input  v-model="all_data.publisher" size="mini" style="width:158px" clearable :disabled="true"></el-input>
                        <span>更新时间</span><el-date-picker v-model='all_data.updated_at'   type="datetime" placeholder="选择日期时间" format="yyyy-MM-dd HH:mm:ss"  value-format="timestamp" size='mini' style="width:198px"></el-date-picker>
                        <span>点击量</span><el-input  @keyup.native="proving('click_volume',1)"   v-model="all_data.click_volume" size="mini" style="width:158px" type="number"   min="0" step="1"  clearable></el-input>
                    </li>
                    <li>
                        <span>排序</span><el-input  v-model="all_data.order"  size="mini" style="width:158px"  type="number"   min="0" step="1" clearable></el-input>
                    </li>
                    <li><p>可自定义点击量，制造假象，排序为商品的排列顺序，数字越小排越前面</p></li>
                     <li>
                        <span>是否推荐</span> <el-radio v-model="all_data.recommend" :label="1">开</el-radio><el-radio v-model="all_data.recommend" :label="0">关</el-radio>
                    </li>
                     <li>
                        <span>是否热销</span> <el-radio v-model="all_data.hot_sale" :label="1">开</el-radio><el-radio v-model="all_data.hot_sale" :label="0">关</el-radio>
                    </li>
                     <li>
                        <span>是否新品</span> <el-radio v-model="all_data.new_product" :label="1">开</el-radio><el-radio v-model="all_data.new_product" :label="0">关</el-radio>
                    </li>
                     <li>
                        <span>上架/下架</span> <el-radio v-model="all_data.status" :label="1">开</el-radio><el-radio v-model="all_data.status" :label="0">关</el-radio>
                    </li>
                    <li style="text-align:center"><el-button style="margin-top:28px;" @click="sure_add" type="success">{{title=='添加产品'?'确认发布':'确认修改'}}</el-button></li>
               </ul>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import commodity_add from "./commodity_add";
export default commodity_add;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    min-height: 100px;
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
    ul {
      width: 96%;
      margin: 0 auto;
      padding: 28px 0;
      li {
        padding: 4px 0;
        span {
          width: 100px;
          display: inline-block;
          text-align: right;
          margin-right: 8px;
          i {
            cursor: pointer;
            font-size: 16px;
            margin-right: 2px;
          }
          i:hover {
            color: red;
          }
        }
        p {
          width: calc(100% - 108px);
          margin-left: 108px;
          color: #ccc;
          padding: 2px 0;
        }
        s {
          color: #ccc;
          text-decoration: none;
          display: inline-block;
          margin-left: 48px;
        }
      }
    }
  }
}
</style>



