import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'

export default {
  name: 'Vip_updata'
  , data() {
    return {
      name: null
      , new_name: null
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'vipleftcomponents': vipleftcomponents

  }
  , created() {

  }
  , methods: {
    updata_name() {
      var that = this;
      if (that.name == null || that.new_name == null || that.name == '' || that.new_name == '') {
        that.$message({ type: 'info', message: '请填写修改信息' });
        return;
      }
      http.ajaxPost('user/edit', { data: { token: http.getToken(), name: that.name, new_name: that.new_name } }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: res.data.message })
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
  }
}


