import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components'
import pagecon from '@/components/page/pagecon'
// import { isNull } from 'util';

export default {
  name: 'Commodity_supplier_lis'
  , data() {
    return {
      value: null,
      tableData: []                 //table中数据
      , multipleSelection: []       //记录勾选内容
      , pages: null                 //页码控制
      , img_url: ''                 //获取图片路径  
      , go_pc_url: ''
      , go_web_url: ''
      , type_num: '1'               //初次加载table数据   1 出售  0 下架 2 售罄  3 库存  4 待审核
      , is_other: 0                 //隐藏操作页面 
      , other_data: {               //隐藏操作页面的数据
        name: null,                 //名字
        rush_price: null            //抢购价
        , rush_start_time: null
        , rush_end_time: null
        , integral_price: null      //积分兑换价
        , postage: null             //邮费
        , id: null                  //当前操作的id
        , market: null              //市场价
        , retail_price: null        //本站价
        , sale_const: null          //销售成本
        , supply_cost: null         //供货成本
        , integral_deduction: null  //积分抵扣限额
        , status: null              //是否上架
        , auditing: null            //是否审核
      }
      , search_data: {              //搜索内容数据
        keyword: null               //搜索文字   
        , search_type: 'name'         //编号number  标题name
        , order: null
        , category: null
        , type: null
      }
      , search_get_type: ''         //是否进行搜索
      , category: []                //全部分类列表数据
      , product_type: []            //全部产品类型列表数据
      , order_type: []              //全部产品类型列表数据 
      , category_all: null          //批量修改的分类
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
    , 'pagecon': pagecon

  }
  , created() {
    var that = this;
    that.go_pc_url = that.axios.defaults.baseURL;
    that.go_web_url = http.get_web_url();
    that.img_url = that.axios.defaults.baseURL;
    that.get_lis(1);  //初次首先获取出售列表
    that.data_null();
    http.ajaxPost('get_screen', { data: { token: http.getToken(), type: 1 } }, res => {
      that.category.push({ id: 0, name: '全部分类' })                                  //栏目处理
      for (var i = 0; i < res.data.data.category.length; i++) {                       //顶级
        that.category.push({ id: res.data.data.category[i].id, name: res.data.data.category[i].name })
        if (res.data.data.category[i].children) {
          for (var e = 0; e < res.data.data.category[i].children.length; e++) {   //二级
            that.category.push({ id: res.data.data.category[i].children[e].id, name: '---' + res.data.data.category[i].children[e].name })
            for (var s = 0; s < res.data.data.category[i].children[e].children.length; s++) { //三级
              that.category.push({ id: res.data.data.category[i].children[e].children[s].id, name: '------' + res.data.data.category[i].children[e].children[s].name })
            }
          }
        }
      }
      that.product_type.push({ name: '产品类型', id: 0 })
      that.order_type.push({ name: '默认排序', id: 0 })
      for (let i in res.data.data.type) {//产品类型选择
        that.product_type.push({ name: res.data.data.type[i], id: i })
      }
      for (let i in res.data.data.order) {//默认排序
        that.order_type.push({ name: res.data.data.order[i], id: i })
      }
    })
  }
  , methods: {
    data_null() { //存储数据为空 
      for (let i in this.other_data) { this.other_data[i] = null }
    }
    , commodity_del(id) { //删除商品 
      var that = this;
      var http_post = function () {
        http.ajaxPost('product/destroy', { data: { token: http.getToken(), id: id } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '删除成功' })
            that.get_lis();
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '删除后无法恢复, 是否继续', '确认删除', http_post);
    }
    , get_lis(e) {
      var that = this;
      let data = {};
      if (that.search_get_type == that.type_num) { //查询后的列表展示
        data = that.search_data;
        data.token = http.getToken();
        data.page = e;
        data.status = that.type_num * 1;
      } else { data = { token: http.getToken(), status: that.type_num * 1, supply: 2 } }
      http.ajaxPost('product', { data: data, page: e }, res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      })
    }
    , search_get() { //进行搜索操作
      var that = this;
      that.search_get_type = that.type_num;
      that.get_lis();
    }
    , chang_type_num() { //切换的table列表 
      this.get_lis();
    }
    , lis_other(type, id, name, price, start, end, supply_cost, integral_deduction, status, auditing) { //页面操作 
      var that = this;
      switch (type) {
        case 0: that.$router.push({ path: '/home/commodity_add' }); break;//添加  跳转至添加商品页面
        case 1: const { href } = this.$router.resolve({ path: '/home/commodity_Inventory', query: { id: JSON.stringify(id) } }); window.open(href, '_blank'); break;//库存  跳转至库存修改页面
        case 2: that.$router.push({ path: '/home/', query: { id: id } }); break;//卡密  跳转至卡密修改页面
        case 3: that.$router.push({ path: '/home/commodity_add', query: { id: id } }); break;//详情  跳往商品详情 
        case 4: //限时抢购 弹出操作层
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.rush_price = price;
          that.other_data.rush_start_time = start * 1000;
          that.other_data.rush_end_time = end * 1000;
          that.other_data.id = id;
          break;
        case 5: //积分兑换 弹出操作层
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.integral_price = price;
          that.other_data.id = id;
          break;
        case 6: //零元购 弹出操作层
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.postage = price;
          that.other_data.id = id;
          break;
        case 8: //通过 审核商品
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.market = price; //市场价
          that.other_data.retail_price = start; //本站价
          that.other_data.sale_const = end; //销售成本
          that.other_data.supply_cost = supply_cost; //供货成本
          that.other_data.integral_deduction = integral_deduction; //积分抵扣限额
          that.other_data.status = status; //是否上架
          that.other_data.auditing = auditing; //是否审核
          that.other_data.id = id;
          break;
        case 7: //关闭操作层 清空存储数据
          that.is_other = 0;
          that.data_null();
          break;
        default:

      }
    }
    , status_updata(status, id) { //上架操作 修改
      var that = this;
      let data = { token: http.getToken() };
      data.status = status;
      http.ajaxPost('product/edit/' + id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '修改成功' })
        } else { that.$message({ type: 'info', message: '操作失败,请检查网络' }); }
      })
    }
    , other_data_updata(type) { // 限时抢购 1 积分兑换 2  零元购 3  4修改审核 提交修改 
      var that = this;
      let data = { token: http.getToken() };
      data.name = that.other_data.name;

      if (type == 1) { //限时抢购提交
        data.rush_price = that.other_data.rush_price;
        data.rush_start_time = that.other_data.rush_start_time / 1000;
        data.rush_end_time = that.other_data.rush_end_time / 1000;
      } else if (type == 2) { //积分兑换提交
        data.integral_price = that.other_data.integral_price;
      } else if (type == 3) { //零元购提交
        data.postage = that.other_data.postage;
      } else if (type == 4) {
        data.market = that.other_data.market; //市场价
        data.retail_price = that.other_data.retail_price; //本站价
        data.sale_const = that.other_data.sale_const; //销售成本
        data.supply_cost = that.other_data.supply_cost; //供货成本
        data.integral_deduction = that.other_data.integral_deduction; //积分抵扣限额
        data.status = that.other_data.status; //是否上架
        data.auditing = that.other_data.auditing; //是否审核
      }
      that.http_now(data);
    }
    , clear_show(type) { //取消操作
      let that = this;
      let data = { token: http.getToken() };
      if (type == 1) { //限时抢购
        data.rush_end_time = 0;
      } else if (type == 2) { //积分兑换
        data.integral_price = 0;
      } else if (type == 3) { //零元购
        data.postage = 0;
      }
      that.http_now(data);
    }
    , http_now(data) {  //修改请求
      var that = this;
      http.ajaxPost('product/edit/' + that.other_data.id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '修改成功' })
          that.get_lis()
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , all_status() {  //批量上架 下架
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '请选择要被操作的数据' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      data.status = that.type_num == '0' ? 1 : 0;
      var http_post = function () {
        http.ajaxPost('product/updates', { data: data }, (res) => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '操作成功' })
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      }
      http.my_alert(that, '您确定要批量修改这些数据吗?', '全部修改', http_post)
    }
    , category_all_updata() { //批量修改
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '请选择要被操作的数据' }); return; }
      if (that.category_all == null || that.category_all == 0) { that.$message({ type: 'info', message: '请选择修改内容' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      data.category_id = that.category_all;
      var http_post = function () {
        http.ajaxPost('product/updates', { data: data }, (res) => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '操作成功' })
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      }
      http.my_alert(that, '您确定要批量修改这些数据吗?', '全部修改', http_post)
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , handleSelectionChange(val) {  //选中的元素
      this.multipleSelection = val;
    }
    , all_yes_select(rows) { //快捷全选   全不选
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    }
    , reverse_select() {
      var that = this;
      if (that.multipleSelection.length == 0) { //如果没有勾选  反选就是全选
        that.all_yes_select(that.tableData);
      } else {
        var select_data = []; //存放已经被勾选的数据
        var new_data = []; //反选后填入的数据
        for (let i in that.tableData) { //首先存入全部数据
          new_data.push(that.tableData[i])
        }
        for (var i in that.multipleSelection) { //此时 获取之前 已经勾选过得数据 
          select_data.push(that.multipleSelection[i])
        }
        that.all_yes_select();//清空之前勾选过得内容
        //获取没有被勾选的数据
        for (var i = 0; i < select_data.length; i++) {
          for (var j = 0; j < new_data.length; j++) {
            if (new_data[j] == select_data[i]) {
              new_data.splice(j, 1)
            }
          }
        }
        if (new_data.length == 0) { //如果没有
          that.all_yes_select();
        } else {
          that.all_yes_select(new_data);
        }
      }
    }
    , del_all() { //全部删除
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '请选择删除内容' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      var http_post = function () {
        http.ajaxPost('product/destroy', { data: data }, (res) => {
          console.log(res.data)
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '删除成功' })
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '删除后无法恢复,您确定要删除吗?', '全部删除', http_post)
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , proving(name, value) { //限制输入数字格式
      this[name][value] = this[name][value].replace(/[^\.\d]/g, ''); //为正数
      this[name][value] = this[name][value].match(/^\d*(\.?\d{0,2})/g)[0]; //保留小数点两位
    }
  }
  , watch: {
    type_num: function (now, old) {  //如果表格动 就清空查询状态
      this.search_get_type = '';
    }
  }
}


