<template>
    <div >
        <el-pagination v-if='pages'  style="width:70%;margin:0 auto;" layout="total, sizes, prev, pager, next, jumper"
            :current-page="pages.current_page"  :total="pages.total" :page-size="pages.per_page"  :page-sizes="[pages.per_page]" 
            @current-change="get_lis" @size-change="get_lis">
        </el-pagination>
        <el-pagination v-if='!pages'  style="width:70%;margin:0 auto;" layout="total, sizes, prev, pager, next, jumper"
            :current-page="0"  :total="0" :page-size="0"  :page-sizes="[0]" >
        </el-pagination>

        
    </div>
</template>

<script>
export default {
  name: "pagecon",
  data() {
    return {};
  },
  props: {
    pages: {}
  },
  created() {},
  computed: {},
  methods: {
    get_lis(e) {
      this.$parent.get_lis(e);
    }
  }
};
</script>


<style scoped>
</style>



