<template>
  <div style='height:100%; width: 100%;'>
      <!-- 用户登陆 -->
      <div class="Login">
        <div class="login_box">
            <div class='div' style="margin-top:28px;"><img src="../../../static/images/user.png" alt=""><el-input style="width:222px;display:inline-block;margin-top:28px;"  type="text" v-model="ruleForm.name" placeholder="请输入用户名" ></el-input></div>
            <div class='div'><img src="../../../static/images/password.png" alt=""><el-input style="width:222px;display:inline-block;margin-top:28px;" type="password" v-model="ruleForm.password" placeholder="请输入密码" ></el-input></div>
            <!-- 滑动验证 -->
            <div class="drag" style="width:54%;margin:18px auto 0" ref="dragDiv">
              <div class="drag_bg"></div>
              <div class="drag_text">{{confirmWords}}</div>
              <div ref="moveDiv" @mousedown="mousedownFn($event)" :class="{'handler_ok_bg':confirmSuccess}" class="handler handler_bg" style="position: absolute;top: 0px;left: 0px;"></div>
            </div>
            <!-- 按钮操作 -->
            <div style="width:41%;margin:18px auto 0">
                <el-checkbox v-model="checked" style="color:#000">记住用户名</el-checkbox>
            </div>
            <el-button v-if="!confirmSuccess" style='width:268px;display:block;margin:18px auto;' type="primary" disabled>登&nbsp;&nbsp;&nbsp;&nbsp;陆</el-button>
            <el-button v-if="confirmSuccess" @click="submitForm" style='width:268px;display:block;margin:18px auto;' type="success">登&nbsp;&nbsp;&nbsp;&nbsp;陆</el-button>
        </div>
    </div>
    <div style="position:fixed;bottom:28px;left:0;width:100%;color:#fff;height:28px"><div style="width:70%;margin:0 auto;padding:6px 0 ;border-radius:10px;background:rgba(204, 204, 204, 0.58);text-align:center;">Powered by 中山市合家安 © 2014-2018 www.xxxxxxxx.com</div></div>
  </div>
</template>
<script>
import http from "@/api/http";
export default {
  name: "Login",
  data() {
    return {
      ruleForm: { name: "", password: "" },
      beginClientX: 0 /*距离屏幕左端距离*/,
      mouseMoveStata: false /*触发拖动状态  判断*/,
      maxwidth: "" /*拖动最大宽度，依据滑块宽度算出来的*/,
      confirmWords: "拖动滑块验证" /*滑块文字*/,
      confirmSuccess: false /*验证成功判断*/,
      checked: false
    };
  },
  created() {
    this.getCookie();
    // http.ajaxPost('broadcast',{}, res => {console.log(res)});
  },
  mounted() {},
  methods: {
    mousedownFn: function(e) {
      if (!this.confirmSuccess) {
        e.preventDefault && e.preventDefault(); //阻止文字选中等 浏览器默认事件
        this.mouseMoveStata = true;
        this.beginClientX = e.clientX;
      }
    }, //mousedoen 事件
    successFunction() {
      this.confirmSuccess = true;
      this.confirmWords = "验证通过";
      if (window.addEventListener) {
        document
          .getElementsByTagName("html")[0]
          .removeEventListener("mousemove", this.mouseMoveFn);
        document
          .getElementsByTagName("html")[0]
          .removeEventListener("mouseup", this.moseUpFn);
      } else {
        document
          .getElementsByTagName("html")[0]
          .removeEventListener("mouseup", () => {});
      }
      document.getElementsByClassName("drag_text")[0].style.color = "#fff";
      document.getElementsByClassName("handler")[0].style.left =
        this.maxwidth + "px";
      document.getElementsByClassName("drag_bg")[0].style.width =
        this.maxwidth + "px";
    }, //验证成功函数
    mouseMoveFn(e) {
      if (this.mouseMoveStata) {
        let width = e.clientX - this.beginClientX;
        if (width > 0 && width <= this.maxwidth) {
          document.getElementsByClassName("handler")[0].style.left =
            width + "px";
          document.getElementsByClassName("drag_bg")[0].style.width =
            width + "px";
        } else if (width > this.maxwidth) {
          this.successFunction();
        }
      }
    }, //mousemove事件
    moseUpFn(e) {
      this.mouseMoveStata = false;
      var width = e.clientX - this.beginClientX;
      if (width < this.maxwidth) {
        document.getElementsByClassName("handler")[0].style.left = 0 + "px";
        document.getElementsByClassName("drag_bg")[0].style.width = 0 + "px";
      }
    }, //mouseup事件
    submitForm(formName) {
      var that = this;
      var name = that.ruleForm.name;
      var pass = that.ruleForm.password;
      if (name == "" || name == null) {
        that.$message({ message: "请输入正确的用户名", type: "warning" });
        return;
      } else if (pass == "" || pass == null) {
        that.$message({ message: "请输入正确的密码", type: "warning" });
        return;
      }
      if (that.checked) {
        //判断复选框是否被勾选 勾选则调用配置cookie方法
        that.setCookie(name, pass, 7); //传入账号名，密码，和保存天数3个参数
      }
      if (!that.checked) {
        that.clearCookie();
      }
      http.ajaxPost("/login", { data: that.ruleForm }, res => {
        if (res.data.status) {
          http.set_cookies("token", res.data.data.token);
          that.$router.push({path:'/home'});
        } else {
          that.$message({ message: res.data.message, type: "warning" });
          that.clearCookie();
          that.ruleForm.name = "";
          that.ruleForm.password = "";
          return;
        }
      });
    },
    setCookie(c_name, c_pwd, exdays) {
      var exdate = new Date(); //获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //保存的天数
      window.document.cookie =
        "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString(); //字符串拼接cookie
      window.document.cookie =
        "password" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
      window.document.cookie =
        "checked" + "=" + true + ";path=/;expires=" + exdate.toGMTString();
    },
    //读取cookie
    getCookie: function() {
      if (document.cookie.length > 0) {
        //判断是否存在cookie
        var arr = document.cookie.split("; "); //这里显示的格式需要切割一下自己可输出看下
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split("="); //再次切割
          if (arr2[0] == "userName") {
            //判断查找相对应的值
            this.ruleForm.name = arr2[1]; //保存到保存数据的地方
          } else if (arr2[0] == "password") {
            this.ruleForm.password = arr2[1];
          } else if (arr2[0] == "checked") {
            this.checked = arr2[1] == "true" ? true : false;
          }
        }
      }
    },
    clearCookie: function() {
      this.setCookie("", "", -1); //修改2值都为空，天数为负1天就好了
    }
  },
  mounted() {
    this.maxwidth =
      this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
    document
      .getElementsByTagName("html")[0]
      .addEventListener("mousemove", this.mouseMoveFn);
    document
      .getElementsByTagName("html")[0]
      .addEventListener("mouseup", this.moseUpFn);
  }
};
</script>
<style lang="scss" scoped>
.Login {
  width: 100%;
  height: 100%;
  min-width: 900px;
  margin: 0 auto;
  background: url("../../../static/images/bg_login.png") no-repeat;
  background-size: 100% 100%;
  position: relative;
  .login_box {
    position: absolute;
    width: 458px;
    height: 380px;
    top: calc(50% - 190px);
    right: 13%;
    background: rgba(255, 255, 255, 0.4);
    border-radius: 12px;
    .div {
      width: 69%;
      margin: 0 auto;
      img {
        display: inline-block;
        width: 38px;
        height: 38px;
        margin: 0 12px 0 18px;
      }
    }
  }
}
//滑动验证
.drag {
  position: relative;
  background-color: #e8e8e8;
  width: 100%;
  height: 34px;
  line-height: 34px;
  text-align: center;
}
.handler {
  width: 40px;
  height: 32px;
  border: 1px solid #ccc;
  cursor: move;
}
.handler_bg {
  background: #fff
    url("data:image/png;base64,")
    no-repeat center;
}
.handler_ok_bg {
  background: #fff
    url("data:image/png;base64,")
    no-repeat center;
}
.drag_bg {
  background-color: #7ac23c;
  height: 34px;
  width: 0px;
}
.drag_text {
  position: absolute;
  top: 0px;
  width: 100%;
  text-align: center;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
}
</style>




