<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <settingcompent></settingcompent>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>售后客服展示</span>
                </div>
            </div>
            <!-- 分类展示 -->
            <div class="column_modle">
               <ul>
                 <li><span style="font-size:16px;display:inline-block;width:128px;margin-right:18px;">客服电话:</span><el-input size="mini" style="width:218px" v-model="mobile" clearable></el-input></li>
                 <li><span style="font-size:16px;display:inline-block;width:128px;margin-right:18px;">客服微信:</span><el-input size="mini" style="width:218px" v-model="weixin" clearable></el-input></li>
                    <li><span style="font-size:14px;padding:8px 0 ;">内容描述</span></li>
                    <li>
                        <div class='tinymce' id="editorElem" style="text-align:left"></div>
                    </li>
                    <li style="text-align:center"><el-button style="margin-top:28px;" @click="sure_add" type="success">确认发布</el-button></li>
               </ul>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import kefu from "./kefu";
export default kefu;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    min-height: 100px;
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
    ul {
      width: 96%;
      margin: 0 auto;
      padding: 28px 0;
      li {
        padding: 4px 0;
        span {
          width: 100px;
          display: inline-block;
          text-align: right;
          margin-right: 8px;
          i {
            cursor: pointer;
            font-size: 16px;
            margin-right: 2px;
          }
          i:hover {
            color: red;
          }
        }
        p {
          width: calc(100% - 108px);
          margin-left: 108px;
          color: #ccc;
          padding: 2px 0;
        }
        s {
          color: #ccc;
          text-decoration: none;
          display: inline-block;
          margin-left: 48px;
        }
      }
    }
  }
}
</style>



