import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'
export default {
  name: 'Commodity_Inventory'
  , data() {
    return {
      tableData: []
      , img_url: ''
      , stock: null
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
  }
  , created() {
    var that = this;
    that.get_lis();
    that.img_url = that.axios.defaults.baseURL;
  }
  , updata() {
  }
  , mounted() {

  }
  , methods: {
    get_lis() { //获取全部数据
      var that = this;
      if (that.$route.query.id == undefined) { return; }
      http.ajaxPost('product/stock/' + that.$route.query.id, { data: { token: http.getToken() } }, res => {
        console.log(res.data)
        if (res.data.status) {
          that.tableData = res.data.data
        } else { that.$message({ type: 'info', message: '获取列表失败' }) }
      })
    }
    , data_updata(id, stock) { //修改库存
      var that = this;
      var data = { token: http.getToken() };
      if (stock != undefined) {
        data.id = id;
        data.stock = stock;
        if (stock == null || stock == '') {
          that.$message({ type: 'info', message: '请输入对应修改信息' });
          return;
        }
      } else {
        data.stock = that.stock;
        if (that.stock == null || that.stock == '') {
          that.$message({ type: 'info', message: '请输入批量修改信息' });
          return;
        }
        var arr = [];
        for (let i in that.tableData) {
          arr.push(that.tableData[i].id);
        }
        data.id = arr;
      }
      http.ajaxPost('product/stock/' + that.$route.query.id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'warning', message: res.data.message });
          that.get_lis();
        } else { that.$message({ type: 'info', message: '修改失败' }) }
      })
    }
  }

}



