<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <vipleftcomponents></vipleftcomponents>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <!-- <img class="search_img" src="../../../static/images/search.png" alt=""> -->
          <div class="search_modle">
            <span>添加管理员</span>
            <!-- <el-input style="width:208px;display:inlie-block;vertical-align: middle;"  placeholder="请输入内容"></el-input>
            <el-button class="btn" type="danger" style="vertical-align: middle;">搜索</el-button>-->
            <el-button
              type="success"
              @click="go_vip_admin_lis"
              plain
              style="vertical-align: middle;width:144px;float:right;margin: 10px;"
            >管理员列表</el-button>
          </div>
        </div>
        <!-- 填写添加管路员 -->
        <div class="vip_set">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
            style="width:30%;margin:0 auto;padding:18px 12px;min-width:400px"
          >
            <el-form-item label="用户名" prop="name">
              <el-input v-model="ruleForm.name" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="ruleForm.password" placeholder="请输入密码" type="password"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="is_password">
              <el-input v-model="ruleForm.is_password" placeholder="请再次输入密码" type="password"></el-input>
            </el-form-item>
            <el-form-item label="手机" prop="mobile">
              <el-input v-model="ruleForm.mobile"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <!-- 触发添加会员 -->
        <div class="vip_set_addbtn">
          <el-button type="success" @click="submitForm('ruleForm')" plain>添加会员</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import vip_admin_add from "./vip_admin_add";
export default vip_admin_add;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_set {
    background: #fff;
    border-radius: 12px;
    margin: 12px auto;
    padding: 8px 0 0;
  }
  .vip_set_choice {
    background: #fff;
    border-radius: 12px;
    table {
      width: 100%;
      tr {
        td {
          width: 78%;
          border: 1px solid #bbdde5;
          padding: 8px 0;
          vertical-align: middle;
          label[role="radio"] {
            margin-left: 16px;
          }
          ul {
            width: 100%;
            vertical-align: middle;
            li {
              width: 33%;
              float: left;
              padding: 2px 0;
            }
          }
        }
        td:first-child {
          width: 22%;
        }
      }
      tr:first-child {
        text-align: center;
      }
      tr:last-child {
        text-align: center;
        button {
          padding: 6px 10px;
          vertical-align: middle;
        }
      }
    }
  }
  .vip_set_addbtn {
    background: #fff;
    border-radius: 12px;
    text-align: center;
    margin-top: 12px;
    padding: 18px 0;
    button {
      width: 158px;
    }
  }
}
</style>



