import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'
export default {
    name: 'Vip_admin_updata'
    , data() {
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.ruleForm.password) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            type: 0,
            radio: 0
            , ruleForm: {
                name: null,
                password: null,
                is_password: null,
                type: null,
                mobile: null
            },
            rules: {
                name: [
                    { required: true, message: '请输入用户名称', trigger: 'blur' },
                    { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
                ]
                , password: [
                    { required: true, message: '请设置密码', trigger: 'blur' },
                    { min: 6, max: 20, message: '长度在 6 到 20个字符', trigger: 'blur' }
                ]
                , is_password: [
                    { validator: validatePass2, trigger: 'blur' }
                ]
                , mobile: [
                    { required: true, message: '请输入昵称', trigger: 'blur' },
                    { min: 11, max: 11, message: '请输入11位的手机号', trigger: 'blur' }
                ]
            }
            , group: []
            , admin_nav: []
        }

    }
    , components: {
        homelefttop
        , homeleftbottom
        , vipleftcomponents
    }
    , created() {
        var that = this;
        that.type = that.$route.query.type * 1;
        that.get_user();
    }
    , methods: {
        //添加管理员信息提交
        pwd_updata(formName) {
            var that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {
                    var data = { token: http.getToken(), name: that.ruleForm.name, mobile: that.ruleForm.mobile, password: that.ruleForm.password };
                    http.ajaxPost('admin/edit/' + that.$route.query.id, { data }, res => {
                        if (res.data.status) {
                            that.$message({ type: 'success', message: '密码修改成功' });
                            that.go_vip_admin_lis();
                        } else { that.$message({ type: 'info', message: res.data.message }) }
                    })
                } else {
                    return;
                }
            });
        }
        //跳转至管理员列表
        , go_vip_admin_lis() {
            this.$router.push({ path: '/home/vip_admin_lis' })
        }
        , get_user() {
            var that = this;
            var data = { token: http.getToken() };
            http.ajaxPost('admin/edit/' + that.$route.query.id, { data }, res => {
                console.log(res.data)
                that.ruleForm.name = res.data.admin.name
                that.ruleForm.mobile = res.data.admin.mobile
                that.ruleForm.type = res.data.admin.group_admin.get_group.id
                that.group = res.data.group
                that.admin_nav = res.data.admin_nav
                for (let i in that.admin_nav) {
                    for (let s in that.admin_nav[i].children) {
                        if (that.admin_nav[i].children[s].default) {
                           that.radio = that.admin_nav[i].children[s].id;
                        }
                    }
                    if (that.admin_nav[i].default) {
                        that.radio = that.admin_nav[i].id;
                    }
                }
            })
        }
        , power_updata() {
            var that = this;
            let data = {};
            data.type = that.ruleForm.type;
            data.token = http.getToken();
            data.auth_id = [];
            for (let i in that.admin_nav) {
                for (let s in that.admin_nav[i].children) {
                    if (that.admin_nav[i].children[s].checked) {
                        let status = 0;
                        if (that.admin_nav[i].children[s].id == that.radio) { status = 1 }
                        data.auth_id.push({ id: that.admin_nav[i].children[s].id, status: status });
                    }
                }
                if (that.admin_nav[i].checked) {
                    let status = 0;
                    if (that.admin_nav[i].id == that.radio) { status = 1 }
                    data.auth_id.push({ id: that.admin_nav[i].id, status: status });
                }
            }
            http.ajaxPost('admin/edit/' + that.$route.query.id, { data }, res => {
                console.log(res.data)
                if (res.data.status) {
                    that.$message({ type: 'success', message: '修改成功' });
                    that.go_vip_admin_lis();
                } else { that.$message({ type: 'info', message: res.data.message }) }
            })
        }
        , all_yes(e) {    //全选  全不选
            var that = this;
            for (let i in that.admin_nav) {
                for (let s in that.admin_nav[i].children) {
                    that.admin_nav[i].children[s].checked = e;
                }
                that.admin_nav[i].checked = e;
                that.$set(that.admin_nav, i, that.admin_nav[i])
            }
        }

    }
}



