
import axios from "axios";
import Cookies from 'js-cookie'
export default {
    ajaxGet(api, cb) {

        axios.get(api)
            .then(cb)
            .catch(err => {
                console.log(err);
            })
    },
    ajaxPost(api, post, cb) {
        //||api==''  忘记密码
        if (api == '/register' || api == '/login') {
            axios.post(api, post)
                .then(cb)
                .catch(err => {
                    console.log(err);
                })
        } else {
            if (this.getToken()) {
                axios.post(api, post)
                    .then(cb)
                    .catch(err => {
                        console.log(err);
                    })
            } else {
                location.reload()
            }
        }
    },
    getToken() {
        return Cookies.get('token');
    },
    get_web_url() {
        return 'http://www.operating_system.com/';
    },
    clearCookie() {
        var exdate = new Date();//获取时间
        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * -1);//保存的天数
        window.document.cookie = "token" + "=" + '' + ";path=/;expires=" + exdate.toGMTString();
    },
    my_alert(that, str, tit, cd) {
        that.$confirm(str, tit, {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(cd)
            .catch(() => {
                that.$message({
                    type: 'info',
                    message: '已取消操作'
                });
            });
    }
    , set_cookies(a, b) {
        Cookies.set(a, b, { expires: 7 });
    }

}



