<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <vipleftcomponents></vipleftcomponents>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <!-- <img class="search_img" src="../../../static/images/search.png" alt=""> -->
          <div class="search_modle">
            <span>编辑修改管理员</span>
            <el-button
              type="success"
              @click="go_vip_admin_lis"
              plain
              style="vertical-align: middle;width:144px;float:right;margin: 10px;"
            >管理员列表</el-button>
          </div>
        </div>
        <!-- 修改管路员权限 -->
        <div v-if="!type">
          <div class="vip_set">
            <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              label-width="100px"
              class="demo-ruleForm"
              style="width:30%;margin:0 auto;padding:18px 12px;min-width:400px"
            >
              <el-form-item label="用户名" prop="name">
                <el-input v-model="ruleForm.name" placeholder="请输入用户名" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label="类型" style="margin-bottom:0">
                <el-radio
                  v-for="(items,index) in group"
                  v-model="ruleForm.type"
                  :label="items.id"
                  :key="index"
                >{{items.name}}</el-radio>
              </el-form-item>
            </el-form>
            <p style="height:38px;line-height:38px;width:80%;margin:0 auto;color:rgb(92, 92, 92)">PS:自身权限修改后,请退出重新登录,没有权限是访问不到数据,其他管理员权限修改后,被限制的权限内也是请求不到数据的;</p>
          </div>
          <!-- 访问权限选项设置 -->
          <div class="vip_set_choice">
          <div style="height:18px;"></div>
          <table style="width:90%;margin:0 auto">
            <tbody>
              <!-- 选项设置头部 -->
              <tr style="text-align:center">
                <td colspan="2">
                  <el-radio>为默认访问页面</el-radio>&nbsp;&nbsp;&nbsp;&nbsp;
                  <el-checkbox>为访问权限</el-checkbox>
                </td>
              </tr>
              <tr v-for="(items,index) in admin_nav" :key="index">
                <td valign="top">
                  <el-radio v-model="radio" :label="items.id">&nbsp;</el-radio>&nbsp;&nbsp;&nbsp;&nbsp;
                  <el-checkbox v-model="items.checked" :label="items.id" :true-label='items.id' :false-label='null'>{{items.name}}</el-checkbox>
                </td>
                <td>
                  <ul class="clearfix">
                    <li v-for="(item,i) in items.children" :key="i">
                      <el-radio v-model="radio"  >&nbsp;</el-radio>&nbsp;&nbsp;&nbsp;&nbsp;
                      <el-checkbox v-model="item.checked" :label="item.id" :true-label='item.id' :false-label='null' >{{item.name}}</el-checkbox>
                    </li>
                  </ul>
                </td>
              </tr>
              <!-- 底部 -->
              <tr>
                <td colspan="2">
                  <el-button @click="all_yes(true)" style="margin-left:28px;">全选</el-button>
                  <el-button @click="all_yes(false)" >全不选</el-button>
                </td>
              </tr>
            </tbody>
          </table>
          <div style="height:18px;"></div>
        </div>
        </div>

        <!-- 修改管路员密码 -->
        <div class="vip_set" v-if="type">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
            style="width:30%;margin:0 auto;padding:18px 12px;min-width:400px"
          >
            <el-form-item label="用户名" prop="name">
              <el-input v-model="ruleForm.name" placeholder="请输入用户名" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="手机" prop="mobile">
              <el-input v-model="ruleForm.mobile" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="ruleForm.password" placeholder="请输入密码" type="password"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="is_password">
              <el-input v-model="ruleForm.is_password" placeholder="请再次输入密码" type="password"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <!-- 触发添加会员 -->
        <div class="vip_set_addbtn">
          <el-button type="success" v-if="type" @click="pwd_updata('ruleForm')" plain>确认修改</el-button>
          <el-button type="success" v-if="!type" @click="power_updata" plain>确认修改</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import vip_admin_updata from "./vip_admin_updata";
export default vip_admin_updata;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_set {
    background: #fff;
    border-radius: 12px;
    margin: 12px auto;
    padding: 8px 0 0;
  }
  .vip_set_choice {
    background: #fff;
    border-radius: 12px;
    table {
      width: 100%;
      tr {
        td {
          width: 78%;
          border: 1px solid #bbdde5;
          padding: 8px 0;
          vertical-align: middle;
          label[role="radio"] {
            margin-left: 16px;
          }
          ul {
            width: 100%;
            li {
              width: 33%;
              float: left;
              padding: 2px 0;
            }
          }
        }
        td:first-child {
          width: 22%;
        }
      }
      tr:first-child {
        text-align: center;
      }
      tr:last-child {
        text-align: center;
        button {
          padding: 6px 10px;
          vertical-align: middle;
        }
      }
    }
  }
  .vip_set_addbtn {
    background: #fff;
    border-radius: 12px;
    text-align: center;
    margin-top: 12px;
    padding: 18px 0;
    button {
      width: 158px;
    }
  }
}
</style>



