<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <distributionleftcomponents></distributionleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <!-- <img class="search_img" src="../../../static/images/search.png" alt=""> -->
                <div class="search_modle" >
                    <span>提现设置:</span> 
                </div>
            </div>
            <!-- 表格数据 -->
            <div class="distribution">
              <ul>
                <li>
                  <span >提现手续(百分比):</span>
                  <el-input v-model="data_lis.proportion" type="number" placeholder="请输入提现比例百分比" sizi="small" style="display:inline-block;width:258px;" clearable ></el-input>
                </li>
                <li>
                <li>
                  <span >可提现日期:</span>
                    <el-checkbox-group v-model="data_lis.tixiandate" style="display:inline-block;">
                        <el-checkbox-button v-for="(city,index) in xingqi" :label="index" :key="index">{{city}}</el-checkbox-button>
                    </el-checkbox-group>
                </li>
              </ul>
              <div style="text-align:center;padding:12px ;"> 
                <el-button @click="is_updata" type="primary" style="display:inline-block" >确认设置</el-button>
              </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import tixian_setting from "./tixian_setting";
export default tixian_setting;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
        vertical-align: middle;
      }
    }
  }
  .distribution {
    border-radius: 12px;
    margin-top: 12px;
    font-size: 14px;
    color: #5c5c5c;
    background: #fff;
    ul{
      padding: 18px;
      width: 96%;
      margin: 0 auto ;
      li{
        width:88%;margin:12px auto 0;
        span{
          font-size:16px;font-weight:700;display:inline-block;margin:0 18px;width:158px;
        }
        p{
          width:88%;
          color: #ccc;
          margin-left:28px; 
          margin-top: 8px;
        }
      }
    }
  }

}
  
</style>



