<template>
    <div class="body">
        <div class="Layout clearfix">
             <!-- 顶部导航部分 -->
            <div class="Layout_top_home">
                <img class="img" @click="go_home" src="../../../static/images/logobj.png" alt="">
                <span class="title"  @click="go_home" >合家安后台管理系统</span>
                <div class="ul clearfix" >
                    <router-link to='/home/kalman'><img src="../../../static/images/dingdan.png" alt=""><span>卡密</span></router-link>
                    <router-link to='/home/generation_setting'><img src="../../../static/images/fenxiao.png" alt=""><span>分销</span></router-link>
                    <router-link to='/home/tihuo'><img src="../../../static/images/tihuo.png" alt=""><span>提货</span></router-link>
                    <router-link to='/home/huanhuo'><img src="../../../static/images/huanhuo.png" alt=""><span>换货</span></router-link>
                    <router-link to='/home/daohang'><img src="../../../static/images/zixun.png" alt=""><span>导航</span></router-link>
                    <router-link to='/home/commodity_lis'><img src="../../../static/images/shangpin.png" alt=""><span>商品</span></router-link>
                    <router-link to='/home/vip_lis'><img src="../../../static/images/vip.png" alt=""><span>会员</span></router-link>
                    <router-link to='/home/setting'><img src="../../../static/images/shezhi.png" alt=""><span>设置</span></router-link>
                </div >
            </div>
            <router-view/>
        </div>
        <!-- 底部foot -->
        <div style="text-align:center;background:rgba(255, 255, 255, 0.4);margin:20px auto 20px;width:1200px;color:#5c5c5c;font-size:14px;border-radius:4px;height:38px;line-height:38px;">@中山市合家安电器有限公司版权所有</div>
    </div>
</template>
<script>
import home_index from "./home_index";
export default home_index;
</script>
<style lang="scss" >
.body {
  height: 100%;
  overflow: auto;
  background: url("../../../static/images/bg_login.png") no-repeat;
  background-size: 100% 100%;
  .Layout {
    width: 1200px;
    margin: 0 auto;
    .Layout_top_home {
      margin-top: 48px;
      background: #000;
      height: 75px;
      position: relative;
      .title {
        position: absolute;
        color: #fff;
        font-size: 20px;
        top: 22px;
        left: -8px;
        cursor: pointer;
      }
      .img {
        position: absolute;
        left: -30px;
        top: -11px;
        height: 96px;
        width: 307px;
        cursor: pointer;
      }
      .ul {
        position: absolute;
        bottom: 0;
        right: 0;
        width: calc(100% - 267px);
        height: 100%;
        a {
          float: left;
          width: 9%;
          margin: 0 16px;
          height: calc(100% - 4px);
          text-align: center;
          color: #fff;
          border-bottom: 4px rgba(255, 255, 255, 0) solid;
          img {
            width: 28px;
            height: 28px;
            display: block;
            margin: 10px auto;
          }
          span {
            display: block;
          }
        }
        a:hover,
        .a_active {
          border-bottom: 4px #0080ff solid;
          background: rgba(255, 255, 255, 0.178);
        }
      }
    }
    .Layout_left_home {
      background: #1f2b2e;
      width: 220px;
      float: left;
      color: #fff;
      text-align: center;
      .touxiang {
        margin: 26px auto 14px;
        display: block;
        width: 68px;
        height: 68px;
        border-radius: 3px;
      }
      .tiaozhuan {
        margin: 10px 0;
        font-size: 12px;
        a {
          float: left;
          width: 48%;
          background: rgba(255, 255, 255, 0.2);
          color: #fff;
          height: 28px;
          line-height: 28px;
        }
        a:first-child {
          margin-right: 4%;
        }
        a:hover {
          background: #0080ff;
        }
      }
      .modle {
        margin-top: 18px;
        background: rgba(255, 255, 255, 0.2);
        padding: 12px 0;
        li {
          width: 62%;
          float: left;
          img {
            width: 25px;
            display: block;
            margin: 6px auto;
          }
        }
        li:first-child {
          width: 38%;
          span {
            display: block;
            margin: 0 auto;
            font-size: 14px;
            font-weight: 700;
          }
        }
        li:last-child {
          span {
            height: 52px;
            line-height: 52px;
            font-size: 32px;
            font-weight: 700;
          }
          i {
            display: inline-block;
            margin-left: 6px;
          }
        }
      }
      .modle:hover {
        background: #0080ff;
      }
      .service {
        color: #fff;
        width: 90%;
        margin: 0 auto;
        padding-bottom: 18px;
        p {
          margin-bottom: 18px;
          text-align: left;
          font-weight: 700;
          font-size: 14px;
        }
        .cont {
          margin: 12px 0;
          img {
            width: 38px;
            height: 38px;
            margin-right: 16px;
          }
          span {
            font-size: 12px;
          }
        }
        a {
          display: inline-block;
          cursor: pointer;
          width: 48%;
          height: 38px;
          line-height: 38px;
          font-size: 12px;
          text-align: right;
        }
        a:hover {
          color: #fff;
        }
        .one {
          background: url("../../../static/images/tel_bck.png") no-repeat;
          background-position: 0% 0%;
          background-size: 100%;
          margin-right: 2px;
        }
        .one:hover {
          background: url("../../../static/images/tel_bck.png") no-repeat;
          background-position: 0% 32%;
          background-size: 100%;
        }
        .two {
          background: url("../../../static/images/tel_bck.png") no-repeat;
          background-position: 0% 64%;
          background-size: 100%;
        }
        .two:hover {
          background: url("../../../static/images/tel_bck.png") no-repeat;
          background-position: 0% 100%;
          background-size: 100%;
        }
      }
      .navigation {
        li {
          background: #343c3e;
          width: 80%;
          margin: 0 auto 12px;
          height: 32px;
          line-height: 32px;
          a {
            display: block;
            width: 100%;
            height: 100%;
            text-align: center;
            color: #fff;
            position: relative;
            font-size: 14px;
            span {
              position: absolute;
              right: 6px;
              top: calc(50% - 7px);
              height: 14px;
            }
          }
        }
        li:hover {
          background: #0080ff;
        }
      }
    }
    .Layout_right_home {
      float: left;
      width: calc(100% - 220px);
      background: rgba(255, 255, 255, 0.137);
      .Layout_right_box {
        width: 96%;
        margin: 18px auto;
        border-radius: 12px;
      }
    }
  }
}
</style>



