<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <kalmanleftcomponents></kalmanleftcomponents>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <img class="search_img" src="../../../static/images/search.png" alt>
          <div class="search_modle">
            <span>账号搜索:</span>
            <el-input
              size="small"
              style="width:208px;display:inlie-block;"
              v-model="search.keyword"
              placeholder="请输入内容"
            ></el-input>
     
            <div style="float:right;margin-right:158px;">
                  <el-button
              size="small"
              @click="search_btn"
              class="btn"
              style="margin-left:58px;width:128px"
              type="danger"
            >&nbsp;搜&nbsp;&nbsp;索&nbsp;</el-button>
            <!-- <el-button size="small" type="danger" plain @click="kalman_btn">全部导出</el-button>
            <el-button size="small" type="success" plain @click="go_kami_add">生成卡密</el-button> -->
            </div>
          </div>
          <div class="search_modle" style="padding-bottom:6px;">
            <span>选择时间:</span>
            <el-date-picker
              type="datetimerange"
              style="width: 398px;height:38px;vertical-align: middle;"
              v-model="search.times"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['12:00:00']"
            ></el-date-picker>
          </div>
        </div>
        <!-- 会员信息列表展示区 -->
        <div class="vip_lis_table">
          <el-table
            :data="tableData"
            @selection-change="handleSelectionChange"
            ref="multipleTable"
            border
            style="width: 100%"
          >
            <el-table-column type="selection" width="35"></el-table-column>
            <el-table-column label="用户名称">
              <template slot-scope="scope">{{scope.row.username}}</template>
            </el-table-column>
            <el-table-column label="购买数量">
              <template slot-scope="scope">
                {{scope.row.number}}
              </template>
            </el-table-column>
            <el-table-column label="价格">
              <template slot-scope="scope">
                {{scope.row.purchase}}
              </template>
            </el-table-column>
            <el-table-column label="购买总价格">
              <template slot-scope="scope">
                {{scope.row.price}}
              </template>
            </el-table-column>
            <el-table-column label="购买时间">
              <template slot-scope="scope"> {{scope.row.created_at | time}}</template>
            </el-table-column>
            <!-- <el-table-column label="操作">
              <template slot-scope="scope">
                <div>
                  <el-button size="mini" @click="commodity_del(scope.row.id)" type="primary">作废</el-button>
                </div>
              </template>
            </el-table-column> -->
          </el-table>
        </div>
        <!-- 快捷操作 -->
        <!-- <div class="operation" style="padding:12px 0 ;background:#fff">
          <el-button style="margin-left:28px;" @click="all_yes_select(tableData)">全选</el-button>
          <el-button @click="all_yes_select()">全不选</el-button>
          <el-button @click="reverse_select">反选</el-button>
          <el-button @click="del_all">作废</el-button>
        </div> -->
        <!-- 会员列表信息分页 -->
        <div class="page">
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import goumai_lis from "./goumai_lis";
export default goumai_lis;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
</style>



