import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'
export default {
  name: 'Commodity_evaluate_add'
  , data() {
    return {
      fileList: []        //存放图片路径
      , pic_url: 'uploading'                    //上传图片接口
      , max_url: { type: 3 }                    //上传图片 
      , all_data: {                             //全部数据
        pic: []
      }
      , arr_pic: []
      , title: '添加评论'
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
  }
  , created() {
    var that = this;
    that.pic_url = that.axios.defaults.baseURL + that.pic_url;
    if (that.$route.query.id) {
      that.title = '查看评价'
      http.ajaxPost('product_comment/edit/' + that.$route.query.id, { data: { token: http.getToken() } }, res => {
        if (res.data.status) {
          console.log(res)
          that.all_data = res.data.data;
          that.all_data.created_at = that.all_data.created_at * 1000;
          for (let i in that.all_data.pic) {
            that.fileList.push({ name: '配图', value: that.all_data.pic[i] })
            that.arr_pic.push({ url: that.all_data.pic[i] })
          }
        } else { that.$message({ type: 'info', message: '获取失败,请检查网络' }); }
      })
    }
  }
  , updated() {
  }
  , methods: {
    go_commodity_evaluate() {
      this.$router.push({ path: '/home/commodity_evaluate' })
    }
    , data_updata() {
      var that = this;
      let data = that.all_data;
      data.token = http.getToken();
      data.created_at = data.created_at / 1000;
      that.all_data.pic = [];
      for (let i in that.arr_pic) {
        that.all_data.pic.push(that.arr_pic[i].url)
      }
      if (that.$route.query.id) {       //评论修改
        http.ajaxPost('product_comment/edit/' + that.$route.query.id, { data: data }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '修改成功' })
            that.go_commodity_evaluate();
          } else { that.$message({ type: 'info', message: res.data.message }); that.all_data.created_at = that.all_data.created_at * 1000 }
        })
      } else {                          //添加评论 
        http.ajaxPost('product_comment/create', { data: data }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '添加成功' })
            that.go_commodity_evaluate();
          } else { that.$message({ type: 'info', message: res.data.message }); that.all_data.created_at = that.all_data.created_at * 1000 }
        })
      }

    }
    //------------------------------------------------------------
    , handleExceed() { //上传图片超出后执行
      this.$message({ type: 'info', message: '最多上传5张图片,如要替换请删除上一张' })
    }
    , pic_status(e, file, fileList) { //图片上传成功后 添加到上传数据中
      this.arr_pic.push({ url: e.url })
    }
    , beforeRemove(file, fileList) {
      var that = this;
      if (that.$route.query.id) {
        for (let i in that.arr_pic) {
          if (that.arr_pic[i].url == file.value) {
            that.arr_pic.splice(i, 1);
          }
        }
      } else {
        for (let i in that.arr_pic) {
          if (that.arr_pic[i].url == file.response.url) {
            that.arr_pic.splice(i, 1);
          }
        }
      }

    }
    , handleRemove(file, fileList) {

    }
    , handlePreview() {

    }
    , beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif';
      const isLt2M = file.size / 1024 < 500;
      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG 和 png 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 500kb!');
      }
      return isJPG && isLt2M;
    }
    //----------------------------------------------------------
  }
}



