<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <vipleftcomponents></vipleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <img class="search_img" src="../../../static/images/search.png" alt="">
                <div class="search_modle" >
                    <span>基本资料:</span>   
                </div>
            </div>
            <div class="vip_set">
                <div class="vip_set_cont">
                    <span>会员账号ID起始:</span><el-input style="width:358px;display:inlie-block;vertical-align: middle;" type="number"  placeholder="10000"></el-input>
                    <div><span>&nbsp;</span><i>PS:会员账号ID起始</i></div>
                    <el-button class="btn" style="display:block;margin:0 auto;" type="danger">提&nbsp;交</el-button>
                </div>
            </div>
         
         
        </div>
    </div>
</div>
</template>
<script>
import vip_setting from "./vip_setting";
export default vip_setting;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
        vertical-align: middle;
      }
    }
  }
  .vip_set {
    background: #fff;
    border-radius: 12px;
    margin-top: 12px;
    .vip_set_cont {
      width: 90%;
      margin: 0 auto;
      padding: 38px;
      span {
        font-size: 18px;
        font-weight: 700;
        display: inline-block;
        width: 188px;
        margin: 12px 0;
      }
    }
  }
}
</style>



