<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search" style="padding:6px 0">
                <img class="search_img" src="../../../static/images/search.png" alt="">
                <div class="search_modle" >
                    <span style="display:inline-block;width:88px" >评价搜索:</span>
                    <el-input size='small' v-model="keyword" style="width:168px;display:inlie-block;"  placeholder="请输入内容"></el-input>
                    <el-button size='small' @click="search_data" class="btn" type="danger">搜索</el-button>
                    <el-button size='small' style="float:right;margin:10px;width:128px;" class="btn" type="success" plain @click="go_commodity_evaluate_add">添加评论</el-button>
                </div>
            </div> 
            <!-- 评论列表 -->
            <div class="vip_lis_table">
                <el-table :data="tableData" @selection-change="handleSelectionChange" ref="multipleTable" border max-height="800" style="width: 100%">
                    <el-table-column  type="selection"  width="35"> </el-table-column>
                    <el-table-column  prop="user_name"  label="姓名"  show-overflow-tooltip ></el-table-column>
                    <el-table-column  prop="product_name"  label="产品" show-overflow-tooltip ></el-table-column>
                    <el-table-column   prop="star" label="评分" width="60"></el-table-column>
                    <el-table-column   prop="content" label="评价内容" show-overflow-tooltip></el-table-column>
                    <el-table-column    label="时间" width="160" show-overflow-tooltip>
                        <template slot-scope="scope">
                            <div >{{scope.row.created_at | time}}</div>
                        </template>
                    </el-table-column>
                    <el-table-column   label="操作"  width="150">
                        <template slot-scope="scope">
                            <div >
                                <el-button size="mini" @click="go_lis(scope.row.id)" type="primary">编辑</el-button>
                                <el-button size="mini" @click="commodity_del(scope.row.id)" type="danger" >删除</el-button> 
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 快捷操作 -->
            <div class="operation" style='padding:12px 0 ;background:#fff'>
                <el-button style="margin-left:28px;" @click="all_yes_select(tableData)">全选</el-button><el-button @click="all_yes_select()">全不选</el-button>
                <el-button @click="reverse_select">反选</el-button><el-button  @click="del_all">删除</el-button>
            </div>
            <!-- 会员列表信息分页 -->
            <div class="page"><pagecon :pages="pages" @get_lis="get_lis"></pagecon></div>                                    

        </div>
    </div>

</div>
</template>
<script>
import commodity_evaluate from "./commodity_evaluate";
export default commodity_evaluate;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
</style>



