<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <vipleftcomponents></vipleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <img class="search_img" src="../../../static/images/search.png" alt="">
                <div class="search_modle" >
                    <span>基本资料:</span>   
                </div>
            </div>
            <div class="vip_set">
                <div class="vip_set_cont">
                    <div><span>旧账号:</span><el-input v-model="name"  style="width:358px;display:inlie-block;vertical-align: middle;"   placeholder="请输入旧账号名称"></el-input></div>
                    <div><span>新账户:</span><el-input v-model="new_name" style="width:358px;display:inlie-block;vertical-align: middle;"   placeholder="请输入新账号名称"></el-input></div>
                    <el-button class="btn" @click="updata_name" style="display:block;margin:28px auto 0;" type="danger">修改账号</el-button>
                </div>
            </div>
         
         
        </div>
    </div>
</div>
</template>
<script>
import vip_updata from "./vip_updata";
export default vip_updata;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
        vertical-align: middle;
      }
    }
  }
  .vip_set {
    background: #fff;
    border-radius: 12px;
    margin-top: 12px;
    .vip_set_cont {
      width: 50%;
      margin: 0 auto;
      padding: 38px;
      span {
        font-size: 18px;
        font-weight: 700;
        display: inline-block;
        width: 88px;
        margin: 28px 0 0;
      }
    }
  }
}
</style>



