<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <distributionleftcomponents></distributionleftcomponents>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <img class="search_img" src="../../../static/images/search.png" alt>
          <div class="search_modle">
            <span>账号搜索:</span>
            <el-input
              size="small"
              style="width:208px;display:inlie-block;"
              v-model="search.keyword"
              placeholder="请输入内容"
            ></el-input>
            <div style="float:right;margin-right:188px;">
              <el-button
                size="small"
                @click="search_btn"
                class="btn"
                style="margin-left:58px;width:128px;"
                type="danger"
              >&nbsp;搜&nbsp;&nbsp;索&nbsp;</el-button>
            </div>
          </div>
          <div class="search_modle" style="padding-bottom:6px;">
            <span>选择时间:</span>
            <el-date-picker
              type="datetimerange"
              style="width: 398px;height:38px;vertical-align: middle;"
              v-model="search.times"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['12:00:00']"
            ></el-date-picker>
          </div>
        </div>
        <!-- 会员信息列表展示区 -->
        <!-- @selection-change="handleSelectionChange" -->
        <div class="vip_lis_table" style="padding:19px 0 ;">
          <el-table :data="tableData" ref="multipleTable" border style="width: 96%;margin:0 auto; ">
            <el-table-column label="提现账号" fixed > 
              <template slot-scope="scope">{{scope.row.username}}</template>
            </el-table-column>
            <el-table-column label="提现金额"  >
              <template slot-scope="scope">{{scope.row.price}}</template>
            </el-table-column>
            <el-table-column label="实际提现金额"  >
              <template slot-scope="scope">{{scope.row.reality_price}}</template>
            </el-table-column>
            <el-table-column label="平台收益" >
              <template slot-scope="scope">{{scope.row.platform_profit}}</template>
            </el-table-column>
            <el-table-column label="提现利率" >
              <template slot-scope="scope">{{scope.row.proportion}}%</template>
            </el-table-column>
             <el-table-column label="提现时间">
              <template slot-scope="scope">{{scope.row.created_at | time}}</template>
            </el-table-column>
          </el-table>
        </div>
        <div class="page">
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import tixianshouyi from "./tixianshouyi";
export default tixianshouyi;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
</style>



