<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级分类 -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>分类管理</span>
                    <el-button type="success"  @click="close" plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">添加分类</el-button>
                </div>
            </div>
            <!-- 分类展示 -->
            <div class="column_modle">
                <!-- 顶级 -->
                <el-table :data="tableData"   style="width: 100%;" :default-expand-all='is_all'>
                    <el-table-column type="expand">
                        <template slot-scope="props" style="width:100%;padding:0;margin:0" >
                            <!-- 子级 -->
                            <el-table :data="props.row.children" :show-header='false'  style="width: 100%;color:#0080ff" :default-expand-all='is_all' >
                                <el-table-column type="expand" style="background:red">
                                    <template slot-scope="props3">
                                        <!-- 三级 -->
                                        <el-table :data="props3.row.children" :show-header='false'  style="width: 100%;color:red" :default-expand-all='is_all' >
                                            <el-table-column type="expand">
                                               <!-- 四级 -->
                                                 <template slot-scope="props4">
                                                    <el-table :data="props4.row.children" :show-header='false'  style="width: 100%;color:#67c23a" :default-expand-all='is_all' >
                                                        <el-table-column type="expand">
                                                            
                                                        </el-table-column>
                                                        <el-table-column   label="分类名称"  show-overflow-tooltip >
                                                            <template   slot-scope="scope" > 
                                                            <p>{{scope.row.name}}</p>
                                                            </template> 
                                                        </el-table-column>
                                                        <el-table-column   label="排序"   >
                                                            <template   slot-scope="scope" > 
                                                                <el-input type="number" style="width:88px" size='mini' v-model="scope.row.order" ></el-input>                                    
                                                            </template> 
                                                        </el-table-column>
                                                        
                                                        <el-table-column   label="操作" >
                                                            <template slot-scope="scope">
                                                                <div >
                                                                    <el-button size="mini" type="primary">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
                                                                    <el-button size="mini" type="primary" @click="close(1,scope.row.id)">编辑</el-button>
                                                                    <el-button size="mini" type="danger" @click="commodity_column_del(scope.row.id)">删除</el-button> 
                                                                </div>
                                                            </template>
                                                        </el-table-column>
                                                    </el-table>
                                                </template>
                                            </el-table-column>
                                            <el-table-column   label="分类名称"  show-overflow-tooltip >
                                                <template   slot-scope="scope" > 
                                                <p>{{scope.row.name}}</p>
                                                </template> 
                                            </el-table-column>
                                            <el-table-column   label="排序"  >
                                                <template   slot-scope="scope" > 
                                                    <el-input style="width:88px" type="number" size='mini' v-model="scope.row.order" ></el-input>                                    
                                                </template> 
                                            </el-table-column>
                                            <el-table-column   label="操作" >
                                                <template slot-scope="scope">
                                                    <div >
                                                        <el-button size="mini" type="primary" @click="close(0,scope.row.id)" >下级</el-button>
                                                        <el-button size="mini" type="primary" @click="close(1,scope.row.id)">编辑</el-button>
                                                        <el-button size="mini" type="danger" @click="commodity_column_del(scope.row.id)">删除</el-button> 
                                                    </div>
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                    </template>
                                </el-table-column>
                                <el-table-column   label="分类名称"  show-overflow-tooltip >
                                    <template   slot-scope="scope" > 
                                    <p>{{scope.row.name}}</p>
                                    </template> 
                                </el-table-column>
                                <el-table-column   label="排序"   >
                                    <template   slot-scope="scope" > 
                                        <el-input type="number" style="width:88px" size='mini' v-model="scope.row.order"  ></el-input>                                    
                                    </template> 
                                </el-table-column>
                                <el-table-column   label="操作" >
                                    <template slot-scope="scope">
                                        <div >
                                            <el-button size="mini" type="primary" @click="close(0,scope.row.id)" >下级</el-button>
                                            <el-button size="mini" type="primary" @click="close(1,scope.row.id)">编辑</el-button>
                                            <el-button size="mini" type="danger" @click="commodity_column_del(scope.row.id)">删除</el-button> 
                                        </div>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                    </el-table-column>
                    <el-table-column   label="分类名称"  show-overflow-tooltip >
                        <template   slot-scope="scope" > 
                        <p>{{scope.row.name}}</p>
                        </template> 
                    </el-table-column>
                    <el-table-column   label="排序"    >
                        <template   slot-scope="scope" > 
                            <el-input type="number" size='mini' style="width:88px" v-model="scope.row.order" ></el-input>                                    
                        </template> 
                    </el-table-column>
                    <el-table-column   label="操作"  >
                        <template slot-scope="scope">
                            <div >
                                <el-button size="mini" type="primary"  @click="close(0,scope.row.id)">下级</el-button>
                                <el-button size="mini" type="primary" @click="close(1,scope.row.id)">编辑</el-button>
                                <el-button size="mini" type="danger" @click="commodity_column_del(scope.row.id)">删除</el-button> 
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
    <!-- 隐藏区域 -->
    <div v-if="is_other" class="back_shadow" >
        <div class="vip_vip_updata vip_grade" v-if="!table_show">
            <div class="vip_set_cont_modle_top">添加分类<span @click="close" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle" style="padding-top:18px"><span>分类名称:</span><el-input size='small ' v-model="value_name" style="width:282px;margin-left:3px"></el-input></div>
            <div class="vip_set_cont_modle"><span>所属分类:</span>
                <el-select class="select" size='small ' v-model="value" style="width:188px" >
                    <el-option :label="items.name" v-for="(items,index) in data_lis" :key='index'  :value="items.id">{{items.name}}</el-option>
                </el-select>
            </div>       
            <el-button type="success" @click="column_add" plain>提交</el-button>
        </div>
        <div class="vip_vip_updata vip_grade" v-if="table_show">
            <div class="vip_set_cont_modle_top">编辑分类<span @click="close" class="el-icon-error"></span></div>
            <div style=" max-height: 400px;overflow: auto;">
                <div class="vip_set_cont_modle" style="padding-top:18px"><span>分类名称:</span><el-input size='small ' v-model="updata_lis.name" style="width:282px;margin-left:3px"></el-input></div>
                <div class="vip_set_cont_modle"><span>所属分类:</span>
                    <el-select class="select" size='small '  v-model="updata_lis.id" style="width:188px" >
                        <el-option :label="item.name" v-for="(item,index) in data_lis" :key='index'  :value="item.id">{{item.name}}</el-option>
                    </el-select>
                </div> 
                <div class="vip_set_cont_modle"><span>手机图片:</span>
                    <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo"
                    :action='base_url' :on-success='pic_status' :data="data_url"  :limit="1" :on-preview="handlePreview" :on-remove="handleRemove"  :file-list="fileList2" list-type="picture">
                      
                        <el-button size="small" type="primary">点击上传</el-button>
                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb,如要替换图片请删除上一张</div>
                    </el-upload>
                </div> 
                <div class="vip_set_cont_modle" style="padding-top:18px"><span>描述:</span><el-input size='small ' v-model="updata_lis.describe" style="width:282px;margin-left:3px"></el-input></div>
                <div class="vip_set_cont_modle"><span>手机分类封面图:</span>
                     <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo"
                    :action='base_url' :on-success='cover_pic_status' :data="data_url1"  :limit="1" :on-preview="handlePreview" :on-remove="handleRemove"  :file-list="fileList" list-type="picture">
                        <el-button size="small" type="primary">点击上传</el-button>
                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb,如要替换图片请删除上一张</div>
                    </el-upload>
                </div> 
                <div class="vip_set_cont_modle"><span>信息内容:</span>
                        <el-input style="width:calc(100% - 200px);vertical-align: middle;" type="textarea" v-model="updata_lis.mobile_describe" autosize placeholder="请输入内容" ></el-input>
                </div> 
                
                <el-button type="success" @click="commodity_column_updata" plain>提交</el-button>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import commodity_column from "./commodity_column";
export default commodity_column;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    margin-top: 12px;
  }
}
.back_shadow {
  width: 100%;
  min-width: 1000px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4;
  .vip_vip_updata {
    position: absolute;
    width: 768px;
    background: #fff;
    left: calc(50% - 384px);
    top: 18%;
    border-radius: 12px;
    margin-top: 12px;
    padding: 0 0 18px;
    .vip_set_cont_modle_top {
      background: #409eff;
      border-radius: 12px 12px 0 0;
      height: 58px;
      line-height: 58px;
      position: relative;
      text-align: center;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      span {
        position: absolute;
        font-size: 26px;
        color: #ccc;
        float: right;
        top: 16px;
        right: 16px;
        cursor: pointer;
      }
    }
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 128px;
        text-align: right;
        margin-right: 18px;
      }
      i {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
}
</style>



