<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <vipleftcomponents></vipleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <!-- <img class="search_img" src="../../../static/images/search.png" alt=""> -->
                <div class="search_modle" >
                    <span>会员详情</span>
                    <!-- <el-input style="width:208px;display:inlie-block;vertical-align: middle;"  placeholder="请输入内容"></el-input>
                    <el-button class="btn" type="danger" style="vertical-align: middle;">搜索</el-button>     -->
                    <el-button type="success" @click="go_vip_lis" plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">会员列表</el-button>
                </div>
            </div>
            <!-- 基本信息 -->
            <div class="vip_details">
               <h1>基本信息</h1>
               <ul class="clearfix">
                   <li><span>用户ID ：</span><i>{{user_data.id}}</i></li>
                   <li><span>用户姓名 ：</span><i>{{user_data.name}}</i></li>
                   <li><span>电话 ：</span><i>{{user_data.mobile}}</i></li>
                   <li><span>地址 ：</span><i>{{user_data.detailed}}</i></li>
                   <li><span>推荐人 ：</span><i v-if="user_data.father!=null">{{user_data.father.father_name.name}}</i></li>
                   <li style="width:100%"><span>openid ：</span><i  v-if="user_data.user_third!=null">{{user_data.user_third.openid}}</i></li>
                   <!-- <li><span>会员头像 ：</span><img v-if="user_data.avater !=null"  :src="img_url + user_data.avater" alt=""></li> -->
                   <li><span>会员头像 ：</span><img v-if="user_data.avater !=null"  :src="user_data.avater" alt=""></li>
                   <li><span>所在省市区 ：</span><i>{{user_data.province}} -> {{user_data.city}} -> {{user_data.district}}</i></li>
                   <li><span>是否购买过产品 ：</span><i>{{user_data.buy_resources?'是':'否'}}</i></li>
                   <li><span>是否分销资格 ：</span><i>{{user_data.distribution?'是':'否'}}</i></li>
                   <li><span>账户余额 ：</span><i>{{user_data.balances}}</i></li>
                   <li><span>&nbsp;</span><i>&nbsp;</i></li>
                   <li><span>注册时间 ：</span><i>{{user_data.created_at | time}}</i></li>
                   <!-- <li><span>登陆时间 ：</span><i>{{user_data.login_time | time}}</i></li> -->
                   <li><span>&nbsp;</span><i></i></li>
               </ul>
            <!-- 记录table查看 -->
                <el-tabs type="border-card" style="width: 90%;margin: 0 auto;">
                    <!-- 会员业绩 -->
                    <el-tab-pane label="会员业绩" class="vip_achievement">
                        <dl class="data_box clearfix">
                            <dt >会员业绩 </dt>
                            <dd ><p>自己累积消费<br><span>{{achievement_data.my_cumulative}}</span><br></p></dd>
                            <dd ><p>直推总业绩<br><span>{{achievement_data.direct_achievement}}</span><br></p></dd>
                            <dd ><p>直推分销人数<br><span>{{achievement_data.achievement_distribution}}</span><br></p></dd>
                            <dd ><p>提成总计<br><span>{{achievement_data.recharge_integral}}</span><br></p></dd>
                        </dl>
                    </el-tab-pane>
                    <!-- 订单记录 -->
                    <el-tab-pane label="订单记录">
                        <div class='clearfix order'></div>
                        <el-table :data="order_data"   style="width: 100%"> 
                            <el-table-column  prop="order_sn"  label="订单编号" ></el-table-column>
                            <el-table-column  prop="received_people"  label="收货人" ></el-table-column>
                            <el-table-column  prop="price" label="商品总价"></el-table-column>
                            <el-table-column label="下单时间">
                                <template   slot-scope="scope" > 
                                  {{scope.row.created_at | time}}
                              </template> 
                            </el-table-column>
                            <!-- <el-table-column  prop="status" label="订单状态"></el-table-column>
                            <el-table-column   label="操作" width="60">
                              <template   slot-scope="scope"  > 
                                  <span    style="color:#0080ff;display:inline-block;margin-left:12px;cursor:pointer;">查看</span>
                              </template> 
                            </el-table-column> -->
                        </el-table>
                    </el-tab-pane>
                    <!-- 资金记录 -->
                    <el-tab-pane label="资金记录">
                        <div class='clearfix order'></div>
                        <el-table :data="capital_log_data"   style="width: 100%"> 
                            <el-table-column  prop="order_sn"  label="订单编号" ></el-table-column>
                            <el-table-column  label="交易金额" >
                              <template slot-scope="scope">{{scope.row.attribute}}:{{scope.row.number}}</template>  
                            </el-table-column>
                            <el-table-column   label="时间">
                              <template slot-scope="scope">{{scope.row.created_at | time}}</template>  
                            </el-table-column>
                            <el-table-column  prop="surplus" label="余额"></el-table-column>
                            <el-table-column  prop="source_user" label="来源会员"></el-table-column>
                            <el-table-column  prop="type" label="交易类型" width="100" ></el-table-column>
                        </el-table>
                    </el-tab-pane>
                    <!-- 下级会员 -->
                    <el-tab-pane label="下级会员">
                        <div class='clearfix order'></div>
                        <el-table :data="children_data"   style="width: 100%"> 
                            <el-table-column  label="用户名">
                              <template slot-scope="scope">
                                {{scope.row.children_message.children.name}}
                              </template>  
                            </el-table-column>
                            <el-table-column  label="余额">
                              <template slot-scope="scope">
                                 {{scope.row.children_message.children.balances}}
                              </template>  
                            </el-table-column>
                            <el-table-column  label="累计消费">
                              <template slot-scope="scope">
                                 {{scope.row.children_message.achievement.my_cumulative}}
                              </template>  
                            </el-table-column>
                            <el-table-column  label="累计收入">
                              <template slot-scope="scope">
                                {{scope.row.children_message.achievement.my_income}}
                              </template>  
                            </el-table-column>
                        </el-table>
                    </el-tab-pane>
                    <!-- 下级会员 -->
                    <el-tab-pane label="收款信息" class="vip_achievement">
                        <dl class="data_box clearfix">
                            <dd ><p>支付宝<br><i>{{user_data.alipay}}</i><br></p></dd>
                            <dd ><p>真实姓名<br><i>{{user_data.alipay_name}}</i><br></p></dd>
                            <dd ><p>微信号<br><i>{{user_data.wechat}}</i><br></p></dd>
                            <dd ><p></p></dd>
                            <dd ><p>银行账户<br><i>{{user_data.bank}}</i><br></p></dd>
                            <dd ><p>开户名<br><i>{{user_data.bank_name}}</i><br></p></dd>
                            <dd ><p>开户行<br><i>{{user_data.bank_type}}</i><br></p></dd>
                            <dd ><p>开户网点<br><i>{{user_data.net_point}}</i><br></p></dd>
                        </dl>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import vip_details from "./vip_details";
export default vip_details;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_details {
    background: #fff;
    border-radius: 12px;
    margin: 12px auto;
    padding: 18px 0 28px;
    h1 {
      font-size: 16px;
      width: 96%;
      margin: 12px auto;
      font-weight: 700;
    }
    ul {
      width: 90%;
      margin: 0 auto 12px;
      li {
        float: left;
        width: 33%;
        height: 58px;
        line-height: 58px;
        font-size: 14px;
        span {
          font-weight: 700;
        }
        img {
          width: 58px;
          height: 58px;
          display: inline-block;
          vertical-align: middle;
          margin-left: 38px;
        }
      }
    }
    .vip_achievement {
      .data_box {
        border: #e7e7eb 1px solid;
        border-bottom: none;
        margin: 12px auto;
        dt {
          height: 30px;
          line-height: 30px;
          background: #eee;
          border-bottom: #e7e7e7 1px solid;
          font-size: 14px;
          font-weight: bold;
          text-indent: 1.5em;
          span {
            float: right;
            background: red;
            width: 80px;
            height: 28px;
            line-height: 28px;
            color: #fff;
            text-align: center;
            cursor: pointer;
            text-indent: 0;
            font-weight: 400;
            font-size: 12px;
            margin: 1px;
          }
        }
        dt:nth-child(2) {
          height: 188px;
          padding: 10px 0;
          background: #fff;
          img {
            width: 118px;
            float: left;
            margin: 35px 0 0 22px;
            background: #976d3b;
          }
          ul {
            margin: 4px 0 0 22px;
            float: left;
            width: calc(100% - 218px);
            font-weight: 400;
            li:first-child {
              font-weight: 700;
            }
            li {
              span {
                width: 158px;
              }
            }
          }
        }
        dd {
          line-height: 2;
          width: 25%;
          float: left;
          border-bottom: #e7e7e7 1px solid;
          p {
            text-align: left;
            border-right: #eee 1px solid;
            margin: 12px auto;
            text-indent: 28px;
            height: 68px;
            span {
              font-size: 22px;
              font-weight: 700;
              text-indent: 30px;
              display: block;
              color: #000;
            }
            i {
              font-size: 13px;
              text-indent: 16px;
              display: block;
              color: #000;
            }
          }
        }
      }
    }
    .order {
      span {
        float: right;
        background: red;
        width: 80px;
        height: 28px;
        line-height: 28px;
        color: #fff;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}
</style>



