<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <vipleftcomponents></vipleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <img class="search_img" src="../../../static/images/search.png" alt="">
                <div class="search_modle" >
                    <span>管理员搜索:</span>
                    <el-input  v-model="search_data.keyword" style="width:208px;display:inlie-block;vertical-align: middle;"  placeholder="请输入内容"></el-input>
                    <el-button  @click="search_get"  class="btn" type="danger" style="vertical-align: middle;">搜索</el-button>    
                    <el-button  type="success" @click="go_vip_admin_add" plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">添加管理员</el-button>
                </div>
            </div>
            <!-- 会员信息列表展示区 -->
            <div class="vip_admin_lis_table">
                <el-table :data="tableData"  @selection-change="handleSelectionChange" ref="multipleTable" border max-height="800" style="width: 100%">
                    <el-table-column  type="selection"  width="35"> </el-table-column>
                    <el-table-column prop="name"  label="用户名"  >  </el-table-column>
                    <el-table-column    label="操作" >
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="go_vip_admin_updata(1,scope.row.id)" >修改密码</el-button>
                            <el-button type="text" size="small" @click="del_message(scope.row.id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 快捷操作 -->
            <div class="operation" style='padding:12px 0 ;background:#fff'>
                <el-button style="margin-left:28px;"  @click="all_yes_select(tableData)">全选</el-button><el-button  @click="all_yes_select()">全不选</el-button>
                <el-button  @click="reverse_select">反选</el-button><el-button  @click="del_all">删除</el-button>
            </div>
            <!-- 会员列表信息分页 -->
            <div class="page"><pagecon :pages="pages" @get_lis="get_lis"></pagecon></div>                        
        </div>
    </div>
</div>
</template>
<script>
import vip_admin_lis from "./vip_admin_lis";
export default vip_admin_lis;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_admin_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
</style>



