import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import pagecon from '@/components/page/pagecon'
import citycom from '@/components/city/city'
import huanhuocompent from '@/views/huanhuo/huanhuocompent.vue'

export default {
  name: 'Tuanhuo'
  , data() {
    return {
      time_data: null
      , tableData: [] //table表格数据接收包
      , pages: null
      , multipleSelection: []       //记录勾选内容
      , order: []   //选择排序
      , search: {}  //搜索内容
      ,base_url:''
      ,show:0
      , province: null
      , city: null
      , district: null
      ,detailed:null
      ,express_id:null
      ,orders:{}
      ,id:null
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , huanhuocompent
    , pagecon
    , citycom
  }
  , created() {
    this.get_lis();
    this.base_url = this.axios.defaults.baseURL;
  }
  ,updated(){
      var that = this ;
  }
  , methods: {
    
    search_btn() {//搜索
      var that = this;
      let data = {};
      data.screen = that.search;
      data.token = http.getToken();
      http.ajaxPost('huanhuo', { data }, res => {
        if (res.data.status) {
          that.tableData = res.data.data.data;
          that.pages = res.data.data;
          delete that.pages.data;
        } else { that.$message({ type: 'info', message: res.data.message }) ;that.tableData = {} ;}
      })
    }
    ,fahuo(id){
        var that = this ;
        that.show = 1 ;
        that.id = id ;
        http.ajaxPost('huanhuo/edit/'+id, { data: { token: http.getToken()} }, res => {
            that.express_id = res.data.data.express_id;
            that.orders = res.data.data;
            that.province = that.orders.province ;
            that.city = that.orders.city ;
            that.district = that.orders.district ;
            that.detailed = that.orders.detailed ;

          })
    }
    ,close(){
        this.show  = 0 ;
        this.express_id = null;
        this.orders = {};
        this.detailed = null ;
        this.province = null
        this.city = null
        this.district =  null
    }
    ,updata_data(){

    }
    ,id_updata(){ //
        var that = this ;
        let data = {data:{token:http.getToken()}} ;
        data.data.city = that.city ;
        data.data.district = that.district ;
        data.data.detailed = that.detailed ;
        data.data.province = that.province ;
        data.data.express_id = that.express_id ;
        data.data.real_name = that.orders.real_name;
        data.data.mobile = that.orders.mobile;
        http.ajaxPost('huanhuo/edit/' + that.id, data, res => {
            if(res.data.status){ 
                that.$message({ type: 'success', message: res.data.message }) ;
                that.close();
                that.get_lis();
            }else{that.$message(res.data.message)}
          })

    }
    , get_lis(e) { //获取table数据  
      var that = this;
      http.ajaxPost('huanhuo', { data: { token: http.getToken(), screen: that.search }, page: e }, res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
        console.log(that.tableData)
      })
    }
    , commodity_del(id) { //删除商品 
      var that = this;
      var http_post = function () {
        http.ajaxPost('kalman/destroy', { data: { token: http.getToken(), id: id } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '删除成功' })
            that.get_lis();
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '删除后无法恢复, 是否继续', '确认删除', http_post);
    }
    ,go_kami_add(){
      this.$router.push({path:'/home/kalman_add'})
    }
    ,kalman_btn(){
      let URL = this.axios.defaults.baseURL + 'kalman/all_export/' + http.getToken();
      window.open(URL, '_blank', 'width=320,height=568,menubar=no,toolbar=no,status=no,scrollbars=yes')
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , handleSelectionChange(val) {  //选中的元素
      this.multipleSelection = val;
    }
    , all_yes_select(rows) { //快捷全选   全不选
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    }
    , reverse_select() {
      var that = this;
      if (that.multipleSelection.length == 0) { //如果没有勾选  反选就是全选
        that.all_yes_select(that.tableData);
      } else {
        var select_data = []; //存放已经被勾选的数据
        var new_data = []; //反选后填入的数据
        for (let i in that.tableData) { //首先存入全部数据
          new_data.push(that.tableData[i])
        }
        for (var i in that.multipleSelection) { //此时 获取之前 已经勾选过得数据 
          select_data.push(that.multipleSelection[i])
        }
        that.all_yes_select();//清空之前勾选过得内容
        //获取没有被勾选的数据
        for (var i = 0; i < select_data.length; i++) {
          for (var j = 0; j < new_data.length; j++) {
            if (new_data[j] == select_data[i]) {
              new_data.splice(j, 1)
            }
          }
        }
        if (new_data.length == 0) { //如果没有
          that.all_yes_select();
        } else {
          that.all_yes_select(new_data);
        }
      }
    }
    , del_all() { //全部删除
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '请选择删除内容' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      var http_post = function () {
        http.ajaxPost('kalman/destroy', { data: data }, (res) => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '删除成功' })
          } else { that.$message({ type: 'info', message: '操作失败' }) }
        })
      }
      http.my_alert(that, '删除后无法恢复,您确定要删除吗?', '全部删除', http_post);
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
     //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    //城市三级联动
    , province_id(value) {
        this.province = value;
      }
      , city_id(value) {
        this.city = value;
      }
      , district_id(value) {
        this.district = value;
      }
      , province_id_search(value) {
        this.search.province = value;
      }
      , city_id_search(value) {
        this.search.city = value;
      }
      , district_id_search(value) {
        this.search.district = value;
      }
      //修改的三级联动
      , province_ids(value) {
        this.province = value;
      }
      , city_ids(value) {
        this.city = value;
      }
      , district_ids(value) {
        this.district = value;
      }
      //修改的三级联动
      , province_idss(value) {
        this.supply.province = value;
      }
      , city_idss(value) {
        this.supply.city = value;
      }
      , district_idss(value) {
        this.supply.district = value;
      }
  }
}


