import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import pagecon from '@/components/page/pagecon'
import citycom from '@/components/city/city'
import kalmanleftcomponents from '@/views/kalman/kalman_left_components.vue'

export default {
  name: 'Kalman'
  , data() {
    return {
        max_url: { type: 2 }     
        ,pic_url: 'uploading' 
        ,base_Url:''
        ,data_lis:{name:null,pic:null,price:null}
    }
  }
  , components: {
    homelefttop
    , homeleftbottom
    , kalmanleftcomponents
    , pagecon
    , citycom
  }
  , created() {
   
    this.base_Url = this.axios.defaults.baseURL;
    this.pic_url = this.axios.defaults.baseURL + this.pic_url
    this.get_lis();
  }
  , methods: {
    is_ok(){
        var that = this ;
        if(that.data_lis.name==null || that.data_lis.name == '' || that.data_lis.pic == null || that.data_lis.price == null || that.data_lis.price=='' ){
            that.$message('请输入重要信息');
            return;
        }
        let data = {} ;
        data.data = that.data_lis;
        data.data.token = http.getToken();  
        http.ajaxPost('kalman/edit', data, res => {
            if(res.data.status){
                that.$message({
                    message:res.data.message ,
                    type: 'success'
                  });
            }else{ that.$message(res.data.message);}
        })
    }
    ,get_lis(){
        var that = this ;
        let data = {data:{token:http.getToken()}};
        http.ajaxPost('kalman/edit', data, res => {
            that.data_lis.name = res.data.data.name
            that.data_lis.pic = res.data.data.pic
            that.data_lis.price = res.data.data.price*1
        })
    }

    // 头像上传
    ,beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    }
    ,handlePreview(){

    }
    ,handleRemove(){

    }
    ,beforeRemove(){

    }
    ,pic_status(e){
        this.data_lis.pic = e.url;
    }
    ,handleExceed(){

    }
    ,fileList_pic(){

    }
  }
}


