<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>{{title_top}}</span>
                    <el-button type="success" @click="is_show_updata" plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">{{title}}</el-button>
                </div>
            </div>
            <div v-if="is_show">
                <!-- 分类展示 -->
                <div class="column_modle">
                    <el-table :data="tableData"  border max-height="800" style="width: 100%" @selection-change="handleSelectionChange"  ref="multipleTable" >
                        <el-table-column  type="selection"  width="40" :select-on-indeterminate='true' > </el-table-column>
                        <el-table-column   label="排序"  width="99" > 
                            <template   slot-scope="scope" > 
                                <el-input type="number" size='mini' v-model="scope.row.order" ></el-input>        
                            </template> 
                        </el-table-column>
                        <el-table-column   label="标题"  width="250" >
                            <template   slot-scope="scope" > 
                            <p>{{scope.row.name}}</p>
                            </template> 
                        </el-table-column>
                        <el-table-column   label="链接"  >
                            <template   slot-scope="scope" > 
                            <a :href="scope.row.url">{{scope.row.url}}</a>
                            </template> 
                        </el-table-column>
                        <el-table-column   label="操作"  width="150">
                            <template slot-scope="scope">
                                <div >
                                    <el-button size="mini" type="primary" @click="commodity_brand_updata(scope.row.id)" >编辑</el-button>
                                    <el-button size="mini" type="danger" @click="commodity_brand_del(scope.row.id)">删除</el-button> 
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!-- 快捷操作 -->
                    <div class="operation" style='padding:12px 0 ;background:#fff'>
                        <el-button style="margin-left:28px;" @click="all_yes_select(tableData)">全选</el-button><el-button @click="all_yes_select()">全不选</el-button>
                        <el-button @click="reverse_select" >反选</el-button><el-button @click="del_all">删除</el-button>
                    </div>
                </div>
                <!-- 提示 -->
                <div style="background:#fff;border-radis:12px;margin-top:12px;line-height:1.5">
                    <h1 style="width:90%;text-align:left;padding-top:12px;margin:0 auto;font-weight:700;font-size:14px" >页面功能说明：</h1>
                    <p style="width:90%;text-align:left;text-indent:22px;padding:12px;margin:0 auto;" >可以预先添加您的产品中，所有的品牌名称及LOGO，在添加产品的时候可直接进行选择，在网站前台，如果该分类下有品牌，他则会显示品牌的搜索，在产品详细页，右侧则会显示该品牌LOGO。点击LOGO可进入该品牌的所有产品</p>
                </div>                                  
            </div>
            <!-- 隐藏操作 -->
            <div v-if="!is_show" style="background:#fff;border-radius:12px;margin-top:12px;">
                <div class="commodity_brand_add_box">
                    <div class="vip_set_cont_modle" style="padding-top:18px"><span>品牌名称:</span><el-input size='small ' v-model="add_lis.name" style="width:282px;margin-left:3px"></el-input></div>
                    <div class="vip_set_cont_modle"><span>分类:</span>
                        <el-select class="select" size='small '  v-model="add_lis.category_id" style="width:188px" >
                            <el-option :label="item.name" v-for="(item,index) in data_lis" :key='index'  :value="item.id">{{item.name}}</el-option>
                        </el-select>
                    </div> 
                    <div class="vip_set_cont_modle" style="padding-top:18px"><span>排序:</span><el-input size='small ' type="number" v-model="add_lis.order" style="width:282px;margin-left:3px"></el-input></div>
                    <div class="vip_set_cont_modle" style="padding-top:18px"><span>链接地址:</span><el-input size='small ' v-model="add_lis.url" style="width:282px;margin-left:3px"></el-input></div>
                    <div class="vip_set_cont_modle"><span>图片上传:</span>
                        <el-upload style="display:inline-block;vertical-align: middle;margin-top:12px;" :action="base_url" :data="data_url" :on-success='pic_status' :file-list="fileList"  list-type="picture-card" :limit="1" >
                            <i class="el-icon-plus"></i>
                        </el-upload>
                        <div style="width:60%;margin:12px auto 0;" slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb , 替换/添加需把默认图片删除</div>

                    </div> 
                    <el-button  type="success" @click="commodity_brand_add" plain>确&nbsp;&nbsp;认</el-button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import commodity_brand from "./commodity_brand";
export default commodity_brand;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }

  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
.commodity_brand_add_box {
  width: 80%;
  margin: 12px auto;
  padding-bottom: 22px;
  .vip_set_cont_modle {
    margin-top: 8px;
    span {
      width: 128px;
      display: inline-block;
      text-align: right;
      margin-right: 18px;
    }
  }
  button {
    display: block;
    margin: 28px auto;
  }
}
</style>



