import Vue from 'vue'
import App from './App'
import router from './router'
import moment from 'moment'//导入文件
import ElementUI from 'element-ui'; import 'element-ui/lib/theme-chalk/index.css'; Vue.use(ElementUI);
import axios from 'axios'; import VueAxios from 'vue-axios'; Vue.use(VueAxios, axios);
import Cookies from 'js-cookie'
axios.defaults.baseURL = 'http://hjac.zhongboyx.cn/';
// axios.defaults.baseURL = 'http://www.operating_system.com/';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
Vue.config.productionTip = false;
import http from '@/api/http';
import '../static/css/reset.css';  //引入复位样式
// 时间转换
Vue.filter('time', function (value, formatString) {  //挂载全局 时间格式化 过滤器  value  是输入内容
  if (value == 0) {
    return '时间信息错误!'
  } else {
    formatString = formatString || 'YYYY-MM-DD HH:mm';
    return moment.unix(value).format(formatString); // 这是时间戳转时间
  }
  
})
//路由守卫

router.beforeEach(function(to,from,next){
  if (to.meta.requireAuth) {  
   let token =  http.getToken();
      if (token) {  
        next();
      }else{
        next({
            path: '/login'
            ,query: {redirect: to.fullPath}  
            })
        }
  }else{
      next();
  }
})
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



