<template>
<div>
    <img class="touxiang" src="../../../static/images/icon_logo.png" alt="">
    <p style="font-size:12px;">合家安管理系统：欢迎您回来</p>
    <div class="tiaozhuan clearfix">
        <router-link to='/home/vip_admin_lis'>管理员设置 </router-link>
        <a href="javascript:;" @click="out_login">退出登陆</a>
    </div>
    <p style="font-size:12px;color:red;margin-top:18px">管理员统一权限,</p>
    <p style="font-size:12px;color:red;margin-top:3px">修改前请于其它管理员进行沟融</p>
</div>
</template>
<script>
import http from "@/api/http";

export default {
  name: "home_left_top",
  data() {
    return {};
  },
  methods: {
    out_login() {
      http.clearCookie();
      this.$router.push({ path: "/login" });
    } //退出登陆
  }
};
</script>
<style>
</style>



