<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <huanhuocompent></huanhuocompent>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <img class="search_img" src="../../../static/images/search.png" alt>
          <div class="search_modle">
            <span>用户名查询:</span>
            <el-input
              size="small"
              style="width:208px;display:inlie-block;"
              v-model="search.keyword"
              placeholder="请输入内容"
            ></el-input>
            <el-select size="small" class="select" v-model="search.status" placeholder="状态">
              <el-option label="全部" :value="null"></el-option>
              <el-option label="待处理" value="0"></el-option>
              <el-option label="已发货" value="1"></el-option>
              <el-option label="已完成" value="2"></el-option>
            </el-select>
            <div style="float:right;margin-right:158px;">
              <el-button
                size="small"
                @click="search_btn"
                class="btn"
                style="margin-left:58px;width:128px"
                type="danger"
              >&nbsp;搜&nbsp;&nbsp;索&nbsp;</el-button>
              <!-- <el-button size="small" type="danger" plain @click="kalman_btn">全部导出</el-button>
              <el-button size="small" type="success" plain @click="go_kami_add">生成卡密</el-button>-->
            </div>
          </div>
          <div class="search_modle" style="padding-bottom:6px;">
            <span>选择时间:</span>
            <el-date-picker
              type="datetimerange"
              style="width: 398px;height:38px;vertical-align: middle;"
              v-model="search.times"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['12:00:00']"
            ></el-date-picker>
          </div>
        </div>
        <!-- 会员信息列表展示区 -->
        <div class="vip_lis_table">
          <el-table :data="tableData" border style="width: 100%">
            <el-table-column label="信息" width="198" fixed>
              <template slot-scope="scope" style="font-size:12px">
                <p>订单号:{{scope.row.user_buy.order_sn}}</p>
                <p>用户:{{scope.row.user_buy.username}}</p>
                <p>时间:{{scope.row.user_buy.updated_at | time}}</p>
              </template>
            </el-table-column>
            <el-table-column label="商品图片" width="78">
              <template slot-scope="scope">
                <img
                  style="margin: 0px auto;width: 90%;display: block;"
                  :src="base_url + scope.row.user_buy.pic_url"
                  alt
                >
              </template>
            </el-table-column>
            <el-table-column label="商品信息" width="148">
              <template slot-scope="scope">
                <p>{{scope.row.user_buy.productname}}</p>
                <p>{{scope.row.user_buy.parameters_name}}</p>
                <p>{{scope.row.user_buy.parameters_values}}</p>
                <p>价格:{{scope.row.user_buy.sale_const}}</p>
              </template>
            </el-table-column>

            <el-table-column label="换货状态" width="238">
              <template slot-scope="scope">
                <p>{{scope.row.status}}</p>
                <p >用户快递单号:{{scope.row.order_sn}}</p>
                <p v-if="scope.row.express_id">换货快递单号:{{scope.row.express_id}}</p>
              </template>
            </el-table-column>
            <el-table-column label="支付信息" width="198" >
              <template slot-scope="scope">
                <p>支付类型:{{scope.row.user_buy.type}}</p>
                <p>{{scope.row.user_buy.is_kalman?'使用卡密':'未使用卡密'}}</p>
                <p v-if="scope.row.user_buy.is_kalman">卡密:{{scope.row.user_buy.kalman}}</p>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <div>
                  <el-button
                    v-if="scope.row.status=='待处理'"
                    size="mini"
                    @click="fahuo(scope.row.id)"
                    type="primary"
                  >换货</el-button>
                  <!-- <el-button size="mini" @click="commodity_del(scope.row.id)" type="primary">作废</el-button> -->
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div v-if="show" class="back_shadow">
          <div class="vip_vip_updata">
            <div class="vip_set_cont_modle_top">
              退货操作
              <span @click="close" class="el-icon-error"></span>
            </div>
            <div class="vip_set_cont_modle">
              <span>收货人:</span>
              <el-input v-model="orders.real_name" clearable></el-input>
              <span>联系电话:</span>
              <el-input v-model="orders.mobile" type="number" clearable></el-input>
            </div>
           
            <div class="vip_set_cont_modle">
              <span>所在区域:</span>
              <citycom
                :province_ids="province"
                :city_ids="city"
                :district_ids="district"
                v-on:province_id="province_ids"
                v-on:city_id="city_ids"
                v-on:district_id="district_ids"
              ></citycom>
            </div>
              <div class="vip_set_cont_modle">
                <span>联系地址:</span>
                <el-input v-model="detailed" clearable></el-input>
                <span>快递单号:</span>
                <el-input v-model="express_id" clearable></el-input>
              </div>
            <el-button @click="id_updata" type="success" plain>提交</el-button>
          </div>
        </div>
        <!-- 快捷操作 -->
        <!-- <div class="operation" style="padding:12px 0 ;background:#fff">
          <el-button style="margin-left:28px;" @click="all_yes_select(tableData)">全选</el-button>
          <el-button @click="all_yes_select()">全不选</el-button>
          <el-button @click="reverse_select">反选</el-button>
          <el-button @click="del_all">作废</el-button>
        </div>-->
        <!-- 会员列表信息分页 -->
        <div class="page">
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import huanhuo from "./huanhuo";
export default huanhuo;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
  .back_shadow {
    width: 100%;
    min-width: 1000px;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 4;
    .vip_vip_updata {
      position: absolute;
      width: 768px;
      background: #fff;
      left: calc(50% - 384px);
      top: 15%;
      border-radius: 12px;
      margin-top: 12px;
      padding: 0 0 18px;
      .vip_set_cont_modle_top {
        background: #409eff;
        border-radius: 12px 12px 0 0;
        height: 58px;
        line-height: 58px;
        position: relative;
        text-align: center;
        color: #fff;
        font-weight: 700;
        font-size: 16px;
        span {
          position: absolute;
          font-size: 26px;
          color: #ccc;
          float: right;
          top: 16px;
          right: 16px;
          cursor: pointer;
        }
      }
      .vip_set_cont_modle {
        width: 90%;
        margin: 12px auto;
        span {
          font-size: 16px;
          display: inline-block;
          width: 108px;
          text-align: right;
          margin-right: 18px;
        }
        i {
          display: inline-block;
          padding: 0 12px;
          font-size: 14px;
        }
        .el-input {
          width: 188px;
          display: inlie-block;
          vertical-align: middle;
        }
        .select {
          width: 128px;
        }
        // 上传头像样式
        .avatar-uploader {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
          display: inline-block;
        }
        .avatar-uploader:hover {
          border-color: #409eff;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 178px;
          height: 178px;
          line-height: 178px;
          text-align: center;
        }
        .avatar {
          width: 178px;
          height: 178px;
          display: block;
        }
      }
      button {
        width: 158px;
        display: block;
        margin: 38px auto 12px;
      }
    }
    .vip_grade {
      .vip_set_cont_modle {
        width: 90%;
        margin: 12px auto;
        span {
          width: 128px;
          margin-right: 48px;
        }
        .select {
          width: 258px;
        }
        .el-input {
          width: 260px;
        }
      }
    }
    .vip_collect {
      position: absolute;
      width: 768px;
      background: #fff;
      left: calc(50% - 384px);
      top: 20%;
      border-radius: 12px;
      margin-top: 12px;
      padding: 0 0 18px;
      .vip_set_cont_modle_top {
        background: #409eff;
        border-radius: 12px 12px 0 0;
        height: 58px;
        line-height: 58px;
        position: relative;
        text-align: center;
        color: #fff;
        font-weight: 700;
        font-size: 16px;
        span {
          position: absolute;
          font-size: 26px;
          color: #ccc;
          float: right;
          top: 16px;
          right: 16px;
          cursor: pointer;
        }
      }
      .vip_set_cont_modle {
        width: 80%;
        margin: 12px auto;
        span {
          font-size: 16px;
          display: inline-block;
          width: 128px;
          text-align: right;
          margin-right: 18px;
        }
        .el-input {
          width: 258px;
          display: inlie-block;
          vertical-align: middle;
        }
        .select {
          width: 128px;
        }
        // 上传头像样式
        .avatar-uploader {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
          display: inline-block;
        }
        .avatar-uploader:hover {
          border-color: #409eff;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 178px;
          height: 178px;
          line-height: 178px;
          text-align: center;
        }
        .avatar {
          width: 178px;
          height: 178px;
          display: block;
        }
      }
      button {
        width: 158px;
        display: block;
        margin: 38px auto 12px;
      }
    }
  }
}
</style>



