<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <settingcompent></settingcompent>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <div class="search_modle">
            <span>首页轮播</span>
              <el-button @click="add_data" type="success" style="float:right;margin:10px 58px;" plain>添加轮播图</el-button>
          </div>
        </div>
        <!-- 分类展示 -->
        <div class="column_modle">
          <!-- 会员信息列表展示区 -->
          <div class="vip_lis_table">
            <el-table :data="tableData" border style="width: 100%">
              <el-table-column label="跳转路径" width="318" >
                <template slot-scope="scope" style="font-size:12px">
                  {{scope.row.url}}
                </template>
              </el-table-column>
              <el-table-column label="轮播图片" width="208">
                <template slot-scope="scope">
                  <img
                    style="margin: 0px auto;width: 90%;display: block;"
                    :src="base_Url + scope.row.pic"
                    alt
                  >
                </template>
              </el-table-column>
              <el-table-column label="排序" width="58">
                <template slot-scope="scope">
                  <p>{{scope.row.order}}</p>
                </template>
              </el-table-column>
              <el-table-column label="时间" >
                <template slot-scope="scope">
                  <p>{{scope.row.created_at | time}}</p>
                </template>
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <div>
                    <el-button
                      size="mini"
                      @click="fahuo(scope.row.id)"
                      type="primary"
                    >修改</el-button>
                    <el-button size="mini" @click="commodity_del(scope.row.id)" type="primary">删除</el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div v-if="show" class="back_shadow">
            <div class="vip_vip_updata">
              <div class="vip_set_cont_modle_top">
                轮播图设置
                <span @click="close" class="el-icon-error"></span>
              </div>
              <div class="vip_set_cont_modle">
                <span>跳转路径:</span>
                <el-input v-model="url" style="width:50%" clearable></el-input>
              </div>
              <div class="vip_set_cont_modle">
                <span>排序:</span>
                <el-input v-model="order" type="number" clearable></el-input>
              </div>
              <div class="vip_set_cont_modle">
            <span>卡密图片:</span>
            <el-upload
              style="display:inline-block;vertical-align: middle;margin-top:12px;"
              class="upload-demo"
              :action="pic_url"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :before-remove="beforeRemove"
              :before-upload="beforeAvatarUpload"
              :on-success="pic_status"
              :multiple="false"
              :limit="1"
              :on-exceed="handleExceed"
              :data="max_url"
            >
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </div>
              <el-button @click="id_updata" type="success" plain>提交</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import setting from "./setting";
export default setting;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    min-height: 100px;
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
  }
  .back_shadow {
    width: 100%;
    min-width: 1000px;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 4;
    .vip_vip_updata {
      position: absolute;
      width: 768px;
      background: #fff;
      left: calc(50% - 384px);
      top: 15%;
      border-radius: 12px;
      margin-top: 12px;
      padding: 0 0 18px;
      .vip_set_cont_modle_top {
        background: #409eff;
        border-radius: 12px 12px 0 0;
        height: 58px;
        line-height: 58px;
        position: relative;
        text-align: center;
        color: #fff;
        font-weight: 700;
        font-size: 16px;
        span {
          position: absolute;
          font-size: 26px;
          color: #ccc;
          float: right;
          top: 16px;
          right: 16px;
          cursor: pointer;
        }
      }
      .vip_set_cont_modle {
        width: 90%;
        margin: 12px auto;
        span {
          font-size: 16px;
          display: inline-block;
          width: 108px;
          text-align: right;
          margin-right: 18px;
        }
        i {
          display: inline-block;
          padding: 0 12px;
          font-size: 14px;
        }
        .el-input {
          width: 188px;
          display: inlie-block;
          vertical-align: middle;
        }
        .select {
          width: 128px;
        }
        // 上传头像样式
        .avatar-uploader {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
          display: inline-block;
        }
        .avatar-uploader:hover {
          border-color: #409eff;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 178px;
          height: 178px;
          line-height: 178px;
          text-align: center;
        }
        .avatar {
          width: 178px;
          height: 178px;
          display: block;
        }
      }
      button {
        width: 158px;
        display: block;
        margin: 38px auto 12px;
      }
    }
    .vip_grade {
      .vip_set_cont_modle {
        width: 90%;
        margin: 12px auto;
        span {
          width: 128px;
          margin-right: 48px;
        }
        .select {
          width: 258px;
        }
        .el-input {
          width: 260px;
        }
      }
    }
    .vip_collect {
      position: absolute;
      width: 768px;
      background: #fff;
      left: calc(50% - 384px);
      top: 20%;
      border-radius: 12px;
      margin-top: 12px;
      padding: 0 0 18px;
      .vip_set_cont_modle_top {
        background: #409eff;
        border-radius: 12px 12px 0 0;
        height: 58px;
        line-height: 58px;
        position: relative;
        text-align: center;
        color: #fff;
        font-weight: 700;
        font-size: 16px;
        span {
          position: absolute;
          font-size: 26px;
          color: #ccc;
          float: right;
          top: 16px;
          right: 16px;
          cursor: pointer;
        }
      }
      .vip_set_cont_modle {
        width: 80%;
        margin: 12px auto;
        span {
          font-size: 16px;
          display: inline-block;
          width: 128px;
          text-align: right;
          margin-right: 18px;
        }
        .el-input {
          width: 258px;
          display: inlie-block;
          vertical-align: middle;
        }
        .select {
          width: 128px;
        }
        // 上传头像样式
        .avatar-uploader {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
          display: inline-block;
        }
        .avatar-uploader:hover {
          border-color: #409eff;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 178px;
          height: 178px;
          line-height: 178px;
          text-align: center;
        }
        .avatar {
          width: 178px;
          height: 178px;
          display: block;
        }
      }
      button {
        width: 158px;
        display: block;
        margin: 38px auto 12px;
      }
    }
  }
}
</style>



