<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <distributionleftcomponents></distributionleftcomponents>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <img class="search_img" src="../../../static/images/search.png" alt>
          <div class="search_modle">
            <span>账号搜索:</span>
            <el-input
              size="small"
              style="width:208px;display:inlie-block;"
              v-model="search.keyword"
              placeholder="请输入内容"
            ></el-input>
            <el-select size="small" class="select" v-model="search.status" placeholder="提现状态">
              <el-option label="全部" :value="null"></el-option>
              <el-option label="申请中" value="0"></el-option>
              <el-option label="已提现" value="1"></el-option>
            </el-select>
            <div style="float:right;margin-right:188px;">
              <el-button
                size="small"
                @click="search_btn"
                class="btn"
                style="margin-left:58px;width:128px;"
                type="danger"
              >&nbsp;搜&nbsp;&nbsp;索&nbsp;</el-button>
            </div>
          </div>
          <div class="search_modle" style="padding-bottom:6px;">
            <span>选择时间:</span>
            <el-date-picker
              type="datetimerange"
              style="width: 398px;height:38px;vertical-align: middle;"
              v-model="search.times"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['12:00:00']"
            ></el-date-picker>
          </div>
        </div>
        <!-- 会员信息列表展示区 -->
        <!-- @selection-change="handleSelectionChange" -->
        <div class="vip_lis_table" style="padding:19px 0 ;">
          <el-table :data="tableData" ref="multipleTable" border style="width: 96%;margin:0 auto; ">
            <el-table-column label="操作账号" fixed width="138px"> 
              <template slot-scope="scope">{{scope.row.username}}</template>
            </el-table-column>
            <el-table-column label="操作金额"  width="138px">
              <template slot-scope="scope">{{scope.row.price}}</template>
            </el-table-column>
           
            <el-table-column label="提现状态" width="188px">
              <template slot-scope="scope">
                <el-switch v-model="scope.row.status" @change="updata_status(scope.row.id)"  :active-value='1' :inactive-value='0' active-text="提现完成" inactive-text="申请中" :disabled='scope.row.status?true:false'></el-switch>
              </template>
            </el-table-column>
             <el-table-column label="提现方式" width="138px">
              <template slot-scope="scope">{{scope.row.type}}</template>
            </el-table-column>
             <el-table-column label="操作时间" width="158px">
              <template slot-scope="scope">{{scope.row.created_at | time}}</template>
            </el-table-column>
            <el-table-column label="银行账户" width="138px">
              <template slot-scope="scope">{{scope.row.bank }}</template>
            </el-table-column>
            <el-table-column label="开户行" width="138px">
              <template slot-scope="scope">{{scope.row.bank_type }}</template>
            </el-table-column>
            <el-table-column label="开户名" width="138px">
              <template slot-scope="scope">{{scope.row.bank_name }}</template>
            </el-table-column>
            <el-table-column label="开户网点" width="138px">
              <template slot-scope="scope">{{scope.row.net_point }}</template>
            </el-table-column>

            <!-- <el-table-column label="操作">
              <template slot-scope="scope">
                <div>
                  <el-button size="mini" @click="commodity_del(scope.row.id)" type="primary">作废</el-button>
                </div>
              </template>
            </el-table-column>-->
          </el-table>
        </div>
        <!-- 快捷操作 -->
        <!-- <div class="operation" style="padding:12px 0 ;background:#fff">
          <el-button style="margin-left:28px;" @click="all_yes_select(tableData)">全选</el-button>
          <el-button @click="all_yes_select()">全不选</el-button>
          <el-button @click="reverse_select">反选</el-button>
          <el-button @click="del_all">作废</el-button>
        </div>-->
        <!-- 会员列表信息分页 -->
        <div class="page">
          <pagecon :pages="pages" @get_lis="get_lis"></pagecon>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import updata_product from "./updata_product";
export default updata_product;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
</style>



