<template>
  <div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
      <!-- 左侧公共top -->
      <homelefttop></homelefttop>
      <!-- 私有二级导航 -->
      <kalmanleftcomponents></kalmanleftcomponents>
      <!-- 左侧公共bottom -->
      <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
      <div class="Layout_right_box">
        <!-- 顶部搜索功能 -->
        <div class="search">
          <img class="search_img" src="../../../static/images/search.png" alt>
          <div class="search_modle">
            <span style="font-size:16px;font-weight:700">设置卡密:</span>
          </div>
        </div>
        <!-- 会员信息列表展示区 -->
        <div class="vip_lis_table">
          <div class="vip_set_cont_modle">
            <span >卡密名称:</span>
            <el-input v-model="data_lis.name" placeholder="请输入卡密名称" clearable></el-input>
          </div>
          <div class="vip_set_cont_modle">
            <span>卡密价格:</span>
            <el-input v-model="data_lis.price" type="number" placeholder="请输入卡密价格" clearable></el-input>
          </div>
          <div class="vip_set_cont_modle">
            <span>卡密图片:</span>
            <el-upload
              style="display:inline-block;vertical-align: middle;margin-top:12px;"
              class="upload-demo"
              :action="pic_url"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :before-remove="beforeRemove"
              :before-upload="beforeAvatarUpload"
              :on-success="pic_status"
              :multiple="false"
              :limit="1"
              :on-exceed="handleExceed"
              :data="max_url"
            >
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </div>
        <el-button @click="is_ok" type="primary"  style="display:block;width:188px;margin:28px auto;">确认提交</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import kalman_set from "./kalman_set";
export default kalman_set;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
    padding: 18px 0;
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 108px;
        text-align: right;
        margin-right: 18px;
      }
      i {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
      // 上传头像样式
      .avatar-uploader {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        display: inline-block;
      }
      .avatar-uploader:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
      }
      .avatar {
        width: 178px;
        height: 178px;
        display: block;
      }
    }
  }
}
</style>



