import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'
export default {
  name: 'Send_letter'
  , data() {
    return {
      data_lis: {send_name:'admin'}
      ,level:[]
      ,type:1
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'vipleftcomponents': vipleftcomponents
  }
  , created() {
    var that = this;
    http.ajaxPost('inletter/create', { data:{token: http.getToken()} }, res => {
      if (res.data.status) {
        that.level = res.data.data
       }
    })
  },
  methods: {
    //跳转至站内信页
    go_vip_letter_record() {
      this.$router.push({ path: '/home/vip_letter_record' })
    }
    , add_data() {
      var that = this;
      let data = that.data_lis;
      if(data.send_name == null || data.send_name == '' ){
        that.$message({ type: 'info', message: '请输入发件人' })
        return;
      }
      if(that.type){
        delete data.name;
      }else{
        delete data.level;
        if(data.name == null || data.name == ''){
          that.$message({ type: 'info', message: '请输入收件人' })
          return;
        }
      }
      data.token = http.getToken();
      http.ajaxPost('inletter/create', { data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '操作成功' })
          that.$router.push({ path: '/home/vip_letter_record' })
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
  }
}


