import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'
export default {
  name: 'Vip_details'
  , data() {
    return {
      children_data: [] //下级会员
      ,order_data:[] //订单记录
      ,capital_log_data:[] //资金记录
      ,user_data:{} //用户信息
      ,achievement_data:{}  //会员业绩
      ,img_url : '' //图片路径
    }
  }
  , components: {
     homelefttop
    ,homeleftbottom
    ,vipleftcomponents
  }
  , created() {
    var that = this ;
    that.img_url = that.axios.defaults.baseURL;
    // if(that.$route.query.id){that.my_created();};
    if(that.$route.query.id) that.my_created() ;
  }
  , methods: {
    my_created(){
      var that = this ;
      http.ajaxPost('user/show/'+that.$route.query.id,{data:{token:http.getToken()}},res=>{
        console.log(res.data);
        if(res.data.status){
          that.user_data = res.data.data.user;
          that.achievement_data = res.data.data.user.achievement;
          console.log(that.achievement_data)
          that.order_data = res.data.data.user.order;
          that.capital_log_data = res.data.data.user.capital_log;
          that.children_data = res.data.data.user.children;
        }else{that.$message({ type: 'info', message: res.data.message }) ;}
      })
    }
    ,go_vip_lis() { //跳转至管理员列表
      this.$router.push({ path: '/home/vip_lis' })
    }
  }
}



