<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <vipleftcomponents></vipleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>添加管理员</span>
                    <el-button type="success" plain style="vertical-align: middle;width:144px;float:right;margin: 10px;" @click="go_vip_lis">会员列表</el-button>
                </div>
            </div>
            <div class="vip_vip_add">
                 <div class="vip_set_cont_modle">
                     <span>用户名:</span><el-input v-model="data_all.name" clearable></el-input>
                     <span>密码:</span><el-input v-model="data_all.password"  type="password"  clearable ></el-input>
                     <span>确认密码:</span><el-input v-model="data_all.is_password" type="password"  clearable ></el-input>
                </div>
                <div class="vip_set_cont_modle">
                     <span>姓名:</span><el-input  v-model="data_all.nickname" clearable ></el-input>
                     <span>联系地址:</span><el-input v-model="data_all.detailed"  clearable ></el-input>
                     <span>联系电话:</span><el-input v-model="data_all.mobile" clearable type="number"  ></el-input>
                </div>
                <div class="vip_set_cont_modle">
                     <span>联系QQ:</span><el-input v-model="data_all.qq" clearable  ></el-input>
                     <span>E-mail:</span><el-input v-model="data_all.email" clearable type="email" ></el-input>
                </div>
                <div class="vip_set_cont_modle">
                    <span>是否激活:</span>
                    <el-radio v-model="data_all.status"  :label="1">是</el-radio>
                    <el-radio v-model="data_all.status"  :label="2">否</el-radio>
                </div>
                <div class="vip_set_cont_modle" >
                    <span>所在区域:</span>
                    <citycom v-on:province_id="province_id" v-on:city_id="city_id" v-on:district_id="district_id" ></citycom>
                </div>
                <div class="vip_set_cont_modle">
                    <span>上传头像:</span>
                    <el-upload class="avatar-uploader"
                       :action="base_url" :data="data_url"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </div>
                <!-- 根据变化显示 1-->
                <div v-if="agent_province==2" class="vip_set_cont_modle">
                  <span>总代地区:</span>
                    <citycom v-on:province_id2="province_id" v-on:city_id2="city_id" v-on:district_id2="district_id" ></citycom>
                </div>
                <!-- 根据变化显示 2 -->
                <div v-if="agent_province==3">
                   <div  class="vip_set_cont_modle">
                      <span>可发布类别:</span>
                      <p style="width:680px;display:inline-block;vertical-align: middle;">
                        <el-checkbox v-for="(items,index) in category_id" :key='index' v-model="items.checked" style="margin:0;width:128px"  :true-label="1" :false-label='0' >{{items.name}}</el-checkbox>
                      </p>
                  </div>
                  <div class="vip_set_cont_modle">
                    <span>上传LOGO:</span>
                    <el-upload class="avatar-uploader"
                        :action="base_url" :data="data_url"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess1"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="imageUrl1" :src="imageUrl1" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                  </div>
                  <div class="vip_set_cont_modle">
                      <span>商店名称:</span><el-input  v-model="data_all.supply.name" clearable ></el-input>
                      <span>免邮额度:</span><el-input v-model="data_all.supply.free_postage" clearable  ></el-input>
                  </div>
                  <div class="vip_set_cont_modle">
                      <span>客服电话:</span><el-input  v-model="data_all.supply.service_mobile" clearable ></el-input>
                      <span>客服QQ:</span><el-input v-model="data_all.supply.service_qq"  clearable ></el-input>
                      <span>客服旺旺:</span><el-input v-model="data_all.supply.service_wangwang" clearable type="number"  ></el-input>
                  </div>
                </div>
                <el-button type="success" @click="add_vip" plain>添加会员</el-button>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import vip_vip_add from "./vip_vip_add";
export default vip_vip_add;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_vip_add {
    background: #fff;
    border-radius: 12px;

    margin-top: 12px;
    padding: 18px 0;
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 88px;
        text-align: right;
        margin-right: 18px;
      }
      .el-input {
        width: 172px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 152px;
      }
      // 上传头像样式
      .avatar-uploader {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        display: inline-block;
      }
      .avatar-uploader:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
      }
      .avatar {
        width: 178px;
        height: 178px;
        display: block;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
}
</style>



