<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>{{title}}</span>
                    <el-button type="success" @click="go_commodity_evaluate"  plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">评价列表</el-button>
                </div>
            </div>
            <!-- 分类展示 -->
            <div class="column_modle">
                <ul>
                    <li><span>评价人用户名</span><el-input v-model="all_data.user_name" size="medium " style="width:688px" clearable></el-input></li>
                    <li><span>产品名称</span><el-input v-model="all_data.product_name" size="medium " style="width:688px" clearable></el-input></li>
                    <li><span>评价星级</span><el-rate v-model="all_data.star" style="display:inline-block"  :colors="['#99A9BF', '#F7BA2A', '#FF9900']" show-text></el-rate></li>
                    <li><span>评价时间</span><el-date-picker v-model="all_data.created_at" type="datetime"  placeholder="选择日期时间" style="width:280px" format="yyyy-MM-dd HH:mm:ss"  value-format="timestamp" sizi='medium '></el-date-picker></li>
                    <li><span>是否匿名</span> <el-radio v-model="all_data.type" :label="1">开</el-radio><el-radio v-model="all_data.type" :label="0">关</el-radio></li>
                    <li><span>配图</span> 
                        <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                              :on-success='pic_status'  multiple  :limit="5" :on-exceed="handleExceed" :data="max_url" :file-list="fileList"> <el-button size="medium" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload></li>
                    <li><span>评价内容</span><el-input v-model="all_data.content" :autosize="{ minRows: 2, maxRows: 4}" style="width:calc(100% - 216px);vertical-align: middle;" type="textarea"  placeholder="请输入内容" ></el-input> </li>
                    <li><span>管理员备注</span><el-input v-model="all_data.admin_remarks" :autosize="{ minRows: 2, maxRows: 4}" style="width:calc(100% - 216px);vertical-align: middle;" type="textarea"  placeholder="请输入内容" ></el-input> </li>
                    <li style="text-align:center"><el-button style="margin-top:28px;width:128px" @click="data_updata" type="success">{{title=='添加评论'?'提  交':'确认修改'}}</el-button></li>
                </ul>
            </div>
        
        </div>
    </div>
</div>
</template>
<script>
import commodity_evaluate_add from "./commodity_evaluate_add";
export default commodity_evaluate_add;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    min-height: 100px;
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
    ul {
      width: 96%;
      margin: 0 auto;
      padding: 28px 0;
      li {
        padding: 6px 0;
        span {
          width: 90px;
          display: inline-block;
          text-align: right;
          margin-right: 18px;
          color: #5c5c5c;
          font-size: 14px;
        }
        p {
          width: calc(100% - 108px);
          margin-left: 108px;
          color: #ccc;
          padding: 2px 0;
        }
        s {
          color: #ccc;
          text-decoration: none;
          display: inline-block;
          margin-left: 48px;
        }
      }
    }
  }
}
</style>



