<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <distributionleftcomponents></distributionleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <img class="search_img" src="../../../static/images/search.png" alt="">
                <div class="search_modle" >
                    <span>周业绩奖设置:</span>   
                    <el-button type="success"  @click="close()" plain style="vertical-align: middle;width:144px;float:right;margin: 10px;">添加周业绩奖</el-button>
                </div>
            </div>
            <div class="button_box">
                <el-button @click="table_chang(1)" :disabled='tableoder==1' type="primary"  plain style="margin-left:28px;" size="mini">奖励设置</el-button>
                <el-button @click="table_chang(2)" :disabled='tableoder==2' type="primary"  plain style="" size="mini" >周业绩排行</el-button>
                <el-button @click="table_chang(3)" :disabled='tableoder==3' type="primary"  plain style="" size="mini" >周业绩奖励</el-button>
            </div>
            <!-- 奖励设置 -->
            <div class="distribution" v-if="tableoder==1">
                <el-table :data="tableData" @selection-change="handleSelectionChange" ref="multipleTable" border max-height="800" style="width: 100%">
                    <el-table-column  type="selection"  width="38"> </el-table-column>
                    <el-table-column   label="排名"  width="320" > 
                        <template   slot-scope="scope" > 
                          {{scope.row.daishu}}         
                        </template> 
                    </el-table-column>
                    <el-table-column   label="奖励金额"  >
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="奖励积分">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="操作"  width="180">
                        <template slot-scope="scope">
                            <div>
                                <el-button @click="close(scope.row.id)" size="mini" type="primary">编辑</el-button>
                                <el-button @click="data_del(scope.row.id)" size="mini" type="danger" >删除</el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 周业绩排行 -->
            <div class="distribution" v-if="tableoder==2">
                <el-table :data="tableData"  border max-height="800" style="width: 100%">
                    <el-table-column  type="selection"  width="38"> </el-table-column>
                    <el-table-column   label="排名"   > 
                        <template   slot-scope="scope" > 
                          {{scope.row.daishu}}         
                        </template> 
                    </el-table-column>
                    <el-table-column   label="会员用户名"  >
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="会员昵称">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="年">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="周">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="业绩">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                </el-table>
            </div>
            <!-- 周业绩奖励 -->
            <div class="distribution" v-if="tableoder==3">
                <el-table :data="tableData"  border max-height="800" style="width: 100%">
                    <el-table-column  type="selection"  width="38"> </el-table-column>
                    <el-table-column   label="排名"   > 
                        <template   slot-scope="scope" > 
                          {{scope.row.daishu}}         
                        </template> 
                    </el-table-column>
                    <el-table-column   label="会员用户名"  >
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="会员昵称">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="年">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="周">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="业绩">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="奖励积分">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                    <el-table-column   label="奖励金额">
                        <template   slot-scope="scope" > 
                            {{scope.row.bili}}
                        </template> 
                    </el-table-column>
                </el-table>
            </div>
            <!-- 快捷操作 -->
            <div v-if="tableoder==1" class="operation" style='padding:12px 0 ;background:#fff'>
                <el-button @click="all_yes_select(tableData)" size="mini" style="margin-left:28px;" >全选</el-button>
                <el-button @click="all_yes_select()" size="mini" >全不选</el-button>
                <el-button @click="reverse_select" size="mini">反选</el-button>
                <el-button @click="del_all" size="mini" >删除</el-button>
            </div>
        </div>
    </div>
    <!-- 隐藏区域 -->
    <div v-if="ishow" class="back_shadow" >
      <div class="vip_vip_updata vip_grade" >
          <div class="vip_set_cont_modle_top">{{title}}周业绩奖<span @click="close" class="el-icon-error"></span></div>
          <div class="vip_set_cont_modle" style="padding-top:18px"><span>奖励现金</span><el-input size='small ' type="number" step="1" min="0" style="width:282px;margin-left:3px"></el-input></div>    
          <div class="vip_set_cont_modle" style="padding-top:18px"><span>奖励积分</span><el-input size='small ' type="number" step="1" min="0" style="width:282px;margin-left:3px"></el-input></div>    
          <el-button type="success"  plain>提交</el-button>
      </div>
    </div>
</div>
</template>
<script>
import distribution_setting from "./distribution_setting";
export default distribution_setting;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
        vertical-align: middle;
      }
    }
  }
  .distribution {
    border-radius: 12px;
    margin-top: 12px;
    font-size: 14px;
    color: #5c5c5c;
    background: #fff;
  }
  .button_box {
    border-radius: 12px;
    margin-top: 12px;
    background: #fff;
    height: 48px;
    line-height: 48px;
  }
  .operation_cont {
    background: #fff;
    padding: 8px 0 18px;
    p {
      width: 94%;
      margin: 6px auto;
    }
  }
}
.back_shadow {
  width: 100%;
  min-width: 1000px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4;
  .vip_vip_updata {
    position: absolute;
    width: 768px;
    background: #fff;
    left: calc(50% - 384px);
    top: 18%;
    border-radius: 12px;
    margin-top: 12px;
    padding: 0 0 18px;
    .vip_set_cont_modle_top {
      background: #409eff;
      border-radius: 12px 12px 0 0;
      height: 58px;
      line-height: 58px;
      position: relative;
      text-align: center;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      span {
        position: absolute;
        font-size: 26px;
        color: #ccc;
        float: right;
        top: 16px;
        right: 16px;
        cursor: pointer;
      }
    }
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 198px;
        text-align: right;
        margin-right: 18px;
      }
      i {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
}
</style>



