<template>
<div>
    <!-- 左侧信息栏 -->
    <div class="Layout_left_home">
        <!-- 左侧公共top -->
        <homelefttop></homelefttop>
        <!-- 私有二级导航 -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- 左侧公共bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!-- 右侧侧信息栏 -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!-- 顶部搜索功能 -->
            <div class="search">
                <div class="search_modle" >
                    <span>产品库存管理</span>
                    <el-button @click="data_updata" type="success" plain style="vertical-align: middle;width:158px;float:right;margin: 10px;">批量修改</el-button>
                    <el-input v-model="stock" style="vertical-align: middle;width:144px;float:right;" type="number" step="1" min="0" size='medium' ></el-input>
                </div>
            </div>
            <!-- 商品库存展示 -->
            <div class="column_modle">
                <el-table :data="tableData"  border max-height="800" style="width: 100%"   >
                        <el-table-column   label="属性"  width="458" > 
                            <template   slot-scope="scope" > 
                                <p>{{scope.row.parameter_name}} <span v-if="scope.row.parameter_name"> : </span>{{scope.row.parameter_value}}</p>          
                                <p>{{scope.row.plus_price_name}} <span v-if="scope.row.plus_price_name"> : </span>{{scope.row.plus_price_value}}</p>             
                            </template> 
                        </el-table-column>
                        <el-table-column   label="颜色"  width="280" >
                            <template   slot-scope="scope" > 
                                <img :src="img_url+scope.row.pic" alt="" style="display:block;margin: 0 auto;max-height:58px;">
                            </template> 
                        </el-table-column>
                        <el-table-column   label="库存"  >
                            <template   slot-scope="scope" > 
                                <el-input @change="data_updata(scope.row.id,scope.row.stock)" type="number" size='mini' v-model="scope.row.stock"></el-input>                                    
                            </template> 
                        </el-table-column>
                    </el-table>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import commodity_Inventory from "./commodity_Inventory";
export default commodity_Inventory;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    margin-top: 12px;
  }
}
</style>



